import Config from "app/data/Config";
import { Debugbase } from "debugbase/Debugbase";

export default class ProjectHeaderSizes {


    public static heightFromMobileDesign(orginalDesignedHeight: number, round = true, skipFix = false): number 
    {
        let designedWidth = 375;
        let ratio = (window as any).cachedInnerWidth/designedWidth;
        let height = round ? Math.round(ratio * orginalDesignedHeight) : ratio * orginalDesignedHeight;
        
       if (!skipFix && height/(window as any).cachedInnerHeight < 0.36)  {
           height = (window as any).cachedInnerHeight * 0.36;
       }

       return height;
    
    }

    public static headerWidth(): number {

        let innerWidth: number = (window as any).cachedInnerWidth;
		//let imageWidth: number = innerWidth;
	
		//if (innerWidth  > 1650) {
		//	imageWidth = (innerWidth * 0.93);
		//} else if (innerWidth  > 700) { 
		//	imageWidth = (innerWidth - 120);
        //}
        
        return innerWidth;

    }





    public static headerHeight(): number {

        let innerHeight: number = (window as any).cachedInnerHeight;
        let innerWidth: number =  (window as any).cachedInnerWidth;
        //let imageHeight: number = innerHeight * 0.67;
        let imageHeight: number = 375;

        if (Debugbase.system.isPortraitDevice && innerWidth <= 750) {

            if (innerWidth >= Config.squareProjectHeaderWidth) {
                return (window as any).cachedInnerWidth * 0.75;
            }

            return (window as any).cachedInnerWidth;

            //Ratio gets smaller on bigger height devices: 
            let designedRatio = 0.667;
            let maxRatioDesigned = 0.5897;
            let designedInnerHeight = 553;
            let designedInnerMaxHeight = 666;
            let diff = designedRatio - maxRatioDesigned;
            let range = designedInnerMaxHeight -designedInnerHeight;
            let r = range == 0 ? 0 : ((window as any).cachedInnerHeight-designedInnerHeight) / range;

            if(r > 1) r = 1;
            else if(r < 0) r = 0;

            let heightRatio = designedRatio - (diff * r);
            return Math.round((window as any).cachedInnerHeight * heightRatio);

            
        }



      
        let height =  Math.round( ((window as any).cachedInnerWidth / 1920) * 738 );


        if (height/(window as any).cachedInnerHeight  > 0.7 ) {
            return Math.round( ((window as any).cachedInnerWidth / 1920) * 650 );
        }

        return height;
      
        

        if (innerHeight  > 500 && innerHeight  < 1000) {
            imageHeight = ProjectHeaderSizes.videoSize().height  * (innerHeight/1000);
        } else if (innerHeight > 1000) {
            imageHeight = ProjectHeaderSizes.videoSize().height * (innerWidth/1920);
        }
    
		//if (innerWidth  > 1650) {
		//	imageHeight = ProjectHeaderSizes.videoSize().height * (innerWidth/1920);
		//} else if (innerWidth  > 700) { 
            //imageHeight = innerHeight * 0.70;
         //   imageHeight = ProjectHeaderSizes.videoSize().height * (innerWidth/1920);
       // }

       //imageHeight = ProjectHeaderSizes.videoSize().height * (innerWidth/1920);
        
        return imageHeight;
        
    }

    public static headerSize(): any {
        return {
            width: ProjectHeaderSizes.headerWidth(),
            height: ProjectHeaderSizes.headerHeight()
        };
    }

    public static videoSize(): any {
        return {width: 1920, height: 738};
    }

    public static headerYPosition(): number {
        //return ((window as any).cachedInnerWidth < 700) ? 70 : 0; 
        return ((window as any).cachedInnerWidth < 700) ? 0 : 0;
    }
    

}