import {Debugbase} from 'debugbase/Debugbase';
import {MathHelper, Point} from 'debugbase/helper/MathHelper';

export class ParticleRepell {

	private _math: MathHelper;
	private _distance: number;					//Distance to mouse from Math helper 
	private _distanceTolerence: number;			//Distance to (randomly seeded to create partciles that will go furter to meet the mouse)
	private _easePower: number;				//Normal ease power when not effected by mouse proximity (Essentially the )
	private _mouseRepellPower: number;
	private _moveXTarget: number = 0;
	private _moveYTarget: number = 0;
	private	_moveX: number = 0;
	private	_moveY: number = 0; 
	private _powerX: number = 0;
	private _powerY: number = 0;
	private _sprite: PIXI.Sprite;	

	private _repellSeed: number = 1;
	private _repellLimit: number = 50;
	private _repellAmountActive: number;
	private _repellAmount: number;
	private _repellAmountIntro: number;
	private _repellAmountButton: number;
	private _active: boolean = true;
	private _center: Point;
	private _repellCenter: Point;

	constructor(sprite : PIXI.Sprite, distanceTolerence: number = 50, easePower: number = 20) {
		this._math = Debugbase.math;

		this._sprite = sprite;
		this._distanceTolerence = distanceTolerence;
		this._easePower = easePower;
		this._mouseRepellPower = 20;
		this._repellSeed = 1 + (Math.random() * 0.7);
		this._repellAmount = 40 + (Math.random() * 20);
		this._repellAmountIntro = 60 + (Math.random() * 20);
		this._repellAmountButton = 40 + (Math.random() * 10);

		this._center = {
			x: (window as any).cachedInnerWidth / 2,
			y: (window as any).cachedInnerHeight / 2,
		};

	}

	public setActive(active: boolean): void {
		this._active = active;
	}

	public get active(): boolean {
		return this._active;
	}

	public retarget(mouseCords: Point, targetCords: Point): Point {
		
		if (!this._active) {
			return targetCords;
		}

		this._repellCenter =  ( (window as any ).introActive  || (window as any ).buttonActive ) ? this._center : mouseCords;

    	this._distance = Math.sqrt(Math.pow(targetCords.x - this._repellCenter.x, 2) + Math.pow(targetCords.y - this._repellCenter.y, 2));

		this._repellLimit = ( (window as any ).introActive ) ? 75 : 50;
		this._repellAmountActive = ( (window as any ).introActive ) ? this._repellAmountIntro : this._repellAmount;

		if( (window as any).buttonActive ) {
			this._repellLimit = 50;
			this._repellAmountActive  = this._repellAmountButton;
			//this._repellAmountActive = 50;
		}

	    if (this._distance < this._repellLimit) {
	    	let radians = Math.atan2(targetCords.y - this._repellCenter.y, targetCords.x - this._repellCenter.x);
	     	targetCords.x =  this._repellCenter.x + (Math.cos(radians) *  this._repellAmountActive);
	    	targetCords.y = this._repellCenter.y +  (Math.sin(radians) *  this._repellAmountActive);
	    }

	    return targetCords;
             
	}

	public setParticleRepellCenter(center: Point): void {
		this._center = center;
	}

	public getY(): number {
		return this._moveY;
	}

	public getX(): number {
		return this._moveX;
	}

}
