//Contracts: 
import { particles } from 'app/contracts/Particles'; 
//Enums: 
import { ParticleLayoutType } from 'app/enum/Particles';
//Library: 
import { ParticleField} from 'app/library/particle/ParticleField';


export class ScreensGridField extends ParticleField {

	constructor (sceneProps: particles.SceneProps, container: PIXI.Container, config: particles.ParticleFieldConfig) {
		super(sceneProps, container, config);

	}

	protected init(): void {
        this.reposition(ParticleLayoutType.PERSPECTIVE, {x: 0.36333 + 0.41, y: -0.45}, {w: 0.04, h: 0.4}, null, this.particles);
		//this.reposition(ParticleLayoutType.PERSPECTIVE, {x: 0.36333 + 0.41, y: -5}, {w: 0.04, h: 0.4}, null, this.particles);
	}


}
