/** 
 * Set particle count for each container / scene on home
 * Icon particles are calculated from grid data length 
 * 
 * */

import { particles } from "app/contracts/Particles";
import { HomeParticle } from "./HomeParticle";

export default {
    
    introParticles: 500, 
    transitionParticles: 0,
    iconParticles: 1571,     
    iconParticlesMobile: 1000,     
    extraIconOutterParticles: 500,   
    //screensParticles: 3650,
    screensParticles: 2800,
    screensHalftoneParticles: 2800,  
    screensHalftoneParticlesMobileScale: 0.6,    
    screensDropMachineParticles: 1100,  
    screensDropMachineParticlesMobileScale: 0.5,        
    spacesParticles: 2500,
    spacesParticlesMobile: 1500,
    systemsParticles: 3650,
    indexesCache: null,

   total() {
        return this.introParticles + this.transitionParticles + this.iconParticles + this.screensParticles + this.spacesParticles + this.systemsParticles
   }, 

  indexes() {

    if (!this.indexesCache) {
        this.indexesCache =  {
            intro: this.introIndexes(),
            transitions: this.transitionsIndexes(),
            icon: this.iconIndexes(),
            screens: this.screensIndexes(),
            spaces: this.spacesIndexes(),
            systems: this.systemsIndexes(),
        }
    }

    return this.indexesCache;

  },


  getParticlePool(particles: Array<HomeParticle>, type: string) {
    let indexes = this.indexes();
    return particles.slice(indexes[type].lower, indexes[type].upper);
    //return particles.slice(0, indexes[type].upper - indexes[type].lower);
  },


  transitionsIndexes() {
    return {
        lower: 0,
        upper: this.transitionParticles - 1
    }
  },

  introIndexes() {
    return {
        lower: this.transitionParticles,
        upper: this.transitionParticles + this.introParticles - 1
    }
  },

  iconIndexes() {
    return {
        lower: this.transitionParticles + this.introParticles,
        upper: this.transitionParticles + this.introParticles +  this.iconParticles - 1
    }
  },

  screensIndexes() {
    return {
        lower: this.introParticles  + this.transitionParticles + this.iconParticles,
        upper: this.introParticles  +this.transitionParticles + this.iconParticles + this.screensParticles - 1
    }
  },

  spacesIndexes() {
    return {
        lower: this.introParticles  + this.transitionParticles + this.iconParticles + this.screensParticles,
        upper: this.introParticles  +this.transitionParticles + this.iconParticles + this.screensParticles + this.spacesParticles - 1 
    }
  },

  systemsIndexes() {
    return {
      lower: this.introParticles + this.transitionParticles + this.iconParticles + this.screensParticles + this.spacesParticles,
      upper:this.introParticles  + this.transitionParticles + this.iconParticles + this.screensParticles + this.spacesParticles + this.systemsParticles - 1 
    }
  }



}