//v1.0 - Dataloader is proxy for AXIOM. 
//All loaders that need AJAX / Form post style stuff should use this
import {Loader} from './Loader';
import Axios from 'axios';

export abstract class DataLoader extends Loader {

	public url;
	public method: string = 'GET';
	public data: any = null;

	public load(): void {
		this._load();
	}

	//protected 
	//--------------------------------------------------------
	//--------------------------------------------------------
	//--------------------------------------------------------
	//--------------------------------------------------------

	protected dataLoadComplete(data: any): void {
		this.complete(data);
	}

	//private 
	//--------------------------------------------------------
	//--------------------------------------------------------
	//--------------------------------------------------------
	//--------------------------------------------------------

	private get getParams(): any {
		return (this.method === 'GET') ? this.data :  null;
	}

	private get postData(): any {
		return (this.method !== 'GET') ? this.data :  null;
	}

	// Want to use async/await? Add the `async` keyword to your outer function/metho
	private _load() {
	
		Axios(({
			method: this.method,
			url: this.url,
			data: this.postData,
			params: this.getParams,
			onDownloadProgress: this.onProgress,
		} as any ))
		 .then( (response) => {
		    this.dataLoadComplete(response);
		  })
		  .catch( (error) => {
		    this.error(error);
		  });

	}

}
