import { injectable, inject} from 'inversify';
import { Logger, ComplexLogger } from 'app/contracts/Debug';
import { TYPES } from 'types';

@injectable()
export class LoggerService implements Logger {

	@inject(TYPES.ComplexLogger) private _complexLogger: ComplexLogger;

	public log(mgs: string) {
		this._complexLogger.logToString(mgs);
	}

}
