import HomeColours from "./HomeColours";
import HomeScreens from "./HomeScreens";
import HomeZonesConfig from "./HomeZonesConfig";
import ParticleGridPatterns from "./ParticleGridPatterns";
import ParticleTargets from "./ParticleTargets"

export default {

    cols: null,
    rows: null, 
    deadpixels: [],
    pointConfig: [],
    activePoints: [],
    blockSize: 10,
    drawGuides: true,
    guideContainer: null,
    guide: null,
    created: false,
    innerHeight: null,
    innerWidth: null,
    safeZoneWidth: null,
    safeZoneHeight: null,
    gridCenterX: null,
    gridCenterY: null,


    
    setGuideContainer(c) {
        this.guideContainer = c; 
    },


    createGrid() {

        if (!this.innerHeight) {
            this.innerHeight = window.innerHeight;
            this.innerWidth = window.innerWidth;
        }

        this.layout =  this.innerWidth  <= HomeScreens.switchScreensLayoutAt ? 'portrait' : 'landscape';

        let scale;
        let baseBlockSize = 30; 
        let minGridScale = 0.5; 

        if (this.innerWidth <= HomeScreens.switchScreensLayoutAt)  {

            minGridScale = 0.15;
            //this.gridWidth = this.innerWidth * 0.76; 
            this.gridWidth = this.innerWidth; 
            this.gridHeight = this.innerHeight * 0.5; 
            scale =  Math.max(this.innerWidth/ 1300, minGridScale);
           
        } else {
            this.gridWidth = this.innerWidth  <= HomeZonesConfig.noOverlapSize ? this.innerWidth * 0.9 :  this.innerWidth * 0.8; 
            this.gridHeight = this.innerHeight - 175; 
            
            scale =  Math.max(this.innerWidth/1920, minGridScale);
        }

        const mobileLayout = (window as any).cachedInnerWidth > HomeScreens.switchScreensLayoutAt;
        let deadPixelSeed = mobileLayout ? 0.5 : 0.55; 
        let deadPixelSeedEdge = mobileLayout ? 0.75 : deadPixelSeed; 
        let useDeadPixelSeed = deadPixelSeed;
        this.blockSize = Math.round(baseBlockSize * scale);
        
        this.deadpixels = [];
        this.cols = Math.ceil(this.gridWidth/this.blockSize);
        this.rows =  Math.ceil(this.gridHeight/this.blockSize); 
        let isDeadPixel = false;

        for (var c=1; c<=this.cols; c++) {


            

            for (var r=1; r<=this.rows; r++) {
                
                useDeadPixelSeed = c < 5 || this.cols - c <= 5 ||  r < 4 ||  this.rows - r <= 4 ? deadPixelSeedEdge : deadPixelSeed;
                
                isDeadPixel =  Math.random() < useDeadPixelSeed;

                if (isDeadPixel) {
                    if (!this.deadpixels[c]) this.deadpixels[c] = [];
                    this.deadpixels[c][r] = true; 
                } else {
                    if(!this.pointConfig[c]) this.pointConfig[c] = [];
                    this.pointConfig[c][r] = {
                        size: Math.ceil(Math.random() * 1.99),
                        //colour: HomeColours.getRandomColour(HomeColours.purpleBlue)
                        colour: HomeColours.getRandomColour(HomeColours.greys)
                    }
                }
                
            }
 
        }


        this.created = true; 
        
      
        ParticleGridPatterns.totalCols = this.cols;
        ParticleGridPatterns.totalRows = this.rows;
        ParticleGridPatterns.layout =  this.layout;



    },
    
    tick(gridCenterX, gridCenterY, safeZoneWidth, safeZoneHeight, progress) {


        this.activePoints = [];

        this.gridCenterX = gridCenterX;
        this.gridCenterY = this.innerWidth <= HomeScreens.switchScreensLayoutAt ?  this.innerHeight * 0.35 : gridCenterY;

        let startX = Math.round( this.gridCenterX  - (this.gridWidth/2));
        let startY = Math.round( this.gridCenterY - (this.gridHeight/2));

        this.startY = startY;

        if (!this.created) {
            this.createGrid(); 
        }

        this.safeZoneWidth = safeZoneWidth * 0.95;
        this.safeZoneHeight = safeZoneHeight * 0.52;


        let x = startX;
        let y = startY;

        let blockWidth = this.blockSize;
        let blockHeight = this.blockSize;
            

        for (var c=1; c<=this.cols; c++) {

            for (var r=1; r<=this.rows; r++) {
                
                let isNotDeadPixel = !this.deadpixels[c] || !this.deadpixels[c][r];
                if (!this.inSafeZone(x, y - startY) &&  isNotDeadPixel) {
                    if(!this.activePoints[c] ) this.activePoints[c]  = [];
                    this.activePoints[c][r] = r;
                    ParticleTargets.add('grid_' + c + '_' + r, x, y);
                }
                
                y += blockHeight;
                
            }

       
            y = startY;
            x += blockWidth;

        }


        ParticleGridPatterns.pointConfig = this.activePoints;

        
        let fadeAt = 0.7;
        let range = 1 - fadeAt;
        let ratio = (progress- fadeAt) / range; 
        let maxAlpha = (window as any).cachedInnerWidth < 800 ? 0.48 : 0.85;
        let minAlpha = (window as any).cachedInnerWidth < 800 ? 0.35 : 0.55;
    
        this.guideContainer.alpha = Math.max(minAlpha, ratio * maxAlpha);

        //console.log('this.guideContainer.alpha', this.guideContainer.alpha)

        if (this.drawGuides) {
            this.drawGuide(startX, startY);
        }
        
    },

    resize(innerWidth, innerHeight) {
        this.innerWidth = innerWidth;
        this.innerHeight = innerHeight;
        this.created = false;
        this.guideContainer.cacheAsBitmap = false; 
    },


    drawExtraDots(x, fromY,  height, direction = -1, scale = 1) {

  

        let size = Math.ceil(Math.random() * 1.99) * scale;
        //colour: HomeColours.getRandomColour(HomeColours.purpleBlue)
       
        let rows = Math.ceil(height/this.blockSize);
        let sparseY = fromY + (this.blockSize * direction);
        let sparseX = x;

        
        for (var i=1; i<=this.cols; i++) {
            
            for (var j=1; j<=rows; j++) {
                
                
                if(Math.random() < 0.2) {
                    
                    let  colour = '#ffffff';
                    if (Math.random() < 0.75 ) {
                        colour = HomeColours.getRandomColour(HomeColours.blues);
                    }
               
                
                    this.guide.beginFill(colour); 
                    this.guide.drawRect(sparseX, sparseY, size, size); 
                    this.guide.endFill();
                    
                }

                sparseY += (this.blockSize * direction);

            }

            sparseY = fromY + (this.blockSize * direction); 
            sparseX += this.blockSize;

        }


    },

    drawGuide(xGrid, yGrid) 
    {   

        this.guideContainer.x = xGrid;
        this.guideContainer.y = (window as any).yGrid = yGrid;

        let scale = (window as any).cachedInnerWidth < 800  ? 0.7 : 1

        //if (this.guideContainer.isCachedAsTexture) {
        if (this.guideContainer.cacheAsBitmap) {
            return;
        }

        if (!this.guide) {
            this.guide  =  new PIXI.Graphics(); 
            this.guide.alpha = 0.6;
            this.guideContainer.addChild(this.guide);
        }

        this.guide.clear();

        let x = 0;
        let y = 0;

      


        //Draw top dots up
        let sparsePaddingTopPx = 100;
        this.drawExtraDots(x, y, sparsePaddingTopPx, -1, scale);




        

            for (var i=1; i<=this.cols; i++) {

                for (var j=1; j<=this.rows; j++) {
                    
                    let size  = 1;
                    let colour = '#333366';
                    if(this.pointConfig[i] && this.pointConfig[i][j]) {
                        size =  this.pointConfig[i][j].size * scale; 
                        colour =  this.pointConfig[i][j].colour; 
                    }

                    let isNotDeadPixel = !this.deadpixels[i] || !this.deadpixels[i][j];
                    let inSafeZone = this.inSafeZone(x, y, true);
                    if (!inSafeZone && isNotDeadPixel) {
                        this.guide.beginFill(colour); 
                        this.guide.drawRect(x, y, size, size); 
                        this.guide.endFill();
                    } else if (inSafeZone) {
                        if(Math.random() < 0.4) {
                            this.guide.beginFill('#666666'); 
                            this.guide.drawRect(x, y, size, size); 
                            this.guide.endFill();
                        }
                    }
              

                    y +=   this.blockSize;

                }
                
                y = 0;
                x +=  this.blockSize;
            }


            x = 0; 
            let sparsePaddingBottomPx = (window as any).cachedInnerWidth < 800 ? (window as any).cachedInnerHeight * 0.4 : 100;
            this.drawExtraDots(x, (this.rows - 1) * this.blockSize, sparsePaddingBottomPx, 1, scale);

            //this.guideContainer.cacheAsTexture(!this.guideContainer.isCachedAsTexture);
            this.guideContainer.cacheAsBitmap = true; 
            
        return;
        
        

        for (var i=1; i<=cols + 1; i++) {
            this.guide.beginFill(0xffffffff); 
            this.guide.drawRect(x, y, 1, gridHeight); 
            this.guide.endFill();
            x += blockW;
        }

        x = xGrid;
        for (i=1; i<=rows + 1; i++) {
            this.guide.beginFill(0xffffffff); 
            this.guide.drawRect(x, y, gridWidth, 1); 
            this.guide.endFill();
            y += rowH;
        }

    },


    inSafeZone(pointX, pointY, translatedCheck = false) {


        if (this.layout == 'portrait') return false; 

        const centerX = translatedCheck ? this.gridWidth/2 : this.gridCenterX;
        let x1 = Math.round(centerX - (this.safeZoneWidth/2)) + 10;
        let y1 = Math.round(this.gridCenterY - (this.safeZoneHeight/2)) -  this.startY;
        let x2 = x1 + this.safeZoneWidth;
        let y2 = y1 + this.safeZoneHeight;

        //console.log('sz x1', x1, 'x2', x2, 'y1', y1, 'y2', y2, 'pointX', pointX, 'pointY', pointY);

        return pointX > x1 && pointX < x2 && pointY > y1 && pointY < y2; 


    } 





}