import { HomeJSONModel } from 'app/model/HomeJSONModel';
import './controllers';
import controllers from './controllers';
import Preload from './preload';
import {Events} from 'debugbase/events/Events';

// Debug GUI
import { ParticleDebugGUI, ParticleDebugEvent } from 'debug/ParticleDebugGUI';

//console.log = function() {};
//console.trace = function() {};
//console.warn = function() {};

(window as any).tmpApp = false;
(window as any).cachedInnerWidth = window.innerWidth;
(window as any).cachedInnerHeight = window.innerHeight;

// Initialize Debug GUI
ParticleDebugGUI.getInstance();
console.log('Debug GUI initialized - check top-right corner');

// Example: Listen to parameter changes
ParticleDebugGUI.on(ParticleDebugEvent.ANY_CHANGE, (params) => {
    console.log('Debug params changed:', params);
});

// Make it accessible from console
(window as any).ParticleDebugGUI = ParticleDebugGUI;


//Events.init();
//Events.tick( () => {
//	console.log('TEST');
//})

const preload = new Preload();

 (window as any).deltaTime = 1;


preload.onReady = (spritesheet: PIXI.Spritesheet, config: HomeJSONModel = null) => {
	
	controllers.create();
	
	//Create Pixi JS: 
	const pixi = controllers.pixi;
	const timeline = controllers.homeTimelineMain;
	const mouse = controllers.mouse;

	(window as any).timeline = timeline;

	pixi.init();




	pixi.onAppReady = () => {

		pixi.$pixi.ticker.add( () => {
			controllers.tick();
		})

		mouse.DOMReady();
		mouse.setDragCursorActive(true);
		
		timeline.init(
			pixi.$homeContainer,
			spritesheet,
			config
		);
	
		
		timeline.setVirtualWidth(window.innerWidth * 3,  3)


		setTimeout(  () => {
			timeline.introComplete()
		}, 2000);


		//setTimeout(  () => {
		//	controllers.setProcess(1);
		//}, 10000);


	};

}
preload.load();








