//V1.0.0
//Static singleton style 
//Creates and instance of the app and allows access to 
//Debugbase.state.set('project.title', 'Market Masters');
import {DebugbaseApp} from './DebugbaseApp';
import {RouteManager} from  'debugbase/managers/RouteManager';
import {StateManager} from  'debugbase/managers/StateManager';
import {ModuleManager} from  'debugbase/managers/ModuleManager';
import {BehaviourManager} from  'debugbase/managers/BehaviourManager';
import {ControllerManager} from 'debugbase/managers/ControllerManager';

import {SystemHelper} from  'debugbase/helper/SystemHelper';
import {DOMHelper} from  'debugbase/helper/DOMHelper';
import {StringHelper} from  'debugbase/helper/StringHelper';
import {MathHelper} from  'debugbase/helper/MathHelper';
import {FrameworkHelper} from  'debugbase/helper/FrameworkHelper';

export class Debugbase {

	public static app: DebugbaseApp; 

	public static get controller( ): ControllerManager {
		return Debugbase.app.controller; 
	}

	public static get route(): RouteManager {
		return Debugbase.app.route; 
	}

	public static get state(): StateManager {
		return Debugbase.app.state;
	}

	public static get module(): ModuleManager {
		return Debugbase.app.module; 
	}

	public static get behaviour( ): BehaviourManager {
		return Debugbase.app.behaviour; 
	}

	public static get DOM( ): DOMHelper {
		return Debugbase.app.DOM; 
	}

	public static get string( ): StringHelper {
		return Debugbase.app.string; 
	}

	public static get math( ): MathHelper {
		return Debugbase.app.math; 
	}

	public static get framework( ): FrameworkHelper {
		return Debugbase.app.framework; 
	}

	public static get system( ): SystemHelper {
		return Debugbase.app.system; 
	}

	public static init(): void {
		Debugbase.app.init();






	}

}

if (!Debugbase.app ) {
	Debugbase.app = new DebugbaseApp();
}
