//Version 0.1.0
interface Size {
	width: number; 
	height: number; 
}

interface Point {
	x: number;
	y: number;
}

interface Rectangle extends Size, Point {}

enum AnchorType {
	TOP_LEFT = 'top_left',
	CENTER = 'center'
}

export default class DGeometry {
	
	public cover (childSize: Size, containerSize: Size, anchor: AnchorType = AnchorType.TOP_LEFT): Rectangle {
		
		let coverSize: Size = this.getCoverSize(childSize, containerSize);
		let centerCords: Point = this.center(coverSize, containerSize, anchor);
		
		return {
			width: coverSize.width,
			height: coverSize.height,
			x: centerCords.x,
			y: centerCords.y
		};

	}

	public contain (childSize: Size, containerSize: Size, anchor: AnchorType = AnchorType.TOP_LEFT) {
		
		let containedSize: Size = this.getContainedSize(childSize, containerSize);
		let centerCords: Point = this.center(containedSize, containerSize, anchor);
		
		return {
			width: containedSize.width,
			height: containedSize.height,
			x: centerCords.x,
			y: centerCords.y
		};
	}

	public getCoverSize(childSize: Size, containerSize: Size): Size {
		
		let originalRatios = {
		  width: containerSize.width / childSize.width,
		  height: containerSize.height / childSize.height
		};

		// formula for cover:
		let coverRatio = Math.max(originalRatios.width, originalRatios.height); 
	
	// result:
		return {
			width: childSize.width * coverRatio,
			height: childSize.height * coverRatio
		 };

		
	}

	public getContainedSize(childSize: Size, containerSize: Size): Size {
		var ratio: number = Math.min(containerSize.width / childSize.width, containerSize.height / childSize.height);
		return { 
			width: childSize.width * ratio, 
			height: childSize.height * ratio 
		};
	}

	public center(childSize: Size, containerSize: Size, anchor: AnchorType = AnchorType.TOP_LEFT): Point {

		let x: number;
		let y: number;

		if (anchor == AnchorType.TOP_LEFT) {
			x = ( containerSize.width / 2) - (childSize.width / 2) 
			y = ( containerSize.height / 2) - (childSize.height / 2)  
		} if (anchor == AnchorType.CENTER) {
			x = y = 0;
		}

		return {
			x: x,
			y: y
		} 
	}

} 