//v1.0 - HTML loader for loading scripts in 
import {DataLoader} from './DataLoader';

export class JSONLoader extends DataLoader {	
	
	protected onLoaderComplete(response: any): void {

	
		this.complete(response);
	}

	protected complete(data: any = null): void {

		this._complete = true; 
		
		if (this.onComplete) {
			this.onComplete(this.id, data.data, this);
		}

		if (this.onGroupAssetComplete) {
			this.onGroupAssetComplete(data, this);
		}
	}

}
