import { Debugbase } from 'debugbase/Debugbase';
import { Module } from 'debugbase/abstract/Module';
import { InstanceLoader } from 'debugbase/loader/InstanceLoader';
import { Events } from 'debugbase/events/Events';
import { CoreEvents } from 'debugbase/events/CoreEvents';

enum ClearType { DESTROY, REMOVE }

interface RouteConfig {
	path: string;
	controller: string;
	function?: string;
}

export class ModuleManager {

	private _modules: Module[] = [];							//All active modules
	private _instanceLoader: InstanceLoader<Module>;			//To load module instances saved to window scope
	private _created: Module[];  								//Object modules created at runtime and pushed in via Debugbase.module.create();
	private _purgeQueId: any;
	private _autoCreateQueId: any;

	public create(): void {

	}

	public get(id: string): Module {
		return this._getModuleById(id);
	}

	public getByTag(tag: string): Array <Module> {
		return this._getModulesByTag(tag);
	}

	public getByModuleName(name: string): Array <Module> {
		return this._getByModuleName(name);
	}

	public remove(id: string): void {
		this._clearById(id, ClearType.REMOVE);
	}

	public destoryByTag(tag: string): void {
		this._clearByTag(tag);
		this._purge();
	}

	public destroy(id: string): void {
		this._clearById(id, ClearType.DESTROY);
	}

	public DOMReady(): void {
		this._queAutoCreate();
	}

	public DOMUpdated(): void {
		this._quePurge();
		this._queAutoCreate();
	}

	public tick(): void {
		this._modules.forEach((moduleInstance: Module) => {
			if (moduleInstance.tick) {
				moduleInstance.tick();
			}
		});
	}

	public scroll(windowY: number): void {
		this._modules.forEach((moduleInstance: Module) => {
			if (moduleInstance.scroll) {
				moduleInstance.scroll(windowY);
			}
		});
	}

	public resize(sizes: any): void {
		this._modules.forEach((moduleInstance: Module) => {
			if (moduleInstance.resize) {
				moduleInstance.resize(sizes);
			}
		});
	}

	public createModulesInTarget($target: HTMLElement): void {
		this._createModulesInTarget($target);
	}

	//private 
	//--------------------------------------------------
	private _getModuleById(id: string): Module {
		for (let i = 0; i < this._modules.length; i++) {
			if (this._modules[i].id === id) {
				return this._modules[i];
			}
		}
		return null;
	}

	private _getModulesByTag(tag: string): Array<Module> {
		return this._modules.filter( (moduleItem) => moduleItem.tag === tag );
	}

	private _getByModuleName(name: string): Array<Module> {
		return this._modules.filter( (moduleItem) => moduleItem.moduleName === name );
	}

	private _queAutoCreate(): void {
		Debugbase.DOM.clearQue(this._autoCreateQueId);
		this._autoCreateQueId = Debugbase.DOM.que(this._autoCreateModules.bind(this));
	}

	//Grab any module that haven't been created
	//Create modules are assigned the m-mounted="true" attribute and stop being selected again
	private _autoCreateModules(target: any = document): void {

		//Create a dyamic instance loader on the window.Debugbase scope: 
		if (!this._instanceLoader) {
			this._instanceLoader = new InstanceLoader<Module>((window as any).DebugbaseModules);
		}
		
		this._createModulesInTarget(document);

		Events.dispatch(CoreEvents.MODULES_MOUNTED);

	}

	private _createModulesInTarget(target: any): void {
		
		let modules: NodeListOf<Element> = target.querySelectorAll('[m]:not([m-mounted=\'true\'])');
	
		modules.forEach((moduleElement: Element) => {
			
			try {
				this._createModule(moduleElement);
			} catch (e) {
				console.log('MODULE LOAD ERROR !!!!!!!!!!!!!!!!!!!!!!', moduleElement, e);
			}

		});
	}

	//Create a single module to push into the the 
	//This is a special case (Like behaviours) where modules classes are set to set window.scope and create at runtime
	//So those classes need to be imported to app or relevant scope
	private _createModule(element: Element): void {

		//Get a list of attbuites parsed into numbers/booleans/strings: 
		let attributes: any = Debugbase.framework.getParsedAttributes(element);

		//Create the instance: 
		let instance: Module = this._getInstance(attributes.m);

	
		instance.init();
		instance.$container = element;
		instance.id = element.getAttribute('id');

		if (attributes['m-tag']) {
			instance.tag = attributes['m-tag'];
		}

		Debugbase.framework.attributesToConfig(instance, attributes, attributes.m);
		instance.moduleName = attributes.m;

		instance.create();

		//Flag mounted:  
		instance.mounted = true;
		Debugbase.DOM.setAttribute(element, 'm-mounted', 'true');

		//Add to stack: 
		this._modules.push(instance);

	}

	private _quePurge(): void {
		Debugbase.DOM.clearQue(this._purgeQueId);
		this._purgeQueId = Debugbase.DOM.que(this._purge.bind(this));
	}

	private _purge(): void {

		let i = 0;

		console.log('Destroy purche started', this._modules);

		while (i < this._modules.length) {

			//Check if module removed/missing but not destroyed: 
			if (!this._modules[i].destroyed && this._isRemoved(this._modules[i])) {
				this._destroyModule(this._modules[i]);
			}

			if (this._modules[i].destroyed) {
				Events.dispatch(CoreEvents.MODULE_DESTOYED, this._modules[i].id);
				this._modules.splice(i, 1);
			} else {
				i++;
			}

			
		}


		console.log('Destroy purche ended', this._modules);

	}

	private _getInstance(moduleName: string): Module {
		let className: string = Debugbase.string.snakeToClassName(moduleName);		
		return this._instanceLoader.getInstance(className);
	}

	private _isRemoved(moduleInstance: Module): boolean {

		return (moduleInstance.removed || !Debugbase.DOM.contains(moduleInstance.$container));
	}

	private _clearById(id: string, type: ClearType): void {

		let module = this._getModuleById(id);
		if (!module) {
			return;
		}
		//Remove OR destroy module: 
		if (type === ClearType.DESTROY) {
			this._destroyModule(module);
		} else {
			this._removeModule(module);
		}

		//Remove (Que asnc so it only happens once no matter how much its called): 
		this._quePurge();
	}

	private _clearByTag(tag: string): void {
		this._getModulesByTag(tag).forEach( (item) => this._destroyModule(item) );
	}

	private _removeModule(moduleInstance: Module): void {

		if (!moduleInstance.$container) {
			return;
		}

		moduleInstance.removed = true;
		Debugbase.DOM.removeElement(moduleInstance.$container);
		Debugbase.DOM.removeAttributte(moduleInstance.$container, 'm');
		Debugbase.DOM.removeAttributte(moduleInstance.$container, 'm-mouted');
		moduleInstance.$container = null;

		if (!moduleInstance.destroyed) {
			this._destroyModule(moduleInstance);
		}

	}

	private _destroyModule(moduleInstance: Module): void {
		moduleInstance.destroy();
		moduleInstance.active = false;
		moduleInstance.$container = null;
		moduleInstance.destroyed = true;
	}

}

if ('NodeList' in window && !NodeList.prototype.forEach) {
    NodeList.prototype.forEach = function (callback, thisArg) {
      thisArg = thisArg || window;
      for (var i = 0; i < this.length; i++) {
        callback.call(thisArg, this[i], i, this);
      }
    };
  }
