	public resize() {

		this.stopAsActiveScene();

		if (this._resizeTimeout) {
			clearTimeout(this._resizeTimeout);
		}

		this._resizeTimeout = setTimeout(() => {
			this.startAsActiveScene();
			this._resizeTimeout = null;
		}, 500);
	
		return;

		this._shapeFrameCounter = 0;
		
		DelayHelper.setTimeout(() => {
			this._nextScreensSection();
			this._shapeFrameCounter = 0;
		}, 50, null, 'screens');
		

	}


