	private _ttBallon() {

		this._comboCounter.clear();

		let sizes = {
			topLeft: {x:-0.0905, y:0.165, w: 0.077, h:0.3 },
			topRight: {x:-0.0032, y:0.225, w: 0.05, h:0.2 },
			bottomLeft: {x:-0.1116, y:0.515, w: 0.05, h:0.2},
			bottomRight: {x:-0.057, y:0.518, w: 0.05, h:0.2 } 
		}

		//let item = HomeFieldScreensPixelDataConfig.getList()[2];
		//let item = HomeFieldScreensPixelDataConfig.dataSet.m4g;
		let item = HomeFieldScreensPixelDataConfig.dataSet.ttballon;


		gsap.TweenMax.set(this._containerOffsets, {x: -370, y: -400})
		gsap.TweenMax.to(this._containerOffsets, 5, {x: -70, y: -300, ease: gsap.Back.easeOut, rotation: 0.05})
		gsap.TweenMax.to(this._containerOffsets, 5, {x: -50, y: -300, delay: 5, ease: gsap.Back.easeOut, rotation: 0.05})

		this._containerOffsets.x = this._containerOffsets.xI.value = this._containerOffsets.xI.target  = -370;
		this._containerOffsets.y =  this._containerOffsets.yI.value = this._containerOffsets.yI.target   = -400;
		//this._containerOffsets.xI.setVectorConfig(true)
		//this._containerOffsets.yI.setVectorConfig(true)
		//this._containerOffsets.xI.target = -50;
		//this._containerOffsets.yI.target =  -300;



		let i = 0;
		this._shapeParticles.forEach(particle => {

			
			particle.repell.active = true;
			//this._repell.target = this._sceneProps.targets.screenCenter;
			//this._p.repell.target = this._sceneProps.targets.systemsMouse;
			//this._repell.type = RepellType.BOUNDARY;
			particle.particlePhysics.active = false;
			particle.repell.type = RepellType.PUSH;
			//Allow for 150px circle and very tight ring: 
			particle.repell.radius = 100;	
			particle.repell.randomisePower(30, 50);

			particle.animatedProps.stopBreathing();
			particle.animatedProps._x.setVectorConfig(false);
			particle.animatedProps._y.setVectorConfig(false);
		

			if(item && item.pixelData[i]) {
				
				particle.activate();
				
				
				particle.colour.set(item.pixelData[i].rgba);
				particle.particleProps.sprite.blendMode = null;


				let scaleSeed = 0.5 * (item.pixelData[i].gray)
				particle.animatedProps.scale = particle.animatedProps.targetScale = 0.1 + (Math.random() * scaleSeed);
				particle.animatedProps.scale = particle.animatedProps.targetScale = particle.animatedProps.targetScaleSaved = 0.25
				particle.animatedProps.scale = 0;
				particle.animatedProps.alpha = 0;

				let baseScale = 0.1 + (Math.random() * scaleSeed);
				

				if(Math.random() < 0.1) {
					particle.animatedProps.startBreathing(baseScale, 0.001 + Math.random() * 0.04, Math.random() * 0.6);
				}
				
				particle.mouseParallax.active = false;
				particle.animatedProps.alpha = particle.animatedProps.targetAlpha = 0.8 + Math.random() * 0.2;

				let indentX = -0.01;
				let indentY = 0;
				let wCorrect = 0.8;
				let yCorrect = 1.9;
				let x = sizes.bottomRight.x + indentX + (item.pixelData[i].x * (sizes.topLeft.w * wCorrect));
				let y = sizes.bottomRight.y + indentY +(item.pixelData[i].y * (sizes.topLeft.h * yCorrect));

				
				particle.position.set({x:x, y: y}, true);


				//particle.animatedProps.setVectorStyleMovement(true);
				//particle.position.set({x:x, y: y + 0.2});

						
				particle.animatedProps.setEase({
					alpha: 30,
					scale: 30
				})

				particle.animatedProps.alpha = 	particle.animatedProps.targetAlpha =  0;
				particle.animatedProps.hardSetScale(0);
				
				this._comboCounter.add(() => {
					particle.animatedProps.targetAlpha = 0.8 + Math.random() * 0.2;
					particle.animatedProps.targetScale =particle.animatedProps.targetScaleSaved = 0.1 + Math.random() * 0.25;

					particle.animatedProps._x.setVectorConfig(true);
					particle.animatedProps._y.setVectorConfig(true);

				},  Math.random() * 1000);
				

				this._comboCounter.add(() => {
					let xD = Math.random() < 0.5 ? -1 : 1; 
					let yD = Math.random() < 0.5 ? - 1 : 1; 
					particle.position.set({x:x + (xD * (Math.random() * 0.08)), y: y + (yD * (Math.random() * 0.1))});

					particle.animatedProps.targetAlpha = 0;
					particle.animatedProps.targetScale = particle.animatedProps.targetScaleSaved =  0
					
					
					particle.animatedProps.setEase({
						alpha: 30,
						scale: 30
					})

					particle.animatedProps._x.setVectorConfig(true);
					particle.animatedProps._y.setVectorConfig(true);


				}, 6000 + Math.random() * 600);



			} else {
				particle.hide();
				particle.particleProps.sprite.visible = false;
			}

			i++;
			
		});

		HomeScreens.on('bottomRight');
		HomeScreens.on('topLeft');
		


		this._comboCounter.add(() => {
			HomeScreens.on('bottomLeft');
		}, 0);

		this._comboCounter.add(() => {
			HomeScreens.off('bottomLeft');
		}, 1000);

		this._comboCounter.add(() => {
			HomeScreens.off('topLeft');
		}, 7000);


		this._comboCounter.add(() => {
			HomeScreens.off('bottomRight');
		}, 7200);



	}

