	private _doDropMachine(): void {
		
		let totalParticles = HomeFieldConfig.screensDropMachineParticles;
		ParticleHitTests.active = true; 

		//On mobile reduce particle count: 
		if( (window as any).cachedInnerWidth < 700 ) {
			totalParticles *= HomeFieldConfig.screensDropMachineParticlesMobileScale;
		}


		this._doDrop(totalParticles);
		HomeScreens.showDropMachineBlocks();

		this._comboCounter.clear();

		HomeScreens.allOff();

	
		let showBottomRightDesktop = 2000;
		let showBottomRightMobile = 1600;
		let showBottomLeft = 1800;
		let hideTopLeftMobile = 6200;
		let hideTopLeftDesktop = 7000;
		let hideTopRightDesktop = 6800;
		

		//Onm desktop top right is straight away
		this._comboCounter.add( () => { 
			//if ( (window as any).cachedInnerWidth >   HomeScreens.switchScreensLayoutAt) {
				HomeScreens.on('topRight')
			//}
		}, 500);

		//On mobile top right is shifted to bottom so
		this._comboCounter.add( () => { 
			//if ( (window as any).cachedInnerWidth <=  HomeScreens.switchScreensLayoutAt) {
				//HomeScreens.on('topRight')
			//}
		}, showBottomRightMobile);


		this._comboCounter.add( () => { HomeScreens.on('topLeft')}, 400);
		
		this._comboCounter.add( () => { 
			HomeScreens.on('bottomLeft')
		}, showBottomLeft);

		//Only show portrait screen on desktop 
		this._comboCounter.add( () => { 
			//if ( (window as any).cachedInnerWidth >   HomeScreens.switchScreensLayoutAt) {
			HomeScreens.on('bottomRight')
			//}
		},  showBottomRightDesktop);

		this._comboCounter.add( () => HomeScreens.hideDropMachineBlocks(), 6000);

		
		this._comboCounter.add( () => {
			//if ( (window as any).cachedInnerWidth >   HomeScreens.switchScreensLayoutAt) {
				HomeScreens.off('topRight')
			//}
		}, hideTopRightDesktop);

		this._comboCounter.add( () => {
			//if ( (window as any).cachedInnerWidth >   HomeScreens.switchScreensLayoutAt) {
				HomeScreens.off('topLeft')
			//}
		}, hideTopLeftDesktop);

		this._comboCounter.add( () => {
			//if ( (window as any).cachedInnerWidth <=   HomeScreens.switchScreensLayoutAt) {
			//	HomeScreens.off('topLeft')
			//}
		}, hideTopLeftMobile);

			
		this._comboCounter.add( () => HomeScreens.allOff(), 8200);
		

	}


		
	private _doDrop(totalParticles): void {
		
		if((window as any).colourPhase === 'yellow') (window as any).bounceColourTarget = HomeColours.dynamicSets.m4g;
		else (window as any).bounceColourTarget  = HomeColours.purpleBlue;

		let i = 0;
		let mobileLayout = (window as any).cachedInnerWidth <=   HomeScreens.switchScreensLayoutAt
		
		this._shapeParticles.forEach(particle => {
			
			//particle.colour.set(HomeColours.getRandomColour(HomeColours.dynamicSets.halftone));

			i++;
			if(i < totalParticles) {
				particle.screens.leftDrop();
				particle.particleProps.sprite.blendMode = null;


				let partileTexture = Math.random() < 0.2 ? 'solid-ring.png' :  'solid2.png';
				particle.changeParticle(partileTexture);
				
				let baseScale;
				let baseSeedBig = mobileLayout ? 0.6 :  0.8;
				let baseSeedSmall = mobileLayout ? 0.38 :  0.5;
				if(Math.random() < 0.2) {
					baseScale = 0.1 + (Math.random() * baseSeedBig);
				} else {
					baseScale = 0.1 + (Math.random() * baseSeedSmall);
				}

					particle.animatedProps.hardSetScale(baseScale)
					particle.animatedProps.startBreathing(baseScale, Math.random() * 0.02, Math.random() * 0.4);


				let x = -0.08 + (Math.random() * 0.15);
				let y = mobileLayout ? Math.random() * -0.15 : Math.random() * 0.15;
				
				particle.animatedProps.targetAlpha = 0.6 + (Math.random() * 0.4);
				particle.animatedProps.setVectorStyleMovement(false);
				particle.position.set({x:x, y:y}, true);

			} else {
				//particle.hide();
				particle.deactivate();
			}
			
		});
	}


	

	private _doRadialHalftoneImage(): void {
		
		
		//console.log('_doRadialHalftoneImage()');

		(window as any).colourPhase = (window as any).colourPhase  === 'yellow' ? 'green' : 'yellow';

		(window as any).screensZoneScale = this._sceneProps.zones.screens.scale;

		this._radialHalftoneImage = new RadialHalftoneImage();
		
		
		let totalParticles = HomeFieldConfig.screensHalftoneParticles;
		if ( (window as any).cachedInnerWidth < 700 ) {
			totalParticles *= HomeFieldConfig.screensHalftoneParticlesMobileScale;
		}
		
		this._radialHalftoneImage.setPool(this._shapeParticles.slice(0, totalParticles));

		const nonActive = this._shapeParticles.slice(totalParticles);

		nonActive.forEach( (p) => {
			p.deactivate();
		});


		//console.log(JSON.stringify(HomeFieldScreensPixelDataConfig.dataSet.halftone.rawGrid));

		//Home

		//this._radialHalftoneImage.setImgData(HomeFieldScreensPixelDataConfig.dataSet.halftone.rawGrid);
		let imageGrid = new ImagePixelDataGrid();
		imageGrid.setDataFromCache(HalftonePersonPixelCache.data);
		this._radialHalftoneImage.setImgData(imageGrid);
		//this._radialHalftoneImage.setImgData(HomeFieldScreensPixelDataConfig.dataSet.halftone2.rawGrid);
		//this._radialHalftoneImage.setImgData(HomeFieldScreensPixelDataConfig.dataSet.m.rawGrid);
		//this._radialHalftoneImage.offsetX = -0.073;
		this._radialHalftoneImage.offsetX = -0.045;
		this._radialHalftoneImage.offsetY = 0.3;
		

		HomeScreens.sceneProps = this._sceneProps;
		const relativeSizes = HomeScreens.getTotalRelativeSizes();
		//console.log('relativeSizes', relativeSizes);

		/**
		 * Desktop oreginal sizing: 
		 */
		this._radialHalftoneImage.create(0.055, 0.055, 0.0035);
		this._radialHalftoneImage.create(relativeSizes.w/15, relativeSizes.w/15, 0.0035);

		//Mobile sizing: 
		/*	
			this._radialHalftoneImage.offsetX = -0.045;
			this._radialHalftoneImage.offsetX = -0.095;
			this._radialHalftoneImage.offsetY = 0;
			this._radialHalftoneImage.create(relativeSizes.w/8, relativeSizes.w/8, 0.0035);
		*/

		//Phase in the screens: 
		/*
		DelayHelper.setTimeout( () => {
			this._radialHalftoneImage.offY();
		}, 6000, null, 'screens');

		DelayHelper.setTimeout( () => {
			HomeScreens.off('bottomRight')
		}, 7000, null, 'screens');

		DelayHelper.setTimeout( () => {
			HomeScreens.off('bottomLeft')
		}, 7000, null, 'screens');

		DelayHelper.setTimeout( () => {
			HomeScreens.off('topRight')
		}, 8000, null, 'screens');

		DelayHelper.setTimeout( () => {
			HomeScreens.off('topLeft')
		}, 8200, null, 'screens');
		*/

		let reduce = this._reduceFirstPass ? 0.55 : 1;

		this._comboCounter.clear();
		this._comboCounter.add( () => this._radialHalftoneImage.offY(), 6000 * reduce);
		this._comboCounter.add( () => {
			HomeScreens.off('bottomRight');
			HomeScreens.off('bottomLeft')
		}, 7000 * reduce);
		this._comboCounter.add( () => HomeScreens.off('topRight'), 8000 * reduce);
		this._comboCounter.add( () => HomeScreens.off('topLeft'), 8200 * reduce);

		//Turn on all screens: 
		HomeScreens.allOn(100);
		


	}


