	private _doWhoozyImage() {

		let item = HomeFieldScreensPixelDataConfig.getList()[1];

		//console.log('item', item)

		let zone = this._sceneProps.zones.screens;
		let size: geometry.Size = {
			w: (item.size.w/this._sceneProps.sizes.container.w)  * zone.scale ,
			h: (item.size.h/(window as any).cachedInnerHeight) * zone.scale 
		}

		size = {
			w: 0.04 * zone.scale,
			h: 0.24 * zone.scale 
		}

		let position: geometry.Vector2  = {
			x: 0 - (size.w/2),
			y: zone.cy - ((size.h/2) * 1.5)
		}

		position = {
			x: -0.024,
			y: 0.05
		}
	
		//Set frame height. 
		//Note do not have to set scale here as this is done at path level on scaling effects
		let ytweak = item.frame.tweakY ? -80 : 60;
		let xTweak =   item.frame.tweakX ? -80 : 60
		let frameHeightPx = item.size.h +  ytweak
		let scaledFrameHeight = frameHeightPx * zone.scale;
		let borderScaled = ytweak * zone.scale;
		let frameCorrected = scaledFrameHeight-borderScaled;
		let correctY  = (frameCorrected/(window as any).cachedInnerHeight)/2;

		let w = item.size.w +  xTweak;
		let frame = {
			w: w,
			wr: this._homeField.getRelativeWidth(w),
			h: frameHeightPx,
			hr: this._homeField.getRelativeHeight(frameHeightPx),
			x: zone.cx,
			y: position.y + correctY,

		}

		let resetAtStart = true;

		this._repositionGravityBurst(
			ParticleLayoutType.IMAGE_PIXEL_DATA, 
			position,
			size,  
			{pixelData: item.pixelData},
			this._shapeParticles,
			700,
			resetAtStart,
			frame
		);


		HomeScreens.allOff();

		/*
		DelayHelper.setTimeout( () => HomeScreens.on('topLeft'), 50, null, 'screens');
		DelayHelper.setTimeout( () => HomeScreens.on('bottomLeft'), 1800, null, 'screens');
		DelayHelper.setTimeout( () => HomeScreens.on('bottomRight'), 2000, null, 'screens');
		DelayHelper.setTimeout( () => HomeScreens.off('bottomRight'), 3000, null, 'screens');
		DelayHelper.setTimeout( () => HomeScreens.on('bottomRight'), 3700, null, 'screens');
		DelayHelper.setTimeout( () => HomeScreens.off('topLeft'), 3800, null, 'screens');
		DelayHelper.setTimeout( () => HomeScreens.off('bottomLeft'), 5500, null, 'screens');
		*/
		

		this._comboCounter.clear();
		this._comboCounter.add( () => HomeScreens.on('topLeft'), 50);
		this._comboCounter.add( () => HomeScreens.on('bottomLeft'), 1800);
		this._comboCounter.add( () => HomeScreens.on('bottomRight'), 2000);
		this._comboCounter.add( () => HomeScreens.off('bottomRight'), 3000);
		this._comboCounter.add( () => HomeScreens.on('bottomRight'), 3700);
		this._comboCounter.add( () => HomeScreens.off('topLeft'), 3800);
		this._comboCounter.add( () => HomeScreens.off('bottomLeft'), 5500);


				
	}

