import 'reflect-metadata';
import { Container } from 'inversify';
import { TYPES } from './types';
import { Logger, ComplexLogger } from 'app/contracts/Debug';
import { LoggerService } from 'app/services/debug/LoggerService';
import { ComplexLoggerService } from 'app/services/debug/ComplexLoggerService';

const iocContainer = new Container();
iocContainer.bind<Logger>(TYPES.Logger).to(LoggerService);
iocContainer.bind<ComplexLogger>(TYPES.ComplexLogger).to(ComplexLoggerService);
export { iocContainer };
