    loadPixels(item) {
        

        if(!item.imgURL) {
            return;
        }

        let loader =  new ImagePixelDataGrid(item.density,  (pixelData) => {
           
            this.dataSet[item.ref].pixelData = pixelData;
            this.dataSet[item.ref].rawGrid = loader;
        }, item.minspace);

        
        loader.byURL(item.imgURL, item.colourSetName);

    }

