//Contracts: 
import { particles } from 'app/contracts/Particles'; 
import { geometry } from 'app/contracts/Geometry'; 
//Enums: 
import { 
	LookupType, 
	RepellType, 
	AttractType,
	HomeParticleStateType
} from 'app/enum/Particles';
//Library: 
import { Particle } from 'app/library/particle/Particle';
import { ParticleProgressParallax } from 'app/library/particle/behaviour/ParticleProgressParallax';
import { ParticleMouseParallax } from 'app/library/particle/behaviour/ParticleMouseParallax';
import { ParticleRepell } from 'app/library/particle/behaviour/ParticleRepell';
import { ParticleAttract } from 'app/library/particle/behaviour/ParticleAttract';
import { ParticlePath } from 'app/library/particle/behaviour/ParticlePath';
import { ParticleTrailEmitter } from 'app/library/particle/behaviour/ParticleTrailEmitter';
import { ParticleTrail } from 'app/library/particle/behaviour/ParticleTrail';
import { ParticleCounter } from 'app/library/particle/behaviour/ParticleCounter';

//Controllers: 
import {HomeField} from 'app/controller/home/HomeField';
import { particles } from 'pixi.js';
import { ParticleHover } from 'app/library/particle/behaviour/ParticleHover';
import DelayHelper from 'debugbase/helper/DelayHelper';

export class ScreensGridParticle extends Particle {

	private _field: HomeField;
	private _activeState: HomeParticleStateType;
	private _repell: ParticleRepell;
	private _attract: ParticleAttract;
	private _orbitPath: ParticlePath;
	private _iconPath: ParticlePath;
	private _iconPath2: ParticlePath;
	private _triPath: ParticlePath;
	private _rectPath: ParticlePath;
	private _mouseParallax: ParticleMouseParallax;
	private _trailEmitter: ParticleTrailEmitter;
	private _trail: ParticleTrail;
	private _hover: ParticleHover;
	private _counter: ParticleCounter;

	private _openingStateSet: boolean  = false;
	private _openingHoverParticle: boolean;
	private _homeSpawned: boolean = false;
	private _outterEdge: boolean = false;
	private _inOrbit: boolean = false;

	private _centerX: number;

	private _side: number;
	private _homeIconSide: number;
	private _gravityHomeDirection: number = 1;

	private _homeIntroComplete: boolean = false;
	private _mouseDownXEase: number = 1;

	private _checkHomeReachedCounter: number = 0;


	public set field(v : HomeField) {
		this._field = v;
	}

	public get field(): HomeField {
		return this._field;
	}

	public get isHomeTrail(): boolean {
		return (this._randoms.homeSpare === true);
	}

	public changeState(state: HomeParticleStateType, brute: boolean = false): void {
		
		this._activeState = state;

		
		//if (state === HomeParticleStateType.HOME && this._sceneProps.progress.ratio != 0.5) {
		//	return;
		//}

	
		
		
	
		
		this.deactivateAllBehaviours(true);

		if (state === HomeParticleStateType.OPENING) {
			this._openingState();
		} else if (state === HomeParticleStateType.OPENING_HOVER) {
			this._openingHoverState();
		} else if (state === HomeParticleStateType.HOME) {
			//this._preHomeState();
			this._iconHomeState();
		} else if (state === HomeParticleStateType.BLACK_HOLE_FOLLOW) {
			//this._preHomeState();
			this._blackHoleFollow();
		} else if (state === HomeParticleStateType.HOME_SCREENS) {
			this._homeScreensState();
		} else if (state === HomeParticleStateType.HOME_SPACES) {
			this._homeSpacesState();
		} else if (state === HomeParticleStateType.SCREENS) {
			this._screensState();
		} else if (state === HomeParticleStateType.SPACES) {
			this._spacesState();
		}	


	}

	public spawnForHome(): void {
		this._homeState();
	}

	public setMouseParallaxByScale(): void {

		this._animatedProps.setEase({
			x:  this._animatedProps.targetScale * 100,
			y: this._animatedProps.targetScale * 50,
			scale: this._animatedProps.targetScale * 50,
		});

		this._mouseParallax.setup({
			ease: {x: 20, y: 20}, 
			power: {x: (0.05 * this._animatedProps.targetScale), y: (0.08 * this._animatedProps.targetScale)} 
		});

	}

	public tick(): void {
		
		super.tick();
		
		if (this._activeState === HomeParticleStateType.HOME) {
			this._homeTick();
		}


		//Orbit path can be scaled when mouse down: 
		if (this._activeState ===  HomeParticleStateType.BLACK_HOLE_FOLLOW) {
			this._orbitPath.expandMuliplier = this._sceneProps.mouse.isDown ? 1.75 + (Math.abs(this._sceneProps.progress.velocity) * 30) : 1;
			this._orbitPath.speedMuiliplier = this._sceneProps.mouse.isDown ? 1.4  + (Math.abs(this._sceneProps.progress.velocity) * 50) : 1;
			//this._position.trackMouse = (!this._sceneProps.mouse.isDown);

			this._repell.active = (Math.abs(this._sceneProps.progress.velocity) > 0.001) ? false : true;
	

			this._position.xOffset = -1 * (this._sceneProps.progress.velocity * 1.5);

			//this._position.updateTrackMouse = (!this._sceneProps.mouse.isDown);
		} 

	}

	public homeSpawn(): void {
		return;
		this.deactivateAllBehaviours(true);
		this._homeSpawn();
	}

	public colourise(side: number): void {
		if(this._openingHoverParticle){
			this._colouriseSide(side);
		}
	}

	public homeIntroComplete(): void {
		//this._homeIntroComplete = true;
		//If a side spawned particle, make the attract active to pull into center:
		if (this._randoms.includeInHome) {
			//this._startHomeAttract();
		}	
	}

	public homeStartIconLoop(): void {
		//If an icon orbit particle then start that action up: 
		/*if (this._randoms.iconParticle) {
			this._homeStartIconObrit();
		}*/
	}

	public setIntialState(state: HomeParticleStateType): void {

		if (state === HomeParticleStateType.OPENING) {
			this._animatedProps.targetScale = this._animatedProps.scale = 0;
		}
	}

	public activateAsTrail(emitterParticleProps: particles.ParticleProps, emitterAnimatedProps: particles.ParticleProperties, lifetimeFramesBase: number, lifetimeFramesSeed: number): void {
			
		this.deactivateAllBehaviours(true);

		//Match the particle positiion to the 
		//this.position.set({
		//	x: emitterParticleProps.ref.position.x,
		//	y: emitterParticleProps.ref.position.y
		//}, true);

		this.position.active = false;
		this._animatedProps.x = this._animatedProps.targetX = emitterAnimatedProps.x;
		this._animatedProps.y = this._animatedProps.targetY = emitterAnimatedProps.y;

		//Match the colour of the emitter: 
		this.colour.set( emitterParticleProps.ref.colour.get() );

		//Now configure the trail item 
		this._trail.active = true;
		this._trail.alpha = emitterAnimatedProps.alpha; 
		this._trail.scale = emitterAnimatedProps.scale; 
		this._trail.randomiseLifetime(lifetimeFramesBase, lifetimeFramesSeed); 



		this._repell.active = true;
		//this._repell.target = this._sceneProps.targets.screenCenter;
		this._repell.target = this._sceneProps.targets.mouse;
		//this._repell.type = RepellType.BOUNDARY;
		this._repell.type = RepellType.PUSH;
		//Allow for 150px circle and very tight ring: 
		this._repell.radius = 25;	
		this._repell.randomisePower(10, 20);

	}

	//OVERRIDED FROM BASE: 
	//-------------------------------------------------------------------------------------------------------
	private _openingHoverScreens: boolean;
	private _openingHoverSpaces: boolean;

	protected createBehaviours(config: particles.ParticleConfig): void {

		//Only a small range of particles need to be attracted to the intro 
		
		this._randoms = {};
		this._openingHoverParticle = (Math.random() <= 0.25);
		this._openingHoverParticle = true;
		if (this._openingHoverParticle) {
			
			let coloured = (Math.random() <= 0.4);

			if (coloured) {
				let colour = Math.random();
				this._openingHoverScreens = (colour < 0.5);
				this._openingHoverSpaces = (colour >= 0.5);
			}

		}
	
		this._homeIconSide = (window as any).homeField.getHomeIconSide();

		if (this._homeIconSide) {
			this._randoms.iconParticle = true;
			this._colouriseSide(this._homeIconSide);
		} else {
			this._randoms.includeInHome = (Math.random() <= 0.6);
		}

		//If particle not used in home for main side particles or emitter then can used for other usage (Like emitted particles)
		if ( !this._randoms.includeInHome && !this._randoms.iconParticle) {
			this._randoms.homeSpare = true;
		}
	
		//Add the parallax position controller:
		this._position = this._behaviourManager.add(ParticleProgressParallax);

		//Mouse parallax on particles: 
		this._mouseParallax = this._behaviourManager.add(ParticleMouseParallax);
	
		//Add orbit effect for blank hole and random ossocliation: 
		this._orbitPath = this._behaviourManager.add(ParticlePath); 
		this._orbitPath.path = LookupType.CIRCLE;
		//this._orbitPath.path = LookupType.ICON;
		this._orbitPath.randomise();

		this._iconPath = this._behaviourManager.add(ParticlePath);  
		this._iconPath.path = LookupType.ICON;
		this._iconPath.randomise();

		this._iconPath2 = this._behaviourManager.add(ParticlePath);  
		this._iconPath2.path = LookupType.ICON2;
		this._iconPath2.randomise();

		this._triPath = this._behaviourManager.add(ParticlePath);  
		this._triPath.path  = LookupType.TRI;
		this._triPath.randomise();

		this._rectPath = this._behaviourManager.add(ParticlePath);  
		this._rectPath.path  = LookupType.RECT;
		this._rectPath.randomise();
		


		//Add repell behaviour: 
		this._attract =  this._behaviourManager.add(ParticleAttract); 
		this._repell =  this._behaviourManager.add(ParticleRepell); 


		this._mouseParallax.active = true;
		this._mouseParallax.setup({ease: {x: 20, y: 20}, power: {x: 0.05, y: 0.05} });

		this._counter = this._behaviourManager.add(ParticleCounter);

		//If the particle can emit particles in a trail then 
		//if (this._randoms.iconParticle) {
			this._trailEmitter =  this._behaviourManager.add(ParticleTrailEmitter); 
		//}

		//If the particle is spare then can used for trail.
		if (this._randoms.homeSpare) {
			this._trail =  this._behaviourManager.add(ParticleTrail); 
		} 


		this._hover = this._behaviourManager.add(ParticleHover); 

		//Deactivate all behaviours but the 'base' ones
		this.deactivateAllBehaviours(true);



	}

	//PRIVATE METHODS 
	//--------------------------------------------------------------------------------------------------
	private _openingState(): void {
		this._animatedProps.targetScale = 0;
		/*this._animatedProps.setEase( {
			x: 20,
			y: 10,
			scale: 30
		} ) */
		this._animatedProps.setEase( {
			x: 1,
			y: 1,
			scale: 1
		} );
	
	}

	private _openingHoverState(): void {
		
		//If Particle has been selected for the range that get attracted to around the circle circle:
		//Otherwise just leave hidden on the grid:  
		if (this._openingHoverParticle) {

			//Deactivate normal parallax positioning: 
			//this._position.set({x: 0.5, y: 0.5});

			//Attract to center:  
			this._attract.active = true;
			this._attract.target = this._sceneProps.targets.mouse;
			this._attract.instant = true;

			this._repell.target = this._sceneProps.targets.screenCenter;

			//Repell from the center of the screen around the edge bounday of a radius:
			this._repell.active = true;
			this._repell.target = this._sceneProps.targets.screenCenter;
			this._repell.type = RepellType.BOUNDARY;
			this._repell.testAtCount = 1;
			//Allow for 150px circle and very tight ring: 
			this._repell.radius = 80;	
			this._repell.randomisePower(55, 25);

			this._animatedProps.setEase({x: 4, y: 4});

			//Activate the orbit path to create black hole effect around the intial hover element: 
			//The orbit path also makes the scale of the particle 

			this._animatedProps.targetAlpha = 0.1 + (Math.random() * 0.3);

			this._setupPath(this._orbitPath, {
				//--> OLD x: [25, 40], y: [25, 40], speed: [0.5, 0.7], scale: [0.2, 0.25], alpha: [0.2, 0.25], 
				x: [25, 5], y: [25, 5], speed: [0.5, 0.7], scale: [0.05, 0.15], alpha: [0.03, 0.2], 
				randomise: {unsync: true} 
			});

			




			/*if (this._openingHoverScreens) {
				this.colour.set(0xf24a8d);
			} else if (this._openingHoverSpaces) {
				this.colour.set(0x34dccd);
			} else  {
				let colour = (Math.random() < 0.5) ? 0xffffff : 0xcecece;
				this.colour.set( colour );
			}*/

		}

	}


	private _blackHoleFollow(): void {

			if (this._sceneProps.mouse.isDown || this._sceneProps.progress.ratio != 0.5) {
				return;
			}
	

			//Repell from the center of the screen around the edge bounday of a radius:
			this._attract.active = false;
			this._attract.target = this._sceneProps.targets.mouse;
			this._attract.instant = true;
			this._attract.powerX = 0.001;
			this._attract.powerY = 0.001;

			this._repell.active = true;
			this._repell.target = this._sceneProps.targets.mouse;
			this._repell.type = RepellType.PUSH;
			this._repell.testAtCount = 1;
			//Allow for 150px circle and very tight ring: 
			this._repell.radius = 120;	
			this._repell.randomisePower(80, 40);


			this._position.trackMouse = true;

	
			//Activate the orbit path to create black hole effect around the intial hover element: 
			//The orbit path also makes the scale of the particle 
			this._animatedProps.setEase( {
				x: 5 + (Math.random() * 40),
				y: 3 + (Math.random() * 40),
				scale: 10
			} );

			this._animatedProps.targetAlpha = 0.3 + (Math.random() * 0.4);

			
			this._setupPath(this._orbitPath, {
				//--> OLD x: [25, 40], y: [25, 40], speed: [0.5, 0.7], scale: [0.2, 0.25], alpha: [0.2, 0.25], 
				x: [25, 60], y: [25, 60], speed: [0.5, 0.7], scale: [0.05, 0.15], alpha: [0.6, 0.2], 
				randomise: {unsync: true} 
			});

			

			


	}


	private _checkDistanceFromIcon: boolean = false;

	private _homeTick(): void {

		if (this._sceneProps.mouse.isDown && this._sceneProps.progress.ratio != 0.5) {
			this._animatedProps.setEase({
				x: this._mouseDownXEase
			})
		} 
		
		if (this._homeIntroComplete) {
			
			/*if (this._homeSpawned) {
				this._checkHomeReachedCounter++;
				this._checkDistanceFromIcon = false;
				if (this._checkHomeReachedCounter >= 3) {
					this._checkHomeReachedCounter = 0;		
					this._checkDistanceFromIcon = true;	
				}
				this._checkHomeReachedIcon(this._checkDistanceFromIcon);
				this._checkCheckBlackHoleDirection();
			} */

	
		}

		if (this._homeIconSide) {

			//this._counter.active = true;
			//this._iconPath.speed -= 0.001;
			//this._iconPath2.speed -= 0.001;
		}

	}

	private _checkCheckBlackHoleDirection(): void {
		if (this._sceneProps.mouse.scene.x > 0.5) {
			this._gravityHomeDirection = 1;
		} else {
			this._gravityHomeDirection = -1;
		}
	}

	private _homeStartIconObrit(): void {

		//Randomise between the path that goes through the gap and the path hat goes around the sepereated part of the icon:

		//For trail ->> this._counter.randomiseTriggerAt(0, 20);
		this._counter.randomiseTriggerAt(0, 300);


		this._counter.onTrigger = () => { this._homeIconSpawn(); };
		this._counter.start();

	}

	private _iconHomeState(): void {
		

		this._position.trackMouse = false;
		this._mouseParallax.active = false;

		this.colourise(1);


		if (this.hasTag('icon'))  {

			this._repell.active = false;

			/*this._repell.target =  this._sceneProps.targets.mouse;
			this._repell.type = RepellType.PUSH;
			this._repell.testAtCount = 2;
			this._repell.radius = 10;
			*/
			
			this._mouseParallax.active = false;

			this._animatedProps.targetScale = 0.12;
	
			let zIndexRatio = this._animatedProps.targetScale / 0.12;
	
			this._animatedProps.setEase( {
				x: 1 + (zIndexRatio * 19),
				y: 1 + (zIndexRatio * 9),
				scale: 20
			} )

			this._mouseDownXEase = 2 + (Math.random() * 20);
	
			this._animatedProps.targetAlpha = 0.5;
	
			//Allow for 150px circle and very tight ring: 
			this._repell.randomisePower(30, 60);
	
			this._orbitPath.active = false;
			this._attract.active = false;


			this._repell.active = true;

			this._position.trackMouse = false;

			this._repell.active = true;
			this._repell.target = this._sceneProps.targets.mouse;
			this._repell.type = RepellType.PUSH;
			this._repell.testAtCount = 1;
			//Allow for 150px circle and very tight ring: 
			this._repell.radius = 50;	
			this._repell.randomisePower(50, 10);
	
	
			//Lamp / Hover effect: 
			this._hover.active = true;
			this._hover.radius = 200;
			this._hover.setAlpha(0.5, 1);
			this._hover.setScale(0.12, 0.16);
			this._hover.target = this._sceneProps.targets.mouse;

			return;

		}


		//Is a 'spare' particle. So in the case just hide quickly:  
		this._animatedProps.targetScale  = 0;
		this._animatedProps.targetAlpha  = 0;
		
		

	}

	private _preHomeState(): void {

		if (this._openingHoverParticle) {

			//Repell from the center of the screen around the edge bounday of a radius:
			this._repell.active = false;
			
			//Activate the orbit path to create black hole effect around the intial hover element: 
			//The orbit path also makes the scale of the particle 
			this._orbitPath.active = false;

			this._attract.active = true;
			this._attract.instant = false;
			this._attract.type = AttractType.DIRECT;
			let side;
			
			if (this._openingHoverScreens) {
				this._attract.targetPos = {x: this.ICON_FOCUS_SCREENS_X, y: 0.45 };
				side = -1;
			} else if (this._openingHoverSpaces) {
				this._attract.targetPos = {x: this.ICON_FOCUS_SPACES_X, y: 0.45 };
				side = 1;
			} else if (this._animatedProps.x <  (this._sceneProps.sizes.window.w / 2) ) {
				let verticalSide = (Math.random() < 0.5) ? -1 : 1;
				this._attract.targetPos = {x: this.ICON_FOCUS_SCREENS_X, y: 0.45 + (verticalSide * (Math.random() * 0) )};
				side = -1;
			} else {
				this._attract.targetPos = {x: this.ICON_FOCUS_SPACES_X, y: 0.45};
				side = 1;
			}
			
			this._colouriseSide(side);
			this._attract.randomisePowerX(0.0005, 0.003);
			this._attract.randomisePowerY(0.005, 0.003);

			//When complete then startup the particle icon orbit: 
		
			this._counter.randomiseTriggerAt(20, 0);
			this._counter.onTrigger = () => {
				this._field.homeParticleReachedIcon();
			};
			this._counter.start();

		}
	}	

	/** On transition to home set the particles to intial state */
	private _homeState(): void {

		return;

		if (this._randoms.includeInHome) {
			this._homeSpawn(true);
		} else {
			this._animatedProps.targetScale = 0;
		}

	}

	private _homeIconSpawn(): void {



		return;
		let type = 'no-trail';

		
		this._iconPath2.active = false;
		this._iconPath.active = false;
		let iconPath = (Math.random() < 0.5) ? this._iconPath2 :  this._iconPath;
		iconPath.active = true;

		this._setupPath(iconPath, {
			x: [(147 * this._sceneProps.sizes.displayScale), 0], y: [(235  * this._sceneProps.sizes.displayScale), 0], speed: [1.2, 2], 
			randomise: {unsync: true}
		});



		//Move to correct target 
		//TODO change this so the target is anchored to the D icon 
		this.position.set({x: 0.4879, y: 0.3376}, true);

		this._attract.active = true;
		this._attract.instant = false;
		this._attract.targetPos = {x: 0.4879, y: 0.3376};
		this._attract.powerX = 0.001;
		this._attract.powerY = 0.001;

		this._repell.active = true;
		this._repell.testAtCount = 2;
		//this._repell.target = this._sceneProps.targets.screenCenter;
		this._repell.target = this._sceneProps.targets.mouse;
		//this._repell.type = RepellType.BOUNDARY;
		this._repell.type = RepellType.POSITION;
		//Allow for 150px circle and very tight ring: 
		this._repell.radius = 25;	
		this._repell.randomisePower(0.01, 0.01);

		//this.colour.set(0xffffff);
		this._colouriseSide(this._homeIconSide);
		
		let particleIndex = (this._homeIconSide === -1) ? 140 : 510;
		let position;

		if (this._iconPath.active) {
			this._iconPath.pathIndex = particleIndex;
			position = this._iconPath.hardPositionUpdate();
			this._iconPath2.active = false;
		} else {
			this._iconPath2.pathIndex = particleIndex;
			position = this._iconPath2.hardPositionUpdate();
		}

		if (type === 'trail') {
			this._trailEmitter.active = true;
			this._trailEmitter.randomiseChildLifetime(120, 40);
			this._trailEmitter.randomiseSpawnFrames(1, 0);
		} else {
			this._trailEmitter.active = false;
		}

		let speedScreens = (type === 'trail') ? 3.5 : 2;
		let speedSpaces;
		if (this._iconPath2.active) {
			 speedSpaces = (type === 'trail') ? 2 : 1;
		} else {
			 speedSpaces = (type === 'trail') ? 3 : 1.6;
		}

		if (this._homeIconSide === -1) {
			this._counter.randomiseTriggerAt(130, 60);
			this._iconPath.speed = speedScreens * 0.9;
			this._iconPath2.speed = speedScreens * 0.8;
		} else {
			this._counter.randomiseTriggerAt(210, 60);
			this._iconPath.speed = speedSpaces * 0.8;
			this._iconPath2.speed = speedSpaces * 0.8;
		}

		let lifetimeBase = (type === 'trail') ? 230 : 100;
		let lifetimeSeed = (type === 'trail') ? 60 : 30;
		this._trailEmitter.randomiseLifetime(lifetimeBase, lifetimeSeed);

		if (type === 'trail') {
			this._animatedProps.alpha =  this._animatedProps.targetAlpha = 0.3;
			this._animatedProps.scale = this._animatedProps.targetScale = 0.25;
			this._iconPath2.direction = (Math.random() < 0.5) ? -1 : 1;
			this._iconPath.direction = (Math.random() < 0.5) ? -1 : 1;
		} else {
			this._animatedProps.alpha =  this._animatedProps.targetAlpha = 0.2;
			this._animatedProps.scale = this._animatedProps.targetScale = 0.25;
			this._iconPath2.direction = (this._sceneProps.mouse.cords.x < this._sceneProps.sizes.window.w / 2) ? -1 : 1;
			this._iconPath.direction = (this._sceneProps.mouse.cords.x < this._sceneProps.sizes.window.w / 2) ? -1 : 1;
		}

		this._trailEmitter.alpha = this._animatedProps.alpha;
		this._trailEmitter.scale = this._animatedProps.scale;
		this._orbitPath.active = false;

		this._counter.onTrigger = () => { 
			this._homeIconSpawn(); 
		};
		this._counter.start();

	}

	private readonly ICON_FOCUS_SCREENS_X: number = 0.488;
	private readonly ICON_FOCUS_SPACES_X: number = 0.517;

	private _startHomeAttract(): void {
		
		let verticalSide: number  = (Math.random() < 0.5) ? 1 : -1;
		this._attract.active = true;

		this._attract.type = AttractType.DIRECT;
		
		this._attract.instant = false;

		this._animatedProps.setEase({
			x: 4,
			y: 4,
			scale: 200,
			alpha: 140,
		});

		if (this._side === 1) {
			this._attract.targetPos = {x: this.ICON_FOCUS_SPACES_X, y: 0.45 + ( (0.15 * Math.random() ) * verticalSide)};
			this._attract.randomisePowerY(0.0001, 0.0001);
		} else {
			this._attract.targetPos = {x: this.ICON_FOCUS_SCREENS_X, y: 0.45 + ( (0.15 * Math.random() ) * verticalSide)};
			this._attract.randomisePowerY(0.0001, 0.0001);
		}

		this._attract.randomisePowerX(0.0001, 0.0005);
		let distance = Math.sqrt(Math.pow(this._animatedProps.x - this._sceneProps.targets.sceneCenter.position.x , 2) + Math.pow(this._animatedProps.y - (this._sceneProps.sizes.window.h * 0.45), 2));
	
		this._setupPath(this._orbitPath, {
			x: [0, 120], y: [0, 120], speed: [0.2, 0.4], scale: [0.1, 0.27], alpha: [0.3, 0.6],
			randomise: {unsync: true, direction: true}
		});

	}


	private _positionInCircle(center: geometry.Vector2 , angle: number, radius: number, yRatio: number = 1): geometry.Vector2 {
		return  {
			x: center.x + (radius * Math.sin(Math.PI * 2 * angle / 360)),
			y: center.y + ((radius * Math.cos(Math.PI * 2 * angle / 360)) * yRatio)
		};
	}

	private _homeSpawn(fromSetup: boolean = false): void {

		return;

		this._side  = (Math.random() < 0.5) ? 1 : -1;
		let verticalSide: number = (Math.random() < 0.5) ? 1 : -1;
		let center: number = this._sceneProps.targets.sceneCenter.position.x;
		let w: number = this._sceneProps.sizes.window.w;
		let h: number = this._sceneProps.sizes.window.h;

		//Set scale to 0 
		this._animatedProps.scale = 0;

		this._colouriseSide(this._side);

		//If from setup then animate from center of D to randomise position
		//Otherwise spawn randomly from emitter position defined on field and attract to center straight away: 
		if (fromSetup) {
			
			//this._animatedProps.x  = this._position.getAbsX(0.5);
			//this._animatedProps.y = this._animatedProps.targetY = this._position.getAbsY(0.45);

			//let x = 0.5 + (this._side * ((0.08 + (Math.random() * 0.10) )));
			//let y = 0.45 + (verticalSide * ((Math.random() * 0.15) ));

			let targetPositionX = (this._side === -1) ? this.ICON_FOCUS_SCREENS_X : this.ICON_FOCUS_SPACES_X;
			//let radius = 0.12 + (Math.random() * 0.05);
			//let radius = 0.01 + (Math.random() * 0.01);
			let radius = 0.05 + (Math.random() * 0.1);
			let angle ;
			if (this._side === -1) {
				 angle = 300 - (Math.random() * 60);
			} else {
				 angle = 60 + (Math.random() * 60);
			}
			let p: geometry.Vector2 = this._positionInCircle({x: targetPositionX, y: 0.45}, angle, radius, 2.3);


			//this._animatedProps.targetX = this._position.getAbsX(x);
		//	this._animatedProps.targetY = this._position.getAbsX(y);
			//this._position.active = false;

			/*this._position.set({
				x: targetPositionX,
				y: 0.45
			}, true);
			*/

			this._position.set(p, true);

		 this._animatedProps.setEase({
				x: 1.3,
				y: 1.3,
				scale: 10,
				alpha: 10,
			});

			this._animatedProps.scale = this._animatedProps.targetScale = 0.3 + (Math.random() * 0.8);
			this._animatedProps.alpha = this._animatedProps.targetAlpha = 0.0000001;

			//this._animatedProps.alpha = this._animatedProps.targetAlpha = 0.3;

			this._attract.targetPos = {x: p.x, y:  p.y};
		
			this._attract.randomisePowerX(0.0005, 0.0005);
			this._attract.randomisePowerY(0.0015, 0.0015);

			this._attract.instant = false;
			this._attract.onTargetReached = () => {
				this._startHomeAttract();
				//Make sure this only fires once:
				this._homeIntroComplete = true;
				this._attract.onTargetReached = null;
			};

			this._attract.active = true;
			this._attract.active = false;


			DelayHelper.setTimeout( () =>  {
				this._startHomeAttract();
				this._homeIntroComplete = true;	
			}, Math.random() * 400);


		} else {
			let spawnType: string = (this._side === -1) ? 'screens'  : 'spaces';
			let spawnPos = this._field.getSpawnPosition(spawnType);

			let vSide = (Math.random() < 0.5) ? -1 : 1;
			let hSide = (Math.random() < 0.5) ? -1 : 1;
		
			this.position.set({x: (spawnPos.x + ( (Math.random() * 0.001) * vSide) ), y: (spawnPos.y  + ( (Math.random() * 0.003) * hSide)) }, true);
			
			//Attarct to center straight away: 
			this._startHomeAttract();

			this._animatedProps.scale = this._animatedProps.targetScale = 0.3 + (Math.random() * 0.8);
			this._animatedProps.alpha = this._animatedProps.targetAlpha = 0.0000001;

		}

	




		this._repell.active = true;
		//this._repell.target = this._sceneProps.targets.screenCenter;
		this._repell.target = this._sceneProps.targets.mouse;
		//this._repell.type = RepellType.BOUNDARY;
		this._repell.type = RepellType.POSITION;
		//Allow for 150px circle and very tight ring: 
		this._repell.radius = 25;	
		this._repell.testAtCount = 3;
		this._repell.randomisePower(0.003, 0.003);

		this._homeSpawned = true;
		this._outterEdge = true;
		this._trailEmitter.active = false;

	}

	//Check if the paricle has reached the icon. If so joing the main orbit and then tell HomeField to randomly spawn another 
	private _checkHomeReachedIcon(checkDistance: boolean): void {
		
		let targetPosition;
		if (this._side === -1) {
			targetPosition = this.ICON_FOCUS_SCREENS_X;
			this._centerX = this._sceneProps.targets.sceneCenter.position.x - 38;
		} else {
			this._centerX = this._sceneProps.targets.sceneCenter.position.x + 65;
			targetPosition = this.ICON_FOCUS_SPACES_X;
		}

		if (this._outterEdge) {
			
			let distance;

			if (checkDistance) {
				distance = Math.sqrt(Math.pow(this._animatedProps.x - this._centerX, 2) + Math.pow(this._animatedProps.y - (this._sceneProps.sizes.window.h * 0.45), 2));
				this._trailEmitter.active = false;
			}


			//if (Math.random() < 0.0001) {
			//	this._trailEmitter.burst(Math.round(10 +  (Math.random() * 20) ), this._side);
			//}


			if (checkDistance && distance < 340) {
				
				/*this._attract.type = AttractType.ORBIT;
				this._attract.targetPos  = {
					x: 0.5,
					y: 0.45
				};
				this._inOrbit = true;
				this._outterEdge = false;
				*/

				if (Math.random() < 0.01) {
					this._trailEmitter.active = true;
				} 

				let pathWidth = 340;
				let pathHeight = 300;
				let pathIndex: number = (this._sceneProps.lookups.circle as any).getClosestPoint(
					{x: this._centerX, y:  this._sceneProps.sizes.window.h * 0.45},
					{w: 340, h: 300 },
					{x: this._animatedProps.x, y: this._animatedProps.y },
					135
				);

				if (distance < 100 && pathIndex === -1) {
					pathIndex  = (this._sceneProps.lookups.circle as any).getClosestPoint(
						{x: this._centerX, y:  this._sceneProps.sizes.window.h * 0.45},
						{w: 100, h: 100},
						{x: this._animatedProps.x, y: this._animatedProps.y },
						50
					);
					pathWidth = 100;
					pathHeight = 100;
				} 

				if (pathIndex !== -1) {

			
					this._position.set({x: targetPosition, y: 0.44});
					this._repell.active = false;
			
					this._setupPath(this._orbitPath, {
						x: [pathWidth, 0], y: [pathHeight, 0], speed: [0.1, 0.1],
						randomise: {unsync: true, direction: false}
					});

					this._animatedProps.setEase({
						x: 10,
						y: 10,
					});

					//this._triPath.pathIndex = pathIndex;

					this._orbitPath.pathIndex = pathIndex;

		
					this._inOrbit = true;
					this._outterEdge = false;


							this._repell.active = true;
						//this._repell.target = this._sceneProps.targets.screenCenter;
						this._repell.target = this._sceneProps.targets.mouse;
						//this._repell.type = RepellType.BOUNDARY;
						this._repell.type = RepellType.POSITION;
						//Allow for 150px circle and very tight ring: 
						this._repell.radius = 25;	
						this._repell.randomisePower(0.003, 0.003);

						this._attract.onTargetReached = null;
						this._attract.active = true;
						this._attract.instant = false;
						this._attract.targetPos = {x: targetPosition, y: 0.45};
						this._attract.randomisePowerX(0.0001, 0.0001);
						this._attract.randomisePowerY(0.0001, 0.0001);
					
				}

			}

		} else if (this._inOrbit) {

			this._orbitPath.direction = this._gravityHomeDirection;
	
			//this._orbitPath.xMuliplier = this._orbitPath.xMuliplier * 0.9905;
			//this._orbitPath.yMuliplier = this._orbitPath.yMuliplier * 0.9905;
			//this._orbitPath.speed = this._orbitPath.speed * 1.002;

			this._orbitPath.xMuliplier = this._orbitPath.xMuliplier * 0.992;
			this._orbitPath.yMuliplier = this._orbitPath.yMuliplier * 0.992;
			this._orbitPath.speed = this._orbitPath.speed * 1.001;
			
			if (this._orbitPath.yMuliplier < 130) {
				this._inOrbit = false;
			}

		} else {

			//this._orbitPath.xMuliplier = this._orbitPath.xMuliplier * 0.95;
			//this._orbitPath.yMuliplier = this._orbitPath.yMuliplier * 0.95;
			//this._orbitPath.speed = this._orbitPath.speed * 1.002;
			this._orbitPath.xMuliplier = this._orbitPath.xMuliplier * 0.972;
			this._orbitPath.yMuliplier = this._orbitPath.yMuliplier * 0.972;
			this._orbitPath.speed = this._orbitPath.speed * 1.001;

			//if ( this._animatedProps.x < this._centerX + 50 && this._animatedProps.x > this._centerX - 20) {
			if (checkDistance) {
				let distance = Math.sqrt(Math.pow(this._animatedProps.x - this._centerX, 2) + Math.pow(this._animatedProps.y - (this._sceneProps.sizes.window.h * 0.45), 2));
				if (distance < 20) {
					this.deactivateAllBehaviours(true);
					this.field.homeParticleReachedIcon();
					this._homeSpawn();
				}
			}
		}

	}		

	//Home icon has joined the icons orbit: 
	private _homeJoinIconOrbit(): void {
		
		//Not in the ICONS orbit so added to wider particle field: 
		let triPath: boolean = (Math.random() <= 1.0);

		if (triPath) {
			
			this._setupPath(this._triPath, {
				x: [120, 0], y: [120, 0], speed: [0.3, 0.3], randomise: {unsync: true, direction: false}
			});

			this._setupPath(this._orbitPath, {
				x: [0, 30], y: [0, 30], speed: [0.3, 0.6], scale: [0.1, 0.2], alpha: [0.4, 0.8], 
				direction: -1, randomise: {unsync: true, direction: false}
			});

			this.position.set({x: 0.5, y: 0.45});

		} else {
			//Fill in the center with a small ball or particles going around a circle radius:
			this._setupPath(this._orbitPath, {
				x: [0, 60], y: [0, 60], speed: [0.3, 0.6], scale: [0.1, 0.2], alpha: [0.4, 0.8], 
				direction: -1, randomise: {unsync: true, direction: false}
			});
			this.position.set({x: 0.5, y: 0.45});
		}

		this._repell.active = true;
		//this._repell.target = this._sceneProps.targets.screenCenter;
		this._repell.target = this._sceneProps.targets.mouse;
		//this._repell.type = RepellType.BOUNDARY;
		this._repell.type = RepellType.POSITION;
		//Allow for 150px circle and very tight ring: 
		this._repell.radius = 25;	
		this._repell.randomisePower(0.007, 0.007);

		this._attract.onTargetReached = null;
		this._attract.active = true;
		this._attract.instant = false;
		this._attract.targetPos = {x: 0.5, y: 0.45};
		this._attract.randomisePowerX(0.0001, 0.0001);
		this._attract.randomisePowerY(0.0001, 0.0001);

	}

	private _setupPath(target: ParticlePath, props: any): void {
		
		target.active = true; 

		target.xMuliplier = props.x[0] + (Math.random() * props.x[1]);
		target.yMuliplier = props.y[0] + (Math.random() * props.y[1]);
		target.speed = props.speed[0] + (Math.random() * props.speed[1]);

		if (props.alpha) {
			target.alphaActive = true;
			target.alphaRange = {min: props.alpha[0], max: props.alpha[1]};
		} else {
			target.alphaActive = false;
		}

		if (props.scale) {
			target.scaleActive = true;
			target.scaleRange = {min: props.scale[0], max: props.scale[1]};
		} else {
			target.scaleActive = false;
		}

		target.direction = (props.direction)  ? props.direction : 1;

		if (props.randomise) {
			this._orbitPath.randomise(props.randomise.unsync, props.randomise.direction);
		}

	}

	private _homeScreensState(): void {
		
		this._colouriseSide(1);


		if (this.hasTag('shape'))  {

			
			this._repell.active = true;

			this._position.trackMouse = false;

			this._repell.active = true;
			this._repell.target = this._sceneProps.targets.mouse;
			this._repell.type = RepellType.PUSH;
			this._repell.testAtCount = 1;
			//Allow for 150px circle and very tight ring: 
			this._repell.radius = 50;	
			this._repell.randomisePower(50, 10);

			this._mouseParallax.active = true;
			this._mouseParallax.setPower({
				x: 0.001,
				y: 0.001 
			})
			


			this._animatedProps.setEase( {
				x: 10 + (Math.random() * 50),
				y: 8 + (Math.random() * 40),
				scale: 30,
				alpha: 30
			} )

			this._animatedProps.targetAlpha = 0.7 + (Math.random() * 0.3);

			this._orbitPath.active = false;
			this._attract.active = false;


			this._animatedProps.targetAlpha = 0.9 + (Math.random() * 0.1);
			this._animatedProps.targetScale = 0.1 + (Math.random() * 0.2);

			DelayHelper.setTimeout(() => {
				
				this._animatedProps.targetScale = 0.07 + (Math.random() * 0.05);
				this._mouseParallax.active = true;
				this._mouseParallax.setPower({
					x: 0.015,
					y: 0.03 
				})

				//this._animatedProps.targetAlpha = 0.7 + (Math.random() * 0.3);

				DelayHelper.setTimeout(() => {
					this._hover.active = true;
					this._hover.radius = 80;
					this._hover.setAlpha(this._animatedProps.targetAlpha,  this._animatedProps.targetAlpha + 0.5);
					this._hover.setScale(this._animatedProps.targetScale, this._animatedProps.targetScale + 0.1);
					this._hover.target = this._sceneProps.targets.mouse;
				}, 500);
		
				this._repell.active = true;

			}, 1000);
			


			

			return;

		}


		//this._animatedProps.targetScale  = 0;
		//this._animatedProps.targetAlpha  = 0;
		//return;

		
		this.position.set({x: 0.165, y: 0.4});
		this.position.trackMouse = false;


	

		this._setupPath(this._rectPath, {
			//--> OLD x: [25, 40], y: [25, 40], speed: [0.5, 0.7], scale: [0.2, 0.25], alpha: [0.2, 0.25], 
			x: [350, 2], y: [400, 2], speed: [1, 1], scale: [0.05, 0.15], alpha: [0.03, 0.2], 
			randomise: {unsync: true} 
		});

		this.colourise(-2);
		//Is a 'spare' particle. So in the case just hide quickly:  
		
		this._animatedProps.targetScale  = 0.05 + (Math.random() * 0.5);
		this._animatedProps.targetAlpha  = 0.1 + (Math.random() * 0.3);

		this._mouseParallax.active = true;
		this._mouseParallax.setPower({
			x: 0.005,
			y: 0.008 
		})

		this._repell.active = true;
		this._repell.target = this._sceneProps.targets.mouse;
		this._repell.type = RepellType.PUSH;
		this._repell.testAtCount = 1;
		//Allow for 150px circle and very tight ring: 
		this._repell.radius = 40;	
		this._repell.randomisePower(40, 100);
		
		
	}

	public setFrame(config): void {
		this.position.xOffset = 0;
		this.position.set({x: config.x, y: config.y});
		this._rectPath.xMuliplier = config.w + (Math.random() * 2);
		this._rectPath.yMuliplier = config.h + (Math.random() * 2);
	}

	private _homeSpacesState(): void {

		this._position.trackMouse = false;


		if(this.hasTag('grid')) {
			this.setMouseParallaxByScale();
			DelayHelper.setTimeout( () => this.setMouseParallaxByScale(), 1000 );
			this._mouseParallax.active = true;
			this._tint.set(0xffffff);
			return;
		}

		this._animatedProps.targetScale  = 0;
		this._animatedProps.targetAlpha  = 0;


	}

	private _colouriseSide(side: number): void {

		
		if(side == -2) {
			this.colour.set(0xffffff);
		} else if (side === -1) {
			this.colour.set(this._sceneProps.colours.arrays.homeScreensSide[Math.floor( Math.random() * (this._sceneProps.colours.arrays.homeScreensSide.length - 0.01))]);
		} else {
			this.colour.set(this._sceneProps.colours.arrays.homeSpacesSide[Math.floor( Math.random() * (this._sceneProps.colours.arrays.homeScreensSide.length - 0.01))]);
		}

	}

	private _screensState(): void {}
	private _spacesState(): void {

	}

}
