//Contracts: 
import { particles } from 'app/contracts/Particles'; 
import { geometry, Vector2 } from 'app/contracts/Geometry'; 
//Enums: 
import { ParticleLayoutType, HomeParticleStateType, ParticleSceneType } from 'app/enum/Particles';
//Library: 
import { HomeParticle} from 'app/controller/home/HomeParticle';
import { ParticleField} from 'app/library/particle/ParticleField';
import { HomeTimelineMain } from './HomeTimelineMain';

import { MathHelper } from 'debugbase/helper/MathHelper';
import HomeFieldColourProcessor from './homeFieldProcessors/HomeFieldColourProcessor';
import HomeFieldScreensFieldProcessor from './homeFieldProcessors/HomeFieldScreensProcessor';
import HomeFieldIconProcessor from './homeFieldProcessors/HomeFieldIconProcessor';
import HomeFieldSpacesProcessor from './homeFieldProcessors/HomeFieldSpacesProcessor';
import HomeFieldIntroProcessor from './homeFieldProcessors/HomeFieldIntroProcessor';
import HomeFieldWorkProcessor from './homeFieldProcessors/HomeFieldWorkProcessor';
import HomeFieldTransitionParticlesProcessor from './homeFieldProcessors/HomeFieldTransitionParticlesProcessor';

import HomeFieldSceneProgressProcessor from './homeFieldProcessors/HomeFieldSceneProgressProcessor';

import { DepartmentType } from 'app/data/Enums';
import HomeFieldSystemsProcessor from './homeFieldProcessors/HomeFieldSystemsProcessor';
import DelayHelper from 'debugbase/helper/DelayHelper';


type ActiveSceneProcessorType = HomeFieldScreensFieldProcessor|HomeFieldSpacesProcessor|HomeFieldIconProcessor|HomeFieldWorkProcessor|HomeFieldIntroProcessor|HomeFieldSystemsProcessor;

export class HomeField extends ParticleField {

	//
	private _colourProcessor: HomeFieldColourProcessor;
	private _sceneProgressProcessor: HomeFieldSceneProgressProcessor;
	private _transitionParticlesProcessor: HomeFieldTransitionParticlesProcessor;

	//Scene processor. In the main only one will be active at once: 
	private _screensProcessor: HomeFieldScreensFieldProcessor;
	private _spacesProcessor: HomeFieldSpacesProcessor;
	private _systemsProcessor: HomeFieldSystemsProcessor;
	private _iconProcessor: HomeFieldIconProcessor;
	private _introProcessor: HomeFieldIntroProcessor;
	private _workProcessor: HomeFieldWorkProcessor;

	//Only one scene is active at a time as need to share the particles (and reduce complexity)
	private _activeSceneProcessor: ActiveSceneProcessorType;

	private _screensPixelData: any;
	private _iconPixelData: Array<Vector2>;

	private _homeTimeline: HomeTimelineMain;
	private _math: MathHelper;

	private _activeState: HomeParticleStateType;

	private _frozen: boolean = false;
	


	constructor (sceneProps: particles.SceneProps, container: PIXI.Container, config: particles.ParticleFieldConfig) {
		super(sceneProps, container, config);

	}

	public get mousedown(): boolean {
		return this._sceneProps.mouse.isDown;
	}

	public getRelativeWidth(widthPx: number): number
	{
		return widthPx/this._homeTimeline.virtualWidth;
	}

	public getRelativeHeight(heightPx: number): number
	{
		return heightPx/(window as any).cachedInnerHeight;
	}

	public resize(): void {
		//this._particleManager.resize();
		super.resize();
		
	}

	public resizeEvent() {
		
		if (this._activeSceneProcessor === this._screensProcessor) {
			this._screensProcessor.resize();
		}else if (this._activeSceneProcessor === this._systemsProcessor) {
			this._systemsProcessor.resize();
		} else if (this._activeSceneProcessor === this._introProcessor) {
			this._introProcessor.resize();
		}
	}

	public freeze() {
		this._frozen = true;
	}

	public unfreeze() {
		this._frozen = false;
	}

	protected init(): void {

		this._math = new MathHelper();


		
		//Set all core particle data: 
		this.particles.forEach( (particle: HomeParticle, index: number) => {
			particle.field = this;
			particle.index = index;
		});

		//Create the constant processors. These just handle things like colour switching the contant transition type particle pool: 
		this._colourProcessor = new HomeFieldColourProcessor(this, this.particles as Array<HomeParticle>);
		this._transitionParticlesProcessor = new HomeFieldTransitionParticlesProcessor(this, this._sceneProps, this._math);
	
		//Create the scene processors. Only one will probably be active at a time. 
		this._introProcessor = new HomeFieldIntroProcessor(this, this._sceneProps);
		
		
		this._iconProcessor = new HomeFieldIconProcessor(this, this._sceneProps);

		if (this._iconPixelData) {
			this._iconProcessor.setIconPixelData(this._iconPixelData)
		}
	

	
		this._screensProcessor = new HomeFieldScreensFieldProcessor(this, this._sceneProps);
		this._screensProcessor.pixelData = this._screensPixelData;
		

		this._spacesProcessor = new HomeFieldSpacesProcessor(this, this._sceneProps, this._math, this._colourProcessor);

		this._systemsProcessor = new HomeFieldSystemsProcessor(this, this._sceneProps, this._math, this._colourProcessor);
		

		
		this._workProcessor = new HomeFieldWorkProcessor(this);

		//Under normal conditions the intro is the start processor 
		this._activeSceneProcessor = this._introProcessor;

		//Set progress processor. Handles switching between main icon/screens/spaces scenes: 
		this._sceneProgressProcessor = new HomeFieldSceneProgressProcessor(this, this._sceneProps, this._math, {
			spaces: this._spacesProcessor,
			screens: this._screensProcessor,
			systems: this._systemsProcessor,
			icon: this._iconProcessor,
			transitions: this._transitionParticlesProcessor 
		});

		


	}

	public set homeTimeline(v: HomeTimelineMain) {
		//console.log('homeTimeline', v)
		this._homeTimeline = v;
		this._colourProcessor.setHomeTimeline(v);
	}
	


	public setPixelData(pixelData: any) {
		//this._screensProcessor.pixelData = pixelData;
		this._screensPixelData = pixelData;
	}

	public setIconGrid(pixelData: Array<Vector2>) {
		this._iconPixelData = pixelData;
		//console.log('setIconGrid', pixelData)
		this._iconProcessor.setIconPixelData(this._iconPixelData );
		
	}

	
	/** Intro interactive state */
	public startIntroScene() {
		this._activeSceneProcessor = this._introProcessor;
		this._activeSceneProcessor.startAsActiveScene();
		//this._colourProcessor.setActive(true);
	}

	/** Mouse Over on main scene */
	public introMouseOver() {
		this._introProcessor.mouseOver()
	}

	/** Mouse out on main scene */
	public introMouseOut() {
		this._introProcessor.mouseOut()
	}


	public introComplete() {
		this._introProcessor.introComplete()
	}

	



	/** Intro interaction is clear so can start at icon processor */
	public startMainScene() {	
		//this._introProcessor.stopAsActiveScene();
		this._spacesProcessor.stopAsActiveScene();
		this._screensProcessor.stopAsActiveScene();
		this.setActiveSceneProcessor(this._iconProcessor);
		this._transitionParticlesProcessor.toIcon();
		this._sceneProgressProcessor.startMainScene('icon');
	}


	public changeIconColourOnHover() {
		if (this._colourProcessor) {
			//this._colourProcessor.changeIconColourOnHover();
		}
	
	}

	public startTransition(): void {
		this._transitionParticlesProcessor.startSceneDrag();
		if(	this._activeSceneProcessor) {
			this._activeSceneProcessor.startSceneDrag();
		}
		
	}
	    
	public endTransition(): void {
		
		if (this._sceneProps.progress.ratio > 0.95) {
			//SHOW SPACES LOGIC: 
		}

		if (this._sceneProps.progress.ratio < 0.05) {
			//this._screensState();
		}

		if (this._sceneProps.progress.ratio < 0.48  && this._sceneProps.progress.ratio > 0.49) {
			//this._setHomeFocused();
		
		}

	
		//Particles should more or less
		this._transitionParticlesProcessor.endSceneDrag();

		if(	this._activeSceneProcessor) {
			this._activeSceneProcessor.endSceneDrag();
		}

	}




	/** Scene has been switched so  */
	public setActiveSceneProcessor(v: ActiveSceneProcessorType) {
		
		if (this._activeSceneProcessor) {
			this._activeSceneProcessor.stopAsActiveScene();
		}

		this._activeSceneProcessor = v;
		
		(window as any).skipNormalParticleUpdates = true;
		DelayHelper.setTimeout( () => {
			
			(window as any).skipNormalParticleUpdates = true; 
			this._activeSceneProcessor.startAsActiveScene();
	
			/*if (this._activeSceneProcessor === this._spacesProcessor) {
				this._transitionParticlesProcessor.toSpaces();
			}
	
			if (this._activeSceneProcessor === this._screensProcessor) {
				this._transitionParticlesProcessor.toScreens();
			} */

		}, 10)


	}

	public deactivateCurrentScene() {
		if (this._activeSceneProcessor) {
			this._activeSceneProcessor.stopAsActiveScene();
		}
		this._activeSceneProcessor = null;
	}


	public setupForWork(department: DepartmentType): void {
		

		//this._transitionParticlesProcessor.setupForWork(department);

		if (department === DepartmentType.SCREENS) {
			this._screensProcessor.setupForWork();
		} else {
			this._spacesProcessor.setupForWork();
		}

	}

	public showFromWork(department: DepartmentType) {

		///this._transitionParticlesProcessor.showFromWork(department);

		if (department === DepartmentType.SCREENS) {
			this._screensProcessor.showFromWork();
		} else if(department === DepartmentType.SPACES) {
			this._spacesProcessor.showFromWork();
		} else {
			this._systemsProcessor.showFromWork();
		}
	}


	public animateToWork(department: DepartmentType): void {
		
		this._transitionParticlesProcessor.toWork(department);

		/*if (department === DepartmentType.SCREENS) {
			this._screensProcessor.toWork();
		} else {
			this._spacesProcessor.toWork();
		} */

	}

	public animateFromWork(department: DepartmentType) {
		
		//this._sceneProgressProcessor.fromWorkToHome(department);
		//this._transitionParticlesProcessor.fromWorkToHome(department);

		if (department === DepartmentType.SCREENS) {
			//this._screensProcessor.fromWorkToHome();
		} else {
		//	this._spacesProcessor.fromWorkToHome();
		}


	}

	public updateSystemsGrid() {
		this._systemsProcessor.updateGrid();
	}

	public tick(): void {


		//console.log('this._frozen', this._frozen, 'this._activeSceneProcesso',this._activeSceneProcessor)
		
		
		if (this._frozen || !this._sceneProgressProcessor) {
			return;
		}


		this._sceneProgressProcessor.tick();
		this._colourProcessor.tick();
		this._transitionParticlesProcessor.tick();

			
		if (this._activeSceneProcessor) {
			this._activeSceneProcessor.tick();
		}

		if (!this._systemsProcessor.isActive) {
			this._systemsProcessor.tick();
		}
			

	}

	public changeState(state: HomeParticleStateType): void 
	{

	}

	public updateWorkTransitionPartiles(introXPx: number, introProgressRatio: number): void {

		if (this._transitionParticlesProcessor) {
			this._transitionParticlesProcessor.updateWorkTransitionPartiles(introXPx, introProgressRatio);
		}
	
	}




	


	


	



	
	













}
