//V1.0.0
//Instance of a debugbase app 
//Creates instances of the main helpers and 
import 'reflect-metadata';
import {ControllerManager} from 'debugbase/managers/ControllerManager';
import {RouteManager} from  'debugbase/managers/RouteManager';
import {StateManager} from  'debugbase/managers/StateManager';
import {ModuleManager} from  'debugbase/managers/ModuleManager';
import {BehaviourManager} from  'debugbase/managers/BehaviourManager';
//import {EventsManager} from  'debugbase/managers/EventsManager';

import {SystemHelper} from  'debugbase/helper/SystemHelper';
import {DOMHelper} from  'debugbase/helper/DOMHelper';
import {StringHelper} from  'debugbase/helper/StringHelper';
import {MathHelper} from  'debugbase/helper/MathHelper';
import {FrameworkHelper} from  'debugbase/helper/FrameworkHelper';

import {Events} from 'debugbase/events/Events';

import { iocContainer } from 'inversify.config';
import { Logger } from 'app/contracts/debug/Logger';
import { TYPES } from 'types';
import DelayHelper from './helper/DelayHelper';
import ParticleBehaviourLookup from 'app/library/particle/ParticleBehaviourLookup';
import SiteTransitions from 'app/controller/SiteTransitions';



export class DebugbaseApp {

	private _controllerManager: ControllerManager;
	private _routeManager: RouteManager;
	private _moduleManager: ModuleManager;
	private _stateManager: StateManager;
	private _behaviourManager: BehaviourManager;

	private _systemHelper: SystemHelper;
	private _domHelper: DOMHelper;
	private _stringHelper: StringHelper;
	private _mathHelper: MathHelper;
	private _frameworkHelper: FrameworkHelper;
	//private _eventsManager: EventsManager;

	private _LoggerService: Logger;

	public static instance: DebugbaseApp;

	private _preTicks: Array<{callback: CallableFunction, id: string, tag: string}> = [];

	private stats: any;
	count: number = 0;

	private _accumulatorTime = 0;

	constructor() {

		(window as any).cachedInnerWidth = window.innerWidth;
		(window as any).cachedInnerHeight = window.innerHeight;

		const doc = document.documentElement;
		doc.style.setProperty('--doc-height', `${(window as any).cachedInnerHeight}px`)

	
		

		Events.init();
		this._createContainer();
		this._createManagers();
		this._addListeners();
		DebugbaseApp.instance = this;

	
		if(!this.stats) {
			/*this.stats =  new Stats()
			
			//this.stats.showPanel( 1 ); // 0: fps, 1: ms, 2: mb, 3+: custom
			//this.stats.showPanel( 2 );
			document.body.appendChild( this.stats.dom );
			this.stats.showPanel( 0 );
			*/
			
		}

		
		

	}

	public init(): void {
		this._controllerManager.init();
		this._routeManager.init();
	}

	public get controller( ): ControllerManager {
		return this._controllerManager; 
	}

	public get route( ): RouteManager {
		return this._routeManager; 
	}

	public get state( ): StateManager {
		return this._stateManager; 
	}

	public get behaviour( ): BehaviourManager {
		return this._behaviourManager; 
	}

	public get module( ): ModuleManager {
		return this._moduleManager; 
	}

	public get DOM(): DOMHelper {
		return this._domHelper;
	}

	public get string(): StringHelper {
		return this._stringHelper;
	}

	public get math(): MathHelper {
		return this._mathHelper;
	}

	public get framework(): FrameworkHelper {
		return this._frameworkHelper;
	}

	public get system(): SystemHelper {
		return this._systemHelper;
	}

	public addPreTick(callback: CallableFunction, id:string = null, tag:string = null): void {
		this._preTicks.push({
			callback: callback, 
			id: id,
			tag: tag
		});
	}

	public removePreTickById(id:string = null): void {

		console.log('this._preTicks remove start', this._preTicks.length)

		for( let i=0; i<this._preTicks.length; i++) {
			if(this._preTicks[i].id == id) {
				this._preTicks.splice(i, 1);
				console.log('this._preTicks remove end', this._preTicks.length)
				return;
			}
		}

	
	}

	private _createManagers(): void {

		this._controllerManager = new ControllerManager();
		this._routeManager = new RouteManager();
		this._routeManager.controllerManager = this._controllerManager;
		this._stateManager = new StateManager();
		this._moduleManager = new ModuleManager();
		this._behaviourManager = new BehaviourManager();

		this._systemHelper = new SystemHelper();
		this._stringHelper = new StringHelper();
		this._mathHelper = new MathHelper();
		//this._eventsManager = new EventsManager();
		this._domHelper = new DOMHelper();
		this._frameworkHelper = new FrameworkHelper();

	}

	//Add high level listeners: 
	private _addListeners(): void {
		Events.ready(this._onDOMReady.bind(this));
		Events.tick(this._onTick.bind(this));
		Events.DOMUpdate(this._onDOMUpdate.bind(this));
		Events.scroll(this._onScroll.bind(this));
		Events.resize(this._onResize.bind(this));
	}

	//Heartbeat ticker: 

	private _delta: number = 0;
	private _onTick(data): void {
		
		//this._delta += data.delta;  
		//if(this._delta < 1) {
		//	return;	
		//}
		//this._delta -= 1;

		//console.log('data', data.delta.deltaTime)

	
		
		let useDeltaTime = true; 
		let triggerAtDelta = 0.9;
		let useManualTrigger = true;
		let triggerAtMs = 16;

		//Manual trigger no delat (each frame x1). Approx FPS 60 
		//useDeltaTime = false;
		//useManualTrigger = true; 

		//Unbound FPS. Tick on every frame. Delta time managed. Varaiable rate. 
		useDeltaTime = true; 
		useManualTrigger = false;
		triggerAtDelta = 0;


		if(!window.deltaTime ) {
			window.deltaTime  = 0; 
		}

		if(useDeltaTime) window.deltaTime += data.delta.deltaTime; 
		else  window.deltaTime = 1;


		if(ParticleBehaviourLookup.pingPongMode) {
			window.deltaTime *= 2;
		}

		//this._doPreTicks(data.delta);
		//this._moduleManager.tick(data.delta);

		//this._controllerManager.tick(data.delta);

		let tigger = false;


		if (useManualTrigger) {
			this._accumulatorTime += data.delatMs
			if (this._accumulatorTime  >= triggerAtMs) {
				this._accumulatorTime -= triggerAtMs; 
				tigger = true; 
			}
		} else {
			tigger = window.deltaTime >= triggerAtDelta
		}

		if(	(window as any).appIsPaused ) {
			return;
		}
	

		//if(window.deltaTime > 1.99) {
		//	window.deltaTime  = 1.99;
		//}

		if(window.deltaTime > 1.8) {
			window.deltaTime  = 1.8;
		}

		//window.deltaTime  = 1.9;

		if ( tigger ) {
			
		
			if(this.stats) {
				this.stats.begin();
			}
		
			//this._doPreTicks(data.delta);
			//this._moduleManager.tick(data.delta);
			//this._controllerManager.tick(data.delta);

			this._doPreTicks(1);
			DelayHelper.tick();
			SiteTransitions.tick();
			this._moduleManager.tick(1);
			this._controllerManager.tick(1);

			window.deltaTime = 0; 
			
			if(this.stats) {
				this.stats.end();
			}
		} 

	

	}

	private _onDOMReady(): void {
		this._controllerManager.DOMReady();
		this._moduleManager.DOMReady();
		//this._behaviourManager.DOMReady();
	}

	private _onDOMUpdate(): void {
		this._controllerManager.DOMUpdated();
		this._moduleManager.DOMUpdated();
		//this._behaviourManager.DOMUpdated();
	}

	private _onScroll(y: number): void {
		this._domHelper.scroll(y);
		this._controllerManager.scroll(y);
		this._moduleManager.scroll(y);
		//this._behaviourManager.scroll(y);
	}

	private _onResize(sizes): void {

		(window as any).cachedInnerWidth = window.innerWidth;
		(window as any).cachedInnerHeight = window.innerHeight;

		const doc = document.documentElement;
		doc.style.setProperty('--doc-height', `${(window as any).cachedInnerHeight}px`)

		this._systemHelper.resize();
		this._domHelper.resize(sizes);
		this._controllerManager.resize(sizes);
		this._moduleManager.resize(sizes);
		//this._behaviourManager.resize(sizes);
	}

	private _createContainer(): void {
		this._LoggerService = iocContainer.get<Logger>(TYPES.Logger);
		this._LoggerService.log('Hello');
	}


	private _doPreTicks(delta: number ): void {
		for (let i=0; i<this._preTicks.length; i++) {
			this._preTicks[i].callback(delta);
		}
	}



}
