    private _startSine() {



        this._sineLookup = this._sceneProps.lookups.sine.table;
        this._processSine = true;

		this._spaceSineParticles = this._spacesParticles.slice(0, this._screensIsometricGridPositions.length);
		const freeParticles  = this._spacesParticles.slice(this._spaceSineParticles.length);

    
        this._spaceSineParticles.forEach( (particle, index) => {
			
			particle.deactivateAllBehaviours(true);
			
			particle.activate();

			particle.animatedProps.setVectorStyleMovement(false);
            particle.animatedProps.setEase({y: particle.random(10, 20)});
			particle.sineSeed = particle.random(0.4, 0.8);

			particle.animatedProps.targetAlpha = 0.35 + Math.random() * 0.35;

			particle.sineSeed = 5;



			let baseScale = 0.15;
			particle.activate();
			particle.position.active = true; 
			particle.animatedProps.hardSetScale(baseScale);

			particle.repell.active = !(window as any).disablePointerRepell;;
			particle.repell.target = this._sceneProps.targets.spacesMouse;
			particle.repell.type = RepellType.PUSH;
			particle.repell.testAtCount = 1;
			//Allow for 150px circle and very tight ring: 
			particle.repell.radius = 200;
			particle.repell.randomisePower(100, 200);

			/*
			particle.animatedProps.startBreathing(
				baseScale,
				0.08 * baseScale * (0.5 + Math.random() * 0.5),       
				1 * baseScale,
				[-1, 1],
				(Math.random() * 0.2) * baseScale,
				Math.random() < 0.5 ? -1 : 1
			);
			*/
			
	
           
			

        });

		freeParticles.forEach( (particle, index) => {
			particle.hide();
		});



    }

	
	
