	private _setSpacesRain(): void {

		(window as any).skipNormalParticleUpdates = true;

		let midIndex = Math.round(this._spacesParticles.length/2);
		let p1 = this._spacesParticles.slice(0, midIndex);
		let p2 = this._spacesParticles.slice(midIndex);
        
		p1.forEach(this._rainParticle.bind(this));

		DelayHelper.setTimeout( () => {
			(window as any).skipNormalParticleUpdates = true;
			p2.forEach(this._rainParticle.bind(this));
		}, 20);

	}

