import {Debugbase} from 'debugbase/Debugbase';
import {MathHelper, Point} from 'debugbase/helper/MathHelper';
import {ParticlePath}  from 'app/module/home/ParticlePath';
import {ParticleRepell}  from 'app/module/home/ParticleRepell';
import {MouseController}  from 'app/controller/MouseController';
import {TimelineStateType} from 'app/controller/home/HomeTimelineMain';
import {RelativeTimelinePosition}  from 'app/module/home/homeUtil/RelativeTimelinePosition';

export class ParticleAttract {

	private _state: TimelineStateType =  TimelineStateType.INTRO;
	private _math: MathHelper;
	private _distance: number;					//Distance to mouse from Math helper 
	private _distanceTolerence: number;			//Distance to (randomly seeded to create partciles that will go furter to meet the mouse)
	private _distanceToTolerenceRatio: number;	//The ratio of how close mouse is to limit	
	private _attractSeed: number;				//How close the partcile attrack to mouse seeded to short / overshot 
	private _baseEasePower: number;				//Normal ease power when not effected by mouse proximity (Essentially the )
	private _attractEasePower: number;			//Final ease power that takes into account the 
	private _moveXTarget: number = 0;
	private _moveYTarget: number = 0;
	private	_moveX: number = 0;
	private	_moveY: number = 0; 
	private _sprite: PIXI.Sprite;	

	private _forceX: number = 0;
	private _forceY: number = 0;
	private _powerX: number = 0;
	private _powerY: number = 0;

	private _offsetTargetX: number = 0;
	private _offsetTargetY: number = 0;

	private _particlePathOrbit: ParticlePath;
	private _particlePathSine: ParticlePath;
	private _particleRepell: ParticleRepell;

	private _mouse: MouseController;

	private _introActive: boolean = false;
	private _buttonActive: boolean = false;

	//private _screensTargetX : number = 0;
	//private _screensTargetY : number = 0;

	private _spacesTargetPosition: RelativeTimelinePosition;
	private _screensTargetPosition: RelativeTimelinePosition;

	public set introActive(v : boolean) {
		this._introActive = v;
	}

	public set buttonActive(v : boolean) {
		this._buttonActive = v;
	}

	

	constructor(sprite : PIXI.Sprite, particlePathOrbit: ParticlePath = null,  particlePathSine: ParticlePath = null, particleRepell: ParticleRepell = null) {

		this._mouse = Debugbase.controller.get('Mouse') as MouseController;
		this._math = Debugbase.math;
		this._sprite = sprite;
		this._particlePathOrbit = particlePathOrbit;
		this._particlePathSine = particlePathSine;
		this._particleRepell = particleRepell;

		this._attractSeed = 0.8 + (Math.random() * 0.9);
		this._attractSeed = 1;
		//this._attractSeed  = 3;
		this._baseEasePower = 2 + (Math.random() * 2);
		
		//For hoz split:
		this._distanceTolerence = 500 + (Math.random() * 500);
		//For vertical split: 
		this._distanceTolerence = 1200 + (Math.random() * 400);

		//Points 
		//this._offsetTargetX = (Math.random() * 1  < 0.5) ? -50 : 50;
		//this._offsetTargetY = (Math.random() * 1  < 0.5) ? -50 : 50;

		//Centered
		this._offsetTargetX = 0;
		this._offsetTargetY = 0;

		//this._screensTargetX = 0 + (Math.random() * 1800);
		//this._screensTargetY = Math.random() * 1000;

		//When screens then target the logo: 
		//this._screensTargetPosition = new RelativeTimelinePosition(0.24, 0.54);

		this._screensTargetPosition = new RelativeTimelinePosition(
			-0.05 + (Math.random() * 0.22), 
			0.6 + Math.random() * 0.4
		);

		//When screens then target to position under logo:
		//this._spacesTargetPosition = new RelativeTimelinePosition(
		//	0.78 + (Math.random() * 0.22), 
		//	Math.random() * 1
		//);

		let y = ((window as any).randomTriSeed) ? 0.3 : 0.31;
		let x = ((window as any).randomTriSeed) ? 0.674 : 0.674;
		this._spacesTargetPosition = new RelativeTimelinePosition(
			x, 
			y
		);

	}

	public resize(containerWidth: number, containerHeight: number): void {
		this._screensTargetPosition.resize(containerWidth, containerHeight);
		this._spacesTargetPosition.resize(containerWidth, containerHeight);

	}

	public tick(mouseCords: Point, absoluteX: number, containerX: number, absoluteY: number, mouseOffsetX: number, dragOffsetX: number): void {
		
    	//this._repell.distancey = mouseCords.y - (this._absoluteY + this._offsetY);
    	this._distance = this._math.distance(
    		{x: mouseCords.x + this._offsetTargetX, y: mouseCords.y + this._offsetTargetY}, 
    		{x: this._sprite.x, y: this._sprite.y}
    	);

        this._distanceToTolerenceRatio = 0;

        let maxVelocity: number = (this._state === TimelineStateType.HOME_LOGO_OVER) ? 200 : 50;
        let easePower: number = 5;

        if (this._state === TimelineStateType.SPACES_TRANSITION) {
        	maxVelocity = 100;
        } else if (this._state === TimelineStateType.SPACES) {
        	maxVelocity = 1000;
        	easePower = 15;
        }

       // if (this._state === TimelineStateType.SPACES) {
			
		//	this._moveXTarget  = 2000;
		//	maxVelocity = 200;

		//} 



		if ( (this._distance < this._distanceTolerence &&  this._state === TimelineStateType.HOME_LOGO_OVER) || this._state === TimelineStateType.SPACES_TRANSITION || this._state === TimelineStateType.SPACES || this._state === TimelineStateType.SCREENS) {

			if(this._particlePathOrbit) {
				this._particlePathOrbit.setActive(true);
			}
			
			if(this._particlePathSine) {
				this._particlePathSine.setActive(false);
			}

			this._particleRepell.setActive(true);
	

		 	this._distanceToTolerenceRatio = this._distance / this._distanceTolerence;
		 	if (this._distanceToTolerenceRatio > 1) {
		 		this._distanceToTolerenceRatio = 1;
		 	}

		 	// Reverse ratio: 
		 	this._distanceToTolerenceRatio = 1 - this._distanceToTolerenceRatio;
		 
		 	this._moveXTarget  = mouseCords.x - (absoluteX + containerX) + this._offsetTargetX - mouseOffsetX - dragOffsetX;
		 	this._moveYTarget  = mouseCords.y - absoluteY + this._offsetTargetY;

			 if (this._state === TimelineStateType.SPACES) {
			 	
			 	//Update the target position based on the drag/mouse/effect offsets: 
			 	this._spacesTargetPosition.update(
			 		containerX + this._offsetTargetX + mouseOffsetX + dragOffsetX,
			 		this._offsetTargetY
			 	);

		 		//this._moveXTarget  = this._screensTargetX - (absoluteX + containerX) + this._offsetTargetX - mouseOffsetX - dragOffsetX;
		 		//this._moveYTarget  = this._screensTargetY - absoluteY + this._offsetTargetY;
		 		this._moveXTarget  = this._spacesTargetPosition.x - (absoluteX + containerX)  + this._offsetTargetX - mouseOffsetX - dragOffsetX
		 		this._moveYTarget  = this._spacesTargetPosition.y - absoluteY + this._offsetTargetY;

		 	}

		    if (this._state === TimelineStateType.SCREENS) {
		 
		 		this._screensTargetPosition.update(
			 		containerX + mouseOffsetX + dragOffsetX,
			 		this._offsetTargetY
			 	);

		 		this._moveXTarget  = this._screensTargetPosition.x - (absoluteX + containerX) + this._offsetTargetX - mouseOffsetX - dragOffsetX;
		 		this._moveYTarget  = this._screensTargetPosition.y - absoluteY - this._offsetTargetY;

		 	}

		 	if ( (window as any ).introActive && !this._introActive ) {
				this._moveXTarget = 0;
				this._moveYTarget = 0;
			}

		 }  else {

		 	if(this._particlePathOrbit) {
		 		this._particlePathOrbit.setActive(false);
		 	}

		 	if(this._particlePathSine) {
		 		this._particlePathSine.setActive(true);
		 	}
		 	//this._particleRepell.setActive(false);
		 	this._moveXTarget =  0;
		 	this._moveYTarget = 0;

		 }

		this._attractEasePower = this._baseEasePower + (easePower * this._distanceToTolerenceRatio);
		
		//Only allow so much velocity: 
		//For hoz split:
	
		//For vertical split:
		//let maxVelocity: number = 100;


		if (this._moveXTarget - this._moveX > maxVelocity) {
			this._moveXTarget = this._moveX + maxVelocity;
		} else if (this._moveXTarget - this._moveX < -maxVelocity) {
			this._moveXTarget = this._moveX - maxVelocity;
		}

		if (this._moveYTarget - this._moveY > maxVelocity) {
			this._moveYTarget = this._moveY + maxVelocity;
		} else if (this._moveYTarget - this._moveY < -maxVelocity) {
			this._moveYTarget = this._moveY - maxVelocity;
		}

		this._moveX  = this._moveX + ((this._moveXTarget - this._moveX ) /  this._attractEasePower );
		this._moveY  = this._moveY + ((this._moveYTarget - this._moveY ) / this._attractEasePower );

	}	

	public getX() {
		return this._moveX;
	} 

	public getY() {
		return this._moveY;
	} 

	/** Set the timeline state for */
	public setTimelineState( state: TimelineStateType ): void {
		this._state = state;
	}

}
