	protected _absoluteY: number; 				//Translated Y to px values 
	protected _dragOffsetX: number = 0;			//The amount the item is moved on X because or dragging inernia or being repelled by  
	protected _offsetY: number = 0;				//The amount the item is moved on Y because or dragging inernia or being repelled by  
