	//Parallax calulations: 
	private _relativeParallaxX: number;
	private _relativeParallaxXToProgressRatio: number;

	private _parallaxMultiplerStartValue: number = 30;
	private _parallaxEaseMultipler: number = 1;

	protected _scale: number = 1;
	protected _scaleTarget: number = 1;
	protected _displayScale: number = 1;

	protected _rotationTarget: number = 0;
	protected _rotation: number = 0;

	private _alpha: number = 1;

	protected _sprite: PIXI.Sprite;	

	private _tween: gsap.Tween;

	private _settings: TimelineItemConfig;
	private _json: HomeParticleJSONModel;
	private _zone: string = null;

	private _hasAutoEffect: boolean = false;
	private _effectRange: number = 200;
	private _startRangeAt: number = 0;
	private _autoStartRotation: number = 0;
	private _autoStartAlpha: number = 0;
