export class StringHelper {

	public snakeToCamelCase(str: string): string {
		return str.replace(/(\-\w)/g, function(m) { return m[1].toUpperCase(); });
	}

	public snakeToClassName(str: string): string {
		return this.capitalizeFirstLetter( this.snakeToCamelCase(str) );
	}

	public capitalizeFirstLetter(str: string): string {
		return str.charAt(0).toUpperCase() + str.slice(1);
	}

	public contains(str: string, check: string, start: number = 0): boolean {
		return (str.indexOf(check, start) !== -1);
	}

	public remove(str: string, remove: string) {
		var re = new RegExp(this._escapeRegExp(remove), 'g');
		return str.replace(re, '');
	}

	public isNumeric(str: string | number): boolean {
		return !isNaN(parseFloat( str as string )) && isFinite( str as number );
	}

	//Convert string to number or boolean 
	public convert(str: string, convertBlankStringToBoolean: boolean = true): string | number | boolean {
		
		let converted: string | number | boolean = str;

		//If a blank string then most likely a boolean i.e created by attribute-name
		if ( (convertBlankStringToBoolean && str === '') || str.toLowerCase() === 'true') {
			converted = true;
		} else if (str.toLowerCase() === 'false') {
			converted = false; 
		} else if (this.isNumeric(str) ) {
			converted =  parseFloat(str);
		}
		return converted;

	}

	public template(str: string, map: any): string {
		for (var i in map) {
            var re  = new RegExp('%%' + i + '%%', 'g');
            str  = str.replace(re, map[i]); 
        }
        return str; 
	}

	private _escapeRegExp(str: string) {
		return str.replace(/[-[\]{}()*+?.,\\^$|#\s]/g, '\\$&');
	}


}
