	/** When dragging then make the particles follow the scene without easing */
	public startSceneDrag() {

		return;
		this._isDragging = true;

		this._repellActiveDragFlag = this._p.repell.active;
		this._p.repell.active = false;

		this._p.animatedProps.setEase( {x: 2}, false);

	}

	/** When dragging stops can restart normal easing */
	public endSceneDrag() {

		return;
		this._isDragging = false;

		this._p.repell.active = this._repellActiveDragFlag;

		if (!this._nonDragEase) {
			return;
		}
		
		this._p.animatedProps.setEase( {
			x: this._nonDragEase.x,
			y: this._nonDragEase.y,
		}, true, 20 )

	}

	

