	private _iconHomeState(): void {
		

		//Is a 'spare' particle. So in the case just hide quickly:  
		this._p.animatedProps.targetScale  = 0;
		this._p.animatedProps.targetAlpha  = 0;		

		//Decative the hidden particle after it's gone: 
		this._p.startCounter(this._p.counter, 60, this._p.deactivate.bind(this))
		

	}

	private _maskHomeIcon(): void {

		return;
		if (this._sceneProps.masks && this._sceneProps.masks.logo) {

			if (!this._p.particleProps.container.mask && this._p.particleProps.container.name === 'ICON CONTAINER') {
				this._sceneProps.masks.logo.alpha = 1;
				this._p.particleProps.container.mask = this._sceneProps.masks.logo;
			}
		}

	}
	private _removeIconMask() {
		this._p.particleProps.container.mask = null;
	}

