import { particles } from "app/contracts/Particles";
import Config from "app/data/Config";
import { ParticleLayoutType } from "app/enum/Particles";
import { MathHelper } from "debugbase/helper/MathHelper";
import { HomeField } from "../HomeField";
import HomeFieldConfig from "../HomeFieldConfig";
import { HomeParticle } from "../HomeParticle";
import HomeFieldColourProcessor from "./HomeFieldColourProcessor";
import ParticleGridTargets from "../ParticleGridTargets";
import SingleLine from "../gridItems/SingleLine";
import ParticlePool from "../ParticlePool";
import ParallaxItemTargets from "../ParallaxItemTargets";
import ParticleGridPatterns from "../ParticleGridPatterns";
import HomeColours from "../HomeColours";
import ComboCounter from "app/library/counter/ComboCounter";
import { Simple } from "debugbase/tracking/Simple";

export default class HomeFieldSystemsProcessor {


    private _active: boolean = false;
    private _spacesStageIndex = 0;
	private _spacesStageCounter = 0;
    private _systemsParticles: Array<HomeParticle>;
	private _transitionParticles: Array<HomeParticle>;
	private _allParticles: Array<HomeParticle>;
	private _systemsParticleContainer: PIXI.Sprite;

	private _hasBeenStarted: boolean = false;
	private _lines: Array <SingleLine> = [];

	private _resizeTimeout: number;

	private _spacesBoxStages = [
		//{type: 'BasicGrid', frames: 300},
		{type: 'SingleLine', frames: 10000},
		//{type: 'SingleLine', frames: 1000},
		//{type: 'Rain', frames: 1000},
		//{type: 'Rain', frames: 10000}

	];


	private _lineModeIndex = 0;
	private _lineModeCounter = 0;
	private _lineModeChangeAtCount = 0;


	private _colourModeCount = 0;
	private _colourModeChangeAtCount = 3000/16;

	private _lineModeSettings = [
		
		{rightAngle: false, singleLineMode: false,  timeMS: 15000},
		{rightAngle: false, singleLineMode: true,  timeMS: 5000},
		{rightAngle: true, singleLineMode: false,  timeMS: 15000},
		{rightAngle: true, singleLineMode: true,  timeMS: 10000},
		{rightAngle: false, singleLineMode: true,  timeMS: 10000},
		{rightAngle: false, singleLineMode: false,  timeMS: 15000},
		{rightAngle: true, singleLineMode: true,  timeMS: 10000},
		{rightAngle: true, singleLineMode: false,  timeMS: 10000},
	];

	private _counters: ComboCounter;
	private _firstColour;
	private _firstColourSet = false;


    constructor(private _homeField: HomeField, private _sceneProps: particles.SceneProps, private _math: MathHelper, private _colourProcessor:HomeFieldColourProcessor ) {
		
		
		this._counters = new ComboCounter();

		this._systemsParticles =  HomeFieldConfig.getParticlePool(this._homeField.particles as Array<HomeParticle>, 'systems');
		ParticlePool.setPool('systems', this._systemsParticles);

	


		this._transitionParticles =  HomeFieldConfig.getParticlePool(this._homeField.particles as Array<HomeParticle>, 'transitions');
		this._allParticles = this._systemsParticles.concat(this._transitionParticles);
		this._systemsParticleContainer = this._homeField.getSubContainer('systems');

		//ParticleGridTargets.setGuideContainer(this._systemsParticleContainer );

		this._systemsParticleContainer.visible = false;

		this._firstColour = HomeColours.getRandomColour(HomeColours.greenBlues);

	
		

	
		
	}   

	public get isActive(): boolean 
	{
		return this._active;
	
	}


	/**
	 * When skipping upodate then need still animate the grid across: 
	 */
	public updateGrid() {
		this._generateBrokenGrid();
	}
 

    public tick() {

		this._positionParticleContainer();
		this._generateBrokenGrid();

		if(!this._firstColourSet) {
			let isSet = this._colourProcessor.changeSystemsColour([	(window as any).accentColour], true);
			this._firstColourSet = isSet;
		}

		
        if (!this._active) return;

		this._counters.tick();
		this._tickLineMode();
		this._tickMarkerColour();

		console.log('Systems tick length', this._lines.length)

		for (let i=0; i<this._lines.length; i++) {
			this._lines[i].tick();
		}
        
        this._counter();

    }

	public resize() {
	
		return; 
		
		this.stopAsActiveScene();

		if (this._resizeTimeout) {
			clearTimeout(this._resizeTimeout);
		}

		this._resizeTimeout = setTimeout(() => {
			this.startAsActiveScene();
			this._resizeTimeout = null;
		}, 500);
		

	}

	
	public showFromWork() {
		
		this.stopAsActiveScene();
		this.startAsActiveScene();

	}


	private _generateBrokenGrid() 
	{
		let gridSprite: PIXI.Sprite = ParallaxItemTargets.get('systems-grid');
		let w = gridSprite.width;
		let h = gridSprite.height;
		ParticleGridTargets.tick(
			gridSprite.x,
			(window as any).cachedInnerHeight / 2,
			w,
			h,
			this._sceneProps.progress.ratio
		); 

	}


/** User is far enough right to be on spaces scene */
    public startAsActiveScene(reposition: boolean = true) {
		

		//console.trace('Systems startAsActiveScene')

		console.trace('Systems startAsActiveScene');

		(window as any).accentColour = this._firstColour;
		this._colourProcessor.changeSystemsColour([	(window as any).accentColour], true)

		let gridSprite: PIXI.Sprite = ParallaxItemTargets.get('systems-grid');
		let w = gridSprite.width * gridSprite.scale.x;
		let h = gridSprite.height * gridSprite.scale.y;


		ParticleGridTargets.tick(
			gridSprite.x,
			(window as any).cachedInnerWidth / 2,
			w,
			h,
			this._sceneProps.progress.ratio
		);
	
	
		//this._spacesStageIndex = this._math.random(0, this._spacesBoxStages.length - 1, false, true);
		this._spacesStageIndex = 0;
        this._spacesStageCounter = 0;


		this._systemsParticleContainer.visible = true;

		/* if(reposition) {
			//this._homeField.reposition(ParticleLayoutType.GRID, {x: 0.88, y: 0.42}, {w: 2, h: 0.33}, null, this._systemsParticles, 1, false	);

			
			//this._homeField.reposition(ParticleLayoutType.GRID, {x:  -1 * (this._sceneProps.zonePositions.systemsPArea.w/2), y: 0.42}, {w: 2, h: 0.33}, null, this._systemsParticles, 1, false	);



			this._nextSystemsStage();
		} else {
			this._spacesStageCounter = this._spacesBoxStages[this._spacesStageIndex].frames - 90;
			if(!this._hasBeenStarted) {
				this._nextSystemsStage();
			}
		}	*/

		this._counters.clear();

		this._counters.add(() => {
			(window as any).skipNormalParticleUpdates = true;
			this._systemsParticles.forEach(particle => {
				particle.systems.setSystemsStart();
			})
		}, 50)


		this._counters.add(() => {
			(window as any).skipNormalParticleUpdates = true;
			this._nextSystemsStage()
		}, 100);

		this._counters.add(() => {
       	 
			this._systemsParticleContainer.visible = true;
		}, 120)


		this._active = true; 
		this._hasBeenStarted = true;


    }

    public stopAsActiveScene() {


		return;

		console.trace('stopAsActiveScene')


		
		this._counters.clear();

		this._firstColourSet = false;
		this._active = false;
		this._systemsParticleContainer.visible = false;

		this._clearAllLines();


		this._systemsParticles.forEach(particle => {
			particle.position.set({x:2, y:2}, true)
			particle.deactivateAllBehaviours(false);
            particle.setVisible(false);
			particle.animatedProps.targetAlpha = particle.animatedProps.alpha = 0; 
			particle.active = false;
			particle.pooled = false;
			

        });

		

    }

    public startSceneDrag(){}
    public endSceneDrag(){}


	public toWork() {
		
		//gsap.TweenMax.to(this._screensParticleContainer, Config.toWorkTime, {pixi:{y: -1 * (window as any).cachedInnerHeight, ease: Config.toWorkEase});

		this._reduceProcessingForWorkTransition();

		gsap.TweenMax.to(this._systemsParticleContainer, Config.toWorkTime, {pixi:{y: -1 * (window as any).cachedInnerHeight}, ease: Config.toWorkEase, onComplete:() => {
			this._disableForWork();
		},});

	}

	
	public fromWorkToHome() {

		this._enableForWork();
		this.startAsActiveScene(false);

		gsap.TweenMax.to(this._systemsParticleContainer, Config.toWorkTime, {pixi:{y:0}, ease: Config.toWorkEase, onComplete:() => {
		
		},});
	}

	//Deeplink to work: 
	public setupForWork() {
		
	}

	
   
    //PRIVATE 
    //--------------------------------------------------------------------------------------
    //--------------------------------------------------------------------------------------
    //--------------------------------------------------------------------------------------
    //--------------------------------------------------------------------------------------
    //--------------------------------------------------------------------------------------

	private _positionParticleContainer() 
	{

       
		let x = this._sceneProps.progress.containerX + (this._sceneProps.zonePositions.systemsParticles.cx * this._sceneProps.sizes.container.w);
        
        this._systemsParticleContainer.x = x;
		this._sceneProps.mouse.particleContainersX.systems = x;
	}

	






	private _reduceProcessingForWorkTransition() {
		
		this._active = false;
		this._systemsParticles.forEach( (p: HomeParticle) => {
			p.repell.active = false;			
		});

	}

	private _disableForWork(): void {

		this._systemsParticleContainer.visible = false;

		this._systemsParticles.forEach( (p: HomeParticle) => {
			p.deactivateAllBehaviours(false);
			p.active = false;
		});

	}

	
	private _enableForWork(): void {

		this._systemsParticleContainer.visible = true;
		this._systemsParticles.forEach( (p: HomeParticle) => {
			p.active = true;
		});

	}

	


    private _counter(): void {

		this._spacesStageCounter++;

		console.log('this._spacesStageCounter', this._spacesStageCounter, this._spacesBoxStages[this._spacesStageIndex].frames);

		if (this._spacesStageCounter >= this._spacesBoxStages[this._spacesStageIndex].frames) {
			
			
			this._spacesStageCounter = 0;

			//Deal with the index change: 
			this._spacesStageIndex++;
			if (this._spacesStageIndex >= this._spacesBoxStages.length) {
				this._spacesStageIndex = 0; 
				
			}
	
			
			console.log('_nextSystemsStage')
			this._nextSystemsStage();
			
		}
	}


    private _nextSystemsStage(): void {
		

		
		let type: string = this._spacesBoxStages[this._spacesStageIndex].type;

	

		if ( type ===  'Rain') this._setSpacesRain();
		else if ( type ===  'BasicGrid') this._spacesBasicGrid();
		else if ( type ===  'SingleLine') this._singleLine();

        //else if ( type ==  'PhysicsLine') this._setSpacesPerspectiveOrbit(this._boxPerspectiveArea);


		//this._colourProcessor.changeSpacesColour(this._allParticles);
		
		
	
	}









	private _setSpacesRain(): void {
		this._systemsParticles.forEach(particle => {
			particle.systems.rain();
		});
	}

	private _spacesBasicGrid(): void {
		
		let box = this._sceneProps.zonePositions.systemsPArea;

		this._homeField.reposition(
            ParticleLayoutType.GRID, 
            //{x: box.x, y: box.y},
			//{x: this._sceneProps.zonePositions.systemsPArea.cx, y: box.y}, 
			{x: -0.08, y: box.y}, 
            {w: box.w, h: 20}, 
            null, 
            this._systemsParticles, 
            1000,
			true
        );

		this._systemsParticles.forEach( (particle) => particle.systems.basicGrid() );
	

	}

	private _clearAllLines() {

		for (let i=0; i<this._lines.length; i++) {
			this._lines[i].clear();
		}

		this._lines = [];
        

	}

	private _singleLine(): void {


		//IDEAS: 
		// Ping-pong particles 
		// Mini grid, i.e 1-4 in a group to make grids 
		// Colour shifts		
		//Curved movement (created by difference in x/y speeds). 
		//Bounce off edge and exit grid etc with physics 


		(window as any).singleLineMode = false;
		(window as any).rightAngleMode = false;
		let lineConfig = [];

		let totalLines = (window as any).cachedInnerWidth < 600 ? 8 : 10; 
		let delay = 0;

		for(let i = 0; i<totalLines; i++) {

			if(this._lines.length > 10) {
				return;
			}
	
			let spawn = ParticleGridPatterns.getSpawnTarget();
			let forceCol = null;
			let forceRow = null;
	
			if (Math.random() < 0) {
				forceCol = spawn.col;
			} else {
				forceRow = spawn.row;
			}
	
			let end = ParticleGridPatterns.get45DegreeTarget(spawn.col, spawn.row, 8);

			if(!end) {
				end = ParticleGridPatterns.get45DegreeTarget(spawn.col, spawn.row, 8);
			}

			if(!end) {
				end = ParticleGridPatterns.get45DegreeTarget(spawn.col, spawn.row, 8);
			}

			if(!end) {
				end = ParticleGridPatterns.get45DegreeTarget(spawn.col, spawn.row, 8);
			}

			if(!end) {
				continue;
			}

	
			 let lineConfig = 
				{	
					start: `grid_${spawn.col}_${spawn.row}`, 
					end:`grid_${end.col}_${end.row}` 
				}
			
			

			
			//for (let i=0; i<lineConfig.length; i++) {
	

			console.log('lineConfig', lineConfig)
				
					
				let line = new SingleLine(
					lineConfig.start,
					lineConfig.end,
					1,
					1,
					40000,
					'systems',
					delay
				);
	
	
				line.endTargetGridPoint = end;
				//line.fastMode = false;
				///this._counters.add( () => {
				//	line.fastMode = false;
				//}, 5000)
	
				
	
				this._lines.push(line);

				

				
			//}


			delay += 100;


	
			


		}



	
			(window as any).fastModeScale = 0.8;

			(window as any).fastModeScale = 0.6;
			//(window as any).fastModeScale = 0.03;
			let tweenOb = {value: (window as any).fastModeScale };
			gsap.TweenMax.to(tweenOb, 5, {
				value: 0.95, ease: gsap.Power0.easeNone, onUpdate:() => {
					(window as any).fastModeScale = tweenOb.value;
				}
			})


			this._lineModeIndex = 0;
			this._lineModeCounter = 0;
			this._setLineMode(true);
			this._lineModeIndex = 1;


			//(window as any).accentColour = HomeColours.getRandomColour(HomeColours.oranges);
			//(window as any).accentColour = HomeColours.getRandomColour(HomeColours.greenBlues);
			
			(window as any).accentColour = this._firstColour;
			this._colourProcessor.changeSystemsColour([	(window as any).accentColour], true)
		
		


		
	}


	private _totalPasses = 0; 
	private _tickLineMode() {


		this._lineModeCounter += (window as any).deltaTime;
		

		if(this._lineModeCounter  >= this._lineModeChangeAtCount) {

			this._lineModeIndex++;
			if (this._lineModeIndex >= this._lineModeSettings.length) {
				Simple.event('home_systems_full_phase_complete', {total_passes: this._totalPasses});
				this._lineModeIndex = 0;
			}

			Simple.event('home_systems_next_phase', {index: this._lineModeIndex});
			
			this. _lineModeCounter = 0; 

			if (this._lineModeIndex) {
				this._setLineMode();
			}

		} 
	}


	private _setLineMode(firstPass: boolean = false) 
	{
		(window as any).singleLineMode = this._lineModeSettings[this._lineModeIndex].singleLineMode;
		(window as any).rightAngleMode = this._lineModeSettings[this._lineModeIndex].rightAngle;
		this._lineModeChangeAtCount =  this._lineModeSettings[this._lineModeIndex].timeMS ? this._lineModeSettings[this._lineModeIndex].timeMS / 16 : 10000 / 16;

		this._lineModeChangeAtCount *= 0.8;

		//if(firstPass) {
		//	this._lineModeChangeAtCount  *= 0.7;
		//}

	}

	private _tickMarkerColour() {
		
		this._colourModeCount += (window as any).deltaTime;
		
		if(this._colourModeCount >= this. _colourModeChangeAtCount) {
			//(window as any).accentColour = HomeColours.getRandomColour(HomeColours.oranges);
			
			(window as any).accentColour = HomeColours.getRandomColour(HomeColours.greenBlues);

			this._colourProcessor.changeSystemsColour([	(window as any).accentColour])


			this._colourModeCount  = 0;
		}
		
	}


	

	









    





}