import { TimelineItem, TimelineItemConfig, TimelineItemType} from 'app/module/home/TimelineItem';
import {HomeParticleJSONModel} from 'app/model/HomeParticleJSONModel';
import { DepartmentType } from 'app/data/Enums';
//import * as PIXI from 'pixi.js';

export class RelativeChildren extends TimelineItem {

	//Child partciles
	private _childParticles: Array<TimelineItem> = [];

	public create(jsonConfig: HomeParticleJSONModel, settings: TimelineItemConfig): void {
		//Set the department based on postion 
		this.id  = jsonConfig.id;
		this.type = TimelineItemType.RELATIVE_CHILDREN;
		this._repellActive = true;
		this.department = (this.relativeX < 0.5) ? DepartmentType.SCREENS : DepartmentType.SPACES;
		this._createChildParticles(jsonConfig, settings);
	}

	public resize(containerWidth: number, containerHeight: number, displayScale: number, innerwidth: number, positionScale: number) {
		this._childParticles.forEach( (particle) => {
			particle.resize(containerWidth, containerHeight, displayScale, innerwidth, positionScale);
		});
	}

	public tick(xProgressRatio: number, containerX: number, mouseYRatio: number, mouseXRatio: number, mouseIsDown: boolean = false, velocityRatio: number = 0, instant: boolean = false, mousecords: any = null, mothcords: any = null): void {
		this._childParticles.forEach( (particle) => {
			particle.tick(xProgressRatio, containerX, mouseYRatio, mouseXRatio, mouseIsDown, velocityRatio, instant, mousecords, mothcords);
		});
	}

	public animateToWork(moveY: number, delay: number = 0): void {
		this._childParticles.forEach( (particle) => {
			particle.animateToWork(moveY, delay);
		});
	}

	public animateFromWork(delay: number = 1.0): void {
		this._childParticles.forEach( (particle) => {
			particle.animateFromWork(delay);
		});
	}

	public resetIntervalsAndAnimations(): void {
		this._childParticles.forEach( (particle) => {
			particle.resetIntervalsAndAnimations();
		});
	}

	public reset(): void {
		this._childParticles.forEach( (particle) => {
			particle.reset();
		});
	}

	public getItems(): Array<TimelineItem> {
		return this._childParticles;
	}

	public resetFromWork(offY: number): void {
		this._childParticles.forEach( (particle) => {
			particle.resetFromWork(offY);
		});
	}

	//Private 
	//----------------------------------------------------------------------------------------
	private _createChildParticles(parentConfig: HomeParticleJSONModel, settings: TimelineItemConfig): void {

		let baseX: number = parentConfig.relativeX;
		let rangeX: number = parentConfig.relativeChildrenWidth;

		let baseY: number = parentConfig.relativeY;
		let rangeY: number = parentConfig.relativeChildrenHeight;

		parentConfig.children.forEach( (child: HomeParticleJSONModel, index: number) => {

			let config: HomeParticleJSONModel = {
				id: `${parentConfig.id}-particle-${index}`,
				relativeX: baseX + (child.relativeX * rangeX),
				relativeY: baseY + (child.relativeY * rangeY),
				easePower: parentConfig.easePower,
				parallaxOffsetPower: parentConfig.parallaxOffsetPower,
				parallaxPowerMouse: 0,
				autoScale: parentConfig.autoScale,
				autoRotate: parentConfig.autoRotate,
				autoAlpha: parentConfig.autoAlpha,
				randomRotation: parentConfig.randomRotation,
				startScale:  parentConfig.startScale,
				minScale:  parentConfig.minScale,
				maxScale:  parentConfig.maxScale,
				autoRange:  parentConfig.autoRange,
				alpha:  parentConfig.alpha,
				tint:  parentConfig.tint,
				repell: false,
				repellDistance: parentConfig.repellDistance,
				mouseRepellPower: parentConfig.mouseRepellPower,
				mouseRepelEasePower: parentConfig.mouseRepelEasePower
			};

			let texture: PIXI.Texture = settings.textures[child.texture] as PIXI.Texture;
			this._childParticles.push( this._createChildParticle(config, texture, settings.container) );

		});

	}

	private _createChildParticle(config: HomeParticleJSONModel, texture: PIXI.Texture, container: PIXI.Container): TimelineItem {
		
		//A normal pixi sprite that will be animated along the timeline
		let item = new TimelineItem();
				
		item.relativeX = config.relativeX;
		item.relativeY = config.relativeY;
		item.easePower = config.easePower;
		item.parallaxOffsetPower = config.parallaxOffsetPower;
		item.parallaxPowerMouse = 0;	

		if (config.alpha) {
			item.alpha = config.alpha;
		}

		if (config.tint) {
			item.tint = config.tint;
		}
		
		if ( config.mouseRepellPower) {
			item.mouseRepellPower = config.mouseRepellPower;
		}

		if ( config.repellDistance) {
			item.repellDistance = config.repellDistance;
		}

		if ( config.mouseRepelEasePower) {
			item.mouseRepelEasePower = config.mouseRepelEasePower;
		}

		item.create(config, {
			container: container,
			texture: texture
		});

		return item;

	}

}
