//v1.0 
//AbstractController that controllers can extend to get key functionality 
import {Debugbase} from '../Debugbase';
import {StateManager} from  'debugbase/managers/StateManager';
import {ModuleManager} from  'debugbase/managers/ModuleManager';

import {SystemHelper} from  'debugbase/helper/SystemHelper';
import {DOMHelper} from  'debugbase/helper/DOMHelper';
import {MathHelper} from  'debugbase/helper/MathHelper';
import {Events} from 'debugbase/events/Events';

export abstract class Controller {

	public id: string|null; 
	public global: boolean = false;
	public containerSelector;
	public DOMIsReady: boolean = false;

	protected container: Element;
	protected app: Debugbase;
	protected state: StateManager;
	protected module: ModuleManager;
	protected DOM: DOMHelper;
	protected math: MathHelper;
	protected events: Events;
	protected system: SystemHelper;

	//public tags: Array<string>; 
	constructor(id: string|null = null) {
		this.app = Debugbase;
		this.id = id;
		this.state = Debugbase.state;
		this.DOM = Debugbase.DOM;
		this.math = Debugbase.math;
		this.module = Debugbase.module;
		this.system = Debugbase.system;
	}

	//Public 
	//--------------------------------------------------------------
	//--------------------------------------------------------------
	
	//Triggered when the DOM is ready:
	public init(params?: any): void {};
	public DOMReady(): void {};
	public DOMUpdated(): void {};
	public modulesMounted(): void {}; 
	public resize(): void {}; 
	public scroll(windowY: number): void {};
	
	public reset(previouState?, isDeeplink: boolean = false): void {};		
	public show(previouState?, isDeeplink: boolean = false): void {};		
	public hide(nextState?): void {};	

	public tick(delta?: number): void {};
	public pause(direction?: string): void {};
	public unpause(): void {};

	public DOMReadyCore(): void {
		if (this.containerSelector) {
			this.container =  document.querySelector(this.containerSelector);
		}
		this.DOMIsReady = true;
		this.DOMReady();
	}

	public destory(): void {
		this.id = null;
		this.app = null;
		this.state = null;
		this.module = null;
	}

	public pauseForMenu(direction = 'side') {
		
	}

	//Protected 
	//--------------------------------------------------------------
	//--------------------------------------------------------------

	//Allows other controllers to get refernces to other controllers
	//Allow any so controllers can just be instance of anything: 
	protected get(controllerId: string): Controller | any {		
		return Debugbase.controller.get(controllerId);
	}

	protected all(): Array<Controller> | Array<any> {
		//return Debugbase.controller.all();
		return null;
	}

}
