//Enums: 
import { ParticleType } from 'app/enum/Particles';
//Contracts: 
import { particles } from 'app/contracts/Particles'; 
import { homeData } from 'app/contracts/Data'; 
import { geometry } from 'app/contracts/Geometry'; 
import ParticleBehaviourLookup from './ParticleBehaviourLookup';
import { HomeParticle } from 'app/controller/home/HomeParticle';

export class ParticleManager {

	private _container: PIXI.Container;
	private _sceneProps: particles.SceneProps;
	private _particles: Array<particles.Particle> = [];
	private _fields: Array<particles.ParticleField> = [];
	private _i: number = 0;

	constructor (sceneProps: particles.SceneProps, container: PIXI.Container) { 
		//Create particle sets: 
		this._sceneProps = sceneProps;
		this._container = container;
	}

	//GETTER / SETTER 
	//--------------------------------------------------------------------------------------------
	public get particles() : Array<particles.Particle> {
		return this._particles;
	}

	//PUBLIC METHODS 
	//---------------------------------------------------------------------------------------------
	public setContainer(container: PIXI.particles.ParticleContainer): void {
		this._container = container;
	}

	public resize(): void {
		this._particles.forEach( (particle) => particle.resize( ) );
		this._fields.forEach( (field) => field.resize() );
	}

	public tick(): void {
		//this._particles.forEach( (particle) => particle.tick() );
		//this._fields.forEach( (field) => field.tick() );
		
		//console.time("Particles");
		//for (let i = 0, n = this._particles.length; i < n; i++) {
		//	this._particles[i].tick();
		//} 
	
		for (let i = 0, n=this._fields.length; i < n; i++) {
			this._fields[i].tick();
		} 

		
	}


	public destroy(): void {
		this._particles.forEach( (particle) => particle.destroy() );
		this._fields.forEach( (field) => field.destroy() );
	}

	public sortDepth(): void {
		//Reorder the children in the container by their depth: 
		this._container.children.sort( (a: any, b: any) => {
			if (a.z < b.z)  return -1; 
			else if (a.z > b.z) return 1;  
			return 0;
		});	
	}

	public getParticlesById(id: string): Array<particles.Particle> {
		
		let particles: Array<particles.Particle> = [];

		//Get any single partciles that match the id: 
		this._particles.forEach( (particle) =>  {
			if (particle.id === id) {
				particles.push(particle);
			}
		} );

		//Get all particles that belong to a field:
		this._fields.forEach( (field) =>  {
			if (field.id === id) {
				field.particles.forEach( (particle) => particles.push(particle) );
			}
		});

		return particles;

	}

	public getFieldById(id: string): particles.ParticleField {
		let matchedField: particles.ParticleField = null;
		
		this._fields.forEach( (field) =>  {
			if (field.id === id) {
				matchedField = field;
			}
		});
		
		return matchedField;

	}

	public addParticle<T extends particles.Particle>(classname: {new(s: particles.SceneProps, c: PIXI.Container, config: particles.ParticleConfig): T;}|string, config: particles.ParticleConfig, container: PIXI.Container = null): T {
		

	
		let targetContainer: PIXI.Container = container || this._container;
	

		let particle: T|HomeParticle;

		if(classname == 'HomeParticle') {
			particle = new HomeParticle(this._sceneProps, targetContainer, config);
		} else {
			particle  = new classname(this._sceneProps, targetContainer, config);
		}
		

		ParticleBehaviourLookup.addParticle(particle, particle.active)

		this._particles.push(particle);
		return particle;

	}

	public addField<T extends particles.ParticleField>(classname: {new(s: particles.SceneProps, c: PIXI.Container, config: particles.ParticleFieldConfig): T; }, config: particles.ParticleFieldConfig): T {
		let field: T = new classname(this._sceneProps, this._container, config);
		this._fields.push(field);
		return field;
	}

}

