import { TimelineItem, TimelineItemConfig, TimelineItemType} from 'app/module/home/TimelineItem';
import {HomeParticleJSONModel} from 'app/model/HomeParticleJSONModel';
import { DepartmentType } from 'app/data/Enums';
import {PathShapeType }  from 'app/module/home/ParticlePath';
//import * as PIXI from 'pixi.js';

export enum LayoutModeType {RANDOM = 'random', GRID = 'grid', LOGO = 'logo'}

//Masked text item timeline item used for Debug logo 
export class ParticleField extends TimelineItem {

	//Child partciles
	private _childParticles: Array<TimelineItem> = [];
	private _textureCount: number = 0;
	private _layoutMode: LayoutModeType = LayoutModeType.RANDOM;

	public create(jsonConfig: HomeParticleJSONModel, settings: TimelineItemConfig): void {
		//Set the department based on postion 
		this._layoutMode = (jsonConfig.layoutMode) ? jsonConfig.layoutMode : LayoutModeType.RANDOM;
		this.id  = jsonConfig.id;
		this.type = TimelineItemType.PARTICLE_FIELD;
		this._repellActive = true;
		this.department = (this.relativeX < 0.5) ? DepartmentType.SCREENS : DepartmentType.SPACES;
		this._createChildParticles(jsonConfig, settings);
	}

	public resize(containerWidth: number, containerHeight: number, displayScale: number, innerwidth: number, positionScale: number) {
		this._childParticles.forEach( (particle) => {
			particle.resize(containerWidth, containerHeight, displayScale, innerwidth, positionScale);
		});
	}

	public tick(xProgressRatio: number, containerX: number, mouseYRatio: number, mouseXRatio: number, mouseIsDown: boolean = false, velocityRatio: number = 0, instant: boolean = false, mousecords: any = null, mothcords: any = null): void {
		this._childParticles.forEach( (particle) => {
			particle.tick(xProgressRatio, containerX, mouseYRatio, mouseXRatio, mouseIsDown, velocityRatio, instant, mousecords, mothcords);
		});
	}

	public animateToWork(moveY: number, delay: number = 0): void {
		this._childParticles.forEach( (particle) => {
			particle.animateToWork(moveY, delay);
		});
	}

	public animateFromWork(delay: number = 1.0): void {
		this._childParticles.forEach( (particle) => {
			particle.animateFromWork(delay);
		});
	}

	public resetIntervalsAndAnimations(): void {
		this._childParticles.forEach( (particle) => {
			particle.resetIntervalsAndAnimations();
		});
	}

	public reset(): void {
		this._childParticles.forEach( (particle) => {
			particle.reset();
		});
	}

	public resetFromWork(offY: number): void {
		this._childParticles.forEach( (particle) => {
			particle.resetFromWork(offY);
		});
	}

	public getItems(): Array<TimelineItem> {
		return this._childParticles;
	}

	//Private 
	//----------------------------------------------------------------------------------------
	private _createChildParticles(masterConfig: HomeParticleJSONModel, settings: TimelineItemConfig): void {

		let relativeX: number;
		let relativeY: number;
		let easePower: number;
		let parallaxOffsetPower: number;
		let parallaxPowerMouse: number;
		let gridRelativeX: number = masterConfig.relativeX - masterConfig.relXSeed;
		let gridRelativeY: number = masterConfig.relativeY - masterConfig.relYSeed;
		let gridRelativeWidth: number =  masterConfig.relXSeed * 2;
		let gridRelativeHeight: number = masterConfig.relYSeed * 2;
		let cols: number = 39;
		let rows: number = Math.round(masterConfig.particleCount / cols);
		let gridSpacingX: number = gridRelativeWidth / cols;
		let gridSpacingY: number = gridRelativeHeight / rows;
		let colIndex: number = 0;
		let rowIndex: number = 0;
		let pathStartIndexRatio: number = 0;
		let pathIndexShare: number = 1 / cols;

		for (let i = 0; i < masterConfig.particleCount; i++) {

			if (this._layoutMode === LayoutModeType.RANDOM) {
				relativeX = this._randomiseParticleAttribute(masterConfig.relativeX, masterConfig.relXSeed, true);
				relativeY = this._randomiseParticleAttribute(masterConfig.relativeY, masterConfig.relYSeed, true);
				easePower =  this._randomiseParticleAttribute(masterConfig.easePower, masterConfig.easeSeed);
				parallaxOffsetPower = this._randomiseParticleAttribute(masterConfig.parallaxOffsetPower, masterConfig.offsetSeed);
				parallaxPowerMouse = this._randomiseParticleAttribute(masterConfig.parallaxOffsetPower, masterConfig.parallaxSeed);
			} else if (this._layoutMode === LayoutModeType.GRID) {
				relativeX  = gridRelativeX + (colIndex * gridSpacingX);
				relativeY  = gridRelativeY + (rowIndex * gridSpacingY);
				//easePower = masterConfig.easePower - (rowIndex * 0.02);
				easePower = masterConfig.easePower;
				parallaxOffsetPower = masterConfig.parallaxOffsetPower;
				//parallaxPowerMouse = -1 *  masterConfig.parallaxPowerMouse + (rowIndex * 0.02);
				parallaxPowerMouse = -1 *  masterConfig.parallaxPowerMouse;
				pathStartIndexRatio = colIndex * pathIndexShare;
				colIndex++;
				if (colIndex === cols) {
					colIndex = 0;
					rowIndex++;
				}
			}  else if (this._layoutMode === LayoutModeType.LOGO) {
				
				if (settings.logoPointPosition[i]) {
					//relativeX  = 0.485 + (settings.logoPointPosition[i].x * ( (278 * 0.85) / (1920 * 3) ) );
					//relativeY  = 0.3 + (settings.logoPointPosition[i].y * ( (323 * 0.85) / (window as any).cachedInnerHeight ) );

					//relativeX  = 0.45 + (settings.logoPointPosition[i].x * ( (278 * 2.7) / (1920 * 3) ) );
					relativeX  = 0.37 + (settings.logoPointPosition[i].x * ( (239 * 5.8) / (1920 * 3) ) );
					relativeY  = 0.02 + (settings.logoPointPosition[i].y * ( (360 * 3.2) / (window as any).cachedInnerHeight ) );

					if( (window as any).smallLogo ) {
						relativeX  = 0.482 + (settings.logoPointPosition[i].x * ( (239 * 1) / (1920 * 3) ) );
						relativeY  = 0.25 + (settings.logoPointPosition[i].y * ( (360 * 1) / (window as any).cachedInnerHeight ) );
					}


					if( (window as any).biglogo ) {
						relativeX  = 0.44 + (settings.logoPointPosition[i].x * ( (239 * 3.6) / (1920 * 3) ) );
						relativeY  = 0.03 + (settings.logoPointPosition[i].y * ( (360 * 3) / (window as any).cachedInnerHeight ) );
					}

					

				} else {
					relativeX = 0.5;
					relativeY = 0;
				}

				//easePower = masterConfig.easePower - (rowIndex * 0.02);
				easePower = masterConfig.easePower;
				parallaxOffsetPower = masterConfig.parallaxOffsetPower;
				//parallaxPowerMouse = -1 *  masterConfig.parallaxPowerMouse + (rowIndex * 0.02);
				parallaxPowerMouse = -1 *  masterConfig.parallaxPowerMouse;
				pathStartIndexRatio = colIndex * pathIndexShare;

			}

			let config: HomeParticleJSONModel = {
				id: `${masterConfig.id}-particle-${i}`,
				relativeX: relativeX,
				relativeY: relativeY,
				easePower: easePower,
				parallaxOffsetPower: parallaxOffsetPower,
				parallaxPowerMouse: parallaxPowerMouse,
				autoScale: masterConfig.autoScale,
				autoRotate: masterConfig.autoRotate,
				autoAlpha: masterConfig.autoAlpha,
				randomRotation: masterConfig.randomRotation,
				startScale:  masterConfig.startScale,
				minScale:  masterConfig.minScale,
				maxScale:  masterConfig.maxScale,
				autoRange:  masterConfig.autoRange,
				repell: true,
				alpha: masterConfig.alpha,
				bioAlphaMax: (masterConfig.bioAlphaMax) ? masterConfig.bioAlphaMax : null,
				bioAlphaMin: (masterConfig.bioAlphaMin) ? masterConfig.bioAlphaMin : null,
				tint: (masterConfig.tint) ? masterConfig.tint : null,
				path: (masterConfig.path) ? masterConfig.path : null,
				pathDirection: (masterConfig.pathDirection) ? masterConfig.pathDirection : null,
				pathStartIndexRatio: pathStartIndexRatio,
			};

			let texture: PIXI.Texture = this._getTexture(settings.textures as Array<PIXI.Texture>);

			this._childParticles.push( this._createChildParticle(config, texture, settings.container) );

		}

	}

	private _randomiseParticleAttribute(base: number, seed: number, baseOne: boolean = false): number  {
		
		let direction: number = (Math.random() < 0.5) ? -1 : 1;
		let seededValue: number;
		
		if (baseOne) {
			seededValue  =  (1 * (seed * Math.random()));
		} else {
			seededValue = base * (seed * Math.random());
		}

		return base + (seededValue * direction);
	}

	private _getTexture(textures: Array<PIXI.Texture>): PIXI.Texture {

		let texture: PIXI.Texture = textures[this._textureCount];

		this._textureCount++;
		if (this._textureCount === textures.length) {
			this._textureCount = 0;
		}

		return texture; 

	}





	private _createChildParticle(config: HomeParticleJSONModel, texture: PIXI.Texture, container: PIXI.Container): TimelineItem {
		
		//A normal pixi sprite that will be animated along the timeline
		let item = new TimelineItem();
				
		item.relativeX = config.relativeX;
		item.relativeY = config.relativeY;
		item.easePower = config.easePower;
		item.parallaxOffsetPower = config.parallaxOffsetPower;
		item.parallaxPowerMouse = config.parallaxPowerMouse;	

		if (config.alpha) {
			item.alpha = config.alpha;
		}

		if (config.tint) {
			item.tint = config.tint;
		}

		
		//if() {
			//config.path = PathShapeType.CIRCLE;
		//}


		item.create(config, {
			container: container,
			texture: texture
		});

		return item;

	}

}
