import {Controller} from 'debugbase/abstract/Controller';
import { Stats } from 'pixi-stats';


export class PixiController extends Controller {


	
	public onAppReady: CallableFunction;
	public $pixi: PIXI.Application;
	public $homeContainer:  PIXI.Container;

	
	private _res: number;
	private _allowResize: boolean = true;
	
	public async init(): Promise<void> {
		await this.createPixiApp();

	}


	public resize(fromOrientationChange: boolean = false): void {
		//Resize the renderer to 100% height and width: 

		if (!this.$pixi || (!this._allowResize && !fromOrientationChange) ) {
			return;
		}

		this._res = this.getPixiResolution();
		this.$pixi.resolution = this._res;
		this.$pixi.renderer.resolution = this._res;
		
		(window as any).pixiResolution = this._res;


		let innerWidth = window.innerWidth;
		let innerHeight = window.innerHeight

		this.$pixi.resize(  Math.round(innerWidth * this._res),  Math.round(innerHeight * this._res) );
		let canvas = document.getElementById("pixi") as HTMLCanvasElement;
		canvas.style.width = innerWidth + 'px';
		canvas.style.height =innerHeight + 'px';
		canvas.setAttribute('width',  Math.round(innerWidth * this._res) + '');
		canvas.setAttribute('height',  Math.round( innerHeight * this._res) + '');
		

	}

	public getPixi(): PIXI.Renderer {
		return this.$pixi;
	}



	public getHome(): PIXI.Container {
		return this.$homeContainer;
	}



	//PRIVATE 
	//------------------------------------------------------------------------------------


	private createPixiApp(): void {

		//Create a PIXI app to render all the big full screen bits: 
		this._res = this.getPixiResolution();
		this.createAppAsync();
	}


	private async  createAppAsync() {
		
		
		this.$pixi = new PIXI.Application({});
		
		await this.$pixi.init({
			resizeTo: window,
			background: '#000000',
			//background: '#ff0000',
			backgroundAlpha: 1,
			resolution: this.getPixiResolution(),
			transparent: true,
			width: window.innerWidth * this._res, 
			height: window.innerHeight * this._res,
			view: document.getElementById('pixi'),
			antialias: false,
			useContextAlpha: false,
			powerPreference: "high-performance",
			premultipliedAlpha: true,
			preserveDrawingBuffer: false,
			preferWebGLVersion: 2,
			eventFeatures: {
				move: false,
				globalMove: false,
				click: false,
				wheel: false,
			}
		});


		
		Object.assign(this.$pixi.renderer.events.features, {
			move: false,
			globalMove: false,
			click: false,
			wheel: false,
		})
	

		//For debugging
		globalThis.__PIXI_APP__ = this.$pixi;
		
	

		//const canvas = document.querySelector(".extrawebgl") as HTMLCanvasElement;
		// Initialize the GL context
		//const gl = canvas.getContext("webgl");

			
		(window as any).pixiResolution = this._res;
		
		this.$pixi.ticker.minFPS = 60;

		console.log(this.$pixi.ticker);
	
		this.resize();
		//Add the section conatiners for easier layering: 
		this.$homeContainer = new PIXI.Container();
		this.$homeContainer.interactive = false;
		this.$homeContainer.interactiveChildren = false;
		this.$homeContainer.visible = true;
		//this.homeContainer.cullable = true;

		this.$pixi.stage.addChild(
			this.$homeContainer
		);


	
		/*if(this._showPixiFps) {
			const stats = new Stats(this.$pixi.renderer);
		}
		
		if (this._userCounter) {
			const fpsCounter = new PixiFps();
			fpsCounter.scale.set(0.5, 0.5);
			fpsCounter.y = window.innerHeight - 30;
			fpsCounter.x = 10; 
			this._stage.addChild(fpsCounter);
		}
			*/
		

		this.onAppReady();
		

	}



	//Hi-res assets should not be loaded onto mobile, so add  logic here 
	//Also may need to load low res onto Android devices: 
	private getPixiResolution(): number {

		//console.log(' window.devicePixelRatio',  window.devicePixelRatio)

		//return 1;
	
		let resolution =  (window.innerWidth <= 1600 && window.devicePixelRatio)  ? window.devicePixelRatio : 1; 
		if (resolution > 2) {
			resolution = 2;
		}
		//resolution = 1;
		//resolution = 2.0;
		//let resolution = (window.innerWidth < 900) ? 0.5 : 1.0;
		return resolution;
		
	}



	
}
