export default {


    timers:[],
    lastTime: Date.now(),
    id: 1, 
    idCount: 1,
    paused: false,
    pauseInterval: null,

    setTimeout(closure, triggerAt, id = null, group = null): number|string {


        
        let useId = id || this.idCount;
        this.timers.push({
            type: 'TIMEOUT', 
            closure: closure,
            triggerAt: triggerAt,
            count: 0,
            id: useId,
            group: group
        });

        this.idCount++;

        return useId; 

    },
    
    clearTimeout(id) {
       

        if(!id) {
            return;
        }

        let i = 0; 
        while (i < this.timers.length) {
            if (this.timers[i].id == id) {
                this.timers[i].closure = null
                this.timers.splice(i, 1);
                break;
            } 
            i++;
        }
    },

    clearGroup(group: string) {

        let i = 0; 
        while (i < this.timers.length) {
            if (this.timers[i].group == group) {
                this.timers[i].closure = null
                this.timers.splice(i, 1);
                continue;
            } 
            i++;
        }
    },

    pause(delay: number = 1000) {
        
        if(!delay) this.paused = true; 

        clearTimeout(this.pauseInterval)
    
       this.pauseInterval = setTimeout( () => {
            this.paused = true; 
            this.pauseInterval = false;
        }, delay)

    },

    unpause() {
        clearTimeout(this.pauseInterval);
        this.paused = false; 
    },

    tick() {

        if (this.paused) {
            this.lastTime = Date.now();
            return;
        }

        let i=0;

        let time = Date.now();
        let delta =  time - this.lastTime;


        

        while (i < this.timers.length) {
            
            this.timers[i].count += delta;

            if (this.timers[i].count >= this.timers[i].triggerAt) {
                this.timers[i].closure();
                
                if(this.timers[i]) {
                    this.timers[i].closure = null;
                    this.timers.splice(i, 1);
                }



            } else {
                i++;
            }   

        }

        this.lastTime = time;

    }



}