	public changeParticle(type: string|null = null, zDepth = false) {

		if(!type) {
			let rand = Math.random() 
			type = 'solid-ring.png'
			if(rand < 0.2) type = 'solid-square.png';
			else if(rand < 0.4) type = 'solid2.png';

		}
		
		
		this.particleProps.sprite.texture =  (window as any).textures[type];
		
		if(zDepth) {
			this.particleProps.sprite.zIndex = 100; 
		}

		return type;

	}

	public resetParticle() {
		
		this.particleProps.sprite.texture =  (window as any).textures['solid2.png'];

	}




	//Particle is slideing from on scene to another
	public switchScenes(type): void {


		this._isTransitionParticle = true;
		this.deactivateAllBehaviours(true);
		this._transitioning = false;
		this._switching = type;
		this.active = true;
		this._startSceneSwitch(type);


	
	}

	
	public random(base: number = 0, seed: number = 1.0, miniusSeed: boolean = false, round: boolean = false, miniusAll:boolean = false): number 
	{
		let d = miniusSeed && Math.random() < 0.5 ? -1 : 1; 
		let num = base + (Math.random() * (seed * d));

		if (miniusAll) {
			num *= Math.random() < 0.5 ? -1 : 1;
		}
		return (round) ? Math.round(num) : num;
	}




	public setMouseParallaxByScale(): void {

		this._animatedProps.setEase({
			x:  this._animatedProps.targetScale * 100,
			y: this._animatedProps.targetScale * 50,
			scale: this._animatedProps.targetScale * 50,
		});

		this.mouseParallax.setup({
			ease: {x: 20, y: 20}, 
			power: {x: (0.1 * this._animatedProps.targetScale), y: (0.16 * this._animatedProps.targetScale)} 
		});

	}




	

