	//Trugger Home partile colour switch: 
	//Happens on hover or on timeout counter: 
	public homeColourSwitch(config: any, random: boolean, direction: number) {
	
		
		return;
		random  = true;
		let ratio = random ? Math.random() : this._index/900;

		if (ratio > 1) {
			ratio = 1;
		}
		if (direction == -1) {
			ratio = 1 - ratio;
		}

		this.counter.active = true;
		this.counter.onTrigger = () => { this.colourise(config.num, config.row, config.col) }
		this.counter.loop = false;
		this.counter.triggerAtFrames = ratio * 30;
		this.counter.start();

	}



	public startCounter(counter: ParticleCounter, frames: number, onTrigger: CallableFunction, loop: boolean = false) {
		counter.active = true;
		counter.triggerAtFrames = frames;
		counter.onTrigger = onTrigger;
		counter.loop = loop;
		counter.start();
	}

