	public setHomeFocused() {

		this.orbitPath.xMuliplier = this.orbitPath.yMuliplier = this.orbitPath.expandMuliplier = 5;

		if(this._homeType == 1) {
			this.setupPath(this.orbitPath, {
				//--> OLD x: [25, 40], y: [25, 40], speed: [0.5, 0.7], scale: [0.2, 0.25], alpha: [0.2, 0.25], 
				x: [0, 200], y: [0, 200], speed: [0.1, 1], scale: [0.05, 0.12], alpha: [0.6, 1], 
				randomise: {unsync: true, direction: true} 
			});
	
			this._animatedProps.targetScale =  	this._animatedProps.scale =  0.12;
			this._animatedProps.targetAlpha = this._animatedProps.alpha  = this.random(0.1, 1)
		}
	

	}

