	/** Show particle as it's scene slides in  */
	public show(): void {
		this._animatedProps.targetAlpha = this._ta;
		this._animatedProps.scale = 0;
		this._animatedProps.targetScale = this._ts;
		this.hover.active = true;
		this._hiding = false;
	}

	/** Hide particle as its scene slides out  */
	public hide(): void {
		

		
		this.orbitPath.active = false;
		this._hiding = true;

		this._ta = this._animatedProps.targetAlpha;
		this._ts = this._animatedProps.targetScale;
		

		let flash = Math.random() < 0.07;
		flash = false;
		this._animatedProps.targetAlpha = 0;
		this._animatedProps.targetScale = (flash) ? 1 : 0;


		this._animatedProps.targetAlpha = 0;
		this._animatedProps.targetScale = 0;

		this.orbitPath.active = false;	

		this._animatedProps.setEase( {
			scale: 30,
			alpha: 30,
			x: 1,
			y: 1
		} )

	}



