//state.bind(id, elementSeletor / Elements)

import {State} from 'debugbase/state/State';

export class StateManager {

	private _states: Array<State> = [];			//States
	private _refs: any = {}; 					//Refs for direct access to states 
	
	//Direct access data objects (quicker than string)
	public get data(): any {
		return this._refs;
	}

	public set(id: string, value: any): any {
		return null;
		return this._getById(id).setValue(value);
	}

	public get(id: string): any {
		return null;
		return this._getById(id).getValue();
	}

	public DOMReady(): void {
		//this._createAutoStateBinds();
	}

	public DOMUpdated(): void {
		//this._createAutoStateBinds();
	}

	public bind(id: string, $target: Element, setHTMLFromState: boolean): void {
		this._getById(id).bind($target, setHTMLFromState);	
	}

	//private 
	//------------------------------------------------------------
	//------------------------------------------------------------
	//------------------------------------------------------------
	//------------------------------------------------------------

	private _getById(id): State {

		for (var i = 0; i < this._states.length; i++) {
			if (this._states[i].config.id === id) {
				return this._states[i];
			}
		}

		return this._createState(id);

	}

	//Select all states in the HTML: 
	//<h1 s='project-title' s-created></h1>
	/*private  _createAutoStateBinds(): void {
		
		var $targets = $('[s]');
		var id, instance;
		
		$targets.each(function(index) {
			id = $(this).data('s');
			instance = this.get(id);
			instance.config = FrameworkTools.createConfig($(this), instance, 's-{prop}'');
			instance.props.$target = $(this);
			instance.init();
		});

	}
	*/

	private _createState(id: string): State {
		this._refs[id] = this._states[this._states.length - 1];
		return this._refs[id];
	}

}

