import Navigo from 'navigo';
import {ControllerManager} from './ControllerManager';

interface RouteConfig {
	path: string; 
	controller: string; 
	function?: string; 
}

export class RouteManager {

	public openingRouteOnly: boolean = true;

	private _routes: RouteConfig[] = [];
	private _controllerManager: ControllerManager; 

	public set controllerManager(manager: ControllerManager) {
		this._controllerManager = manager;
	}

	public set(routes: RouteConfig[]): void {
		this._routes = routes;
	}

	public init() : void {
		if (this._routes.length) {
			this._createRouter();
		}
	}

	private _createRouter() {
	
		let router = new Navigo(null, {});

		var navigoRoutes = {};


	
		for (let i = 0; i < this._routes.length; i++ ) {
			
			let url = this._routes[i].path;
			let controller =  this._controllerManager.get(this._routes[i].controller);
			let func = (this._routes[i].function) ? this._routes[i].function : 'init';

		
		
			//console.log('url', url)

			navigoRoutes[url] = (params = null, query = null) => {
				
				
					console.log('func', func);
					
				controller[func](params);

				

				//console.log('controller', controller)
				//console.log('func', func)
				//console.log('params', params)
				//Only want to route first load:
				if ( this.openingRouteOnly ) {
					(router as any).pause();
				}
			};

			navigoRoutes[url + '#0'] = (params = null, query = null) => {
				controller[func](params);
				//Only want to route first load:
				if ( this.openingRouteOnly ) {
					(router as any).pause();
				}
			};
			

			navigoRoutes[url + '#1'] = (params = null, query = null) => {
				controller[func](params);
				//Only want to route first load:
				if ( this.openingRouteOnly ) {
					(router as any).pause();
				}
			};

			navigoRoutes[url + '#2'] = (params = null, query = null) => {
				controller[func](params);
				//Only want to route first load:
				if ( this.openingRouteOnly ) {
					(router as any).pause();
				}
			};

			navigoRoutes[url + '#3'] = (params = null, query = null) => {
				controller[func](params);
				//Only want to route first load:
				if ( this.openingRouteOnly ) {
					(router as any).pause();
				}
			};


			
			navigoRoutes[url + '#4'] = (params = null, query = null) => {
				controller[func](params);
				//Only want to route first load:
				if ( this.openingRouteOnly ) {
					(router as any).pause();
				}
			};

			navigoRoutes[url + '#5'] = (params = null, query = null) => {
				controller[func](params);
				//Only want to route first load:
				if ( this.openingRouteOnly ) {
					(router as any).pause();
				}
			};

			navigoRoutes[url + '#6'] = (params = null, query = null) => {
				controller[func](params);
				//Only want to route first load:
				if ( this.openingRouteOnly ) {
					(router as any).pause();
				}
			};
			
		}

		(router as any).notFound(function (query) {
  			//console.log("ROUTE NOT FOUND----");
		});

		//debugger;
		(router as any) 
			.on(navigoRoutes)
			.resolve();
	}

}

