	/** Home particle textures are ready so */
	public createParticlesPixi($homeContainer: PIXI.Container, spritesheet: PIXI.Spritesheet): void {
		
		
		window.homeSpritesheet = spritesheet;
		this.resize();
		this._setContainerX();
		this._$homeContainer = $homeContainer;

		this._$homeContainer.alpha = 0;


		let guidContainer = new PIXI.Container();
		this._$homeContainer.addChild(guidContainer);

		ParticleGridTargets.setGuideContainer(guidContainer)

		this.$iconContainer = new PIXI.Container();
		this._sceneProps.iconContainer = this.$iconContainer;

		this._$homeContainer.addChild(this.$iconContainer);

		(window as any).pass2Container =  new PIXI.Container();
		(window as any).pass2Container.name = 'PASS 2 PARALLAX CONTAINER';
	
		this._checkHasParticlesCreateInit();


		//(window as any).preloadController.onHomePixelDataReady = () => {
					//If this is a deep link direct to spaces / screens: 
				
		this._createItems($homeContainer, spritesheet, 1);
		this._createParticles(spritesheet);
		this._createItems($homeContainer, spritesheet, 2);
		this._created = true;


		this.resize();
		setTimeout(() => {
			this.resize();
		}, 20);
	
			
		//this.homeIntroStart();
	

	}

	public homeIntroStart() 
	{
		let introElements = this._getParticles('introp')
		introElements.forEach( (item) => {
			item.fadeIn(0, 0, 0);
		})
	}

	private homeIntroShow() 
	{
		this._created = true;

		let introElements = this._getParticles('introp')
		let d  = 0.5;
		introElements.forEach( (item) => {
			item.fadeIn(d, (0.2 + Math.random() * 0.2), 0.5);
			d += 0.5;
		})
		//HomeScreens.showAtStart();
	}



	private _initScreensElements()
	{

		window.framesprite =  this._getParticles('screen-frames')[0].sprite;

		HomeScreens.init(
			{
				topLeft:this._getParticles('screensTVTopLeft')[0],
				topRight:this._getParticles('screensTVTopRight')[0],
				bottomLeft:this._getParticles('screensTVBottomLeft')[0],
				bottomRight:this._getParticles('screensTVBottomRight')[0],
			}, 
			this._getParticles('systems-block').concat(this._getParticles('systems-block-move') )
		)
	}




	private _setupForWork() {
		
		this._particleManager.homeField.setupForWork(this._deeplinkWorkDepartment);
		let offY: number = 1; 
		this._items.forEach( (item: TimelineItem) => {
			item.setAnimatingToWork(true);
			item.animateToWork(offY, 0, true);
		});

		
		//this._showHiddenOnDeeplink();


	}


