	public resize(firstResize: boolean = false): void {


		
		this._innerHeight = window.innerHeight ;
		this._innerWidth = window.innerWidth;
		(window as any).cachedInnerWidth = this._innerWidth;
		(window as any).cachedInnerHeight = this._innerHeight;
		this._virtualHeight = (window as any).cachedInnerHeight ;

		this._setSizes();
		this._setMouse();
		this._setMouseParallax();
		this._setMouseProgress();

		//Set the scale of the 
		let scale = this._virtualWidth  / (1920 * 3);
		if (scale > 1.0) {
			scale = 1.0;
		}

		//Position scale
		//Note this divided by 2.5 as the layouts are currently compressed in this way to make transition quicker
		//Value can used to help recaluclate positions and paths
		let positionScale = this._virtualWidth  / (1920 * this._viewPortCount);

		//Loop through the items and pass the new values: 
		this._items.forEach((item: TimelineItem) => {
			item.resize(this._virtualWidth, this._virtualHeight, scale, this._innerWidth, positionScale, this._viewPortCount);
		});
		
		//Reset the width of the tracked elements
		this._cssElements.forEach((css: CSSElement) => {
			if (css.$target.clientWidth) {
				css.width = css.$target.clientWidth;
			}
		});

		if (this._particleManager) {
			
			this._particleManager.resize();
			
			if (firstResize && 	this._particleManager.homeField) {
				this._particleManager.homeField.resizeEvent();
			}

		}


		
	

	}

	public tick(mousecords: any = null, mouseIsDown: boolean = null, velocity: number = null, instant: boolean = false, skipCheck: boolean = false): void {


		
		if (!this._created) {
			//console.log('!this._created');
			return;
		}

	//	if (!skipCheck && this._animatingToWork) {
		//if (!skipCheck && this._animatingToWork) {
		//	return;
		//}

	

		//this._resizeCounter++;
		//this.resize();
		//if (this._resizeCounter > 120) {
		//	this.resize();
		//	this._resizeCounter = 0;
		//}
	

	
		this._setMouse();
		this._setMouseParallax();
		this._setMouseProgress();
		//this._updateTargets();
		
		this._velocity = this._sceneProps.progress.velocity = velocity;



		this._setContentZoneAreas();
		this._setContentZoneParallax();
		this._setContentZonePositions();

		

		this._timelineRegionManager.tick(mousecords, this._containerX, this._virtualWidth, this._virtualHeight, this._progress, this._velocity);
		

		//YYYY this._setTransitionState();

		let ratioYPos = (mousecords.y / this._innerHeight) - 0.5 ;
		let ratioXPos = (mousecords.x / this._innerWidth) - 0.5 ;
		let velocityRatio = (this._velocity / 10);

	
		ParticleHitTests.clear();
		ParticleTargets.clear();
		
		for(let i=0, n=this._items.length; i<n; i++) {
			this._items[i].tick(this._progress, this._containerX, ratioYPos, ratioXPos, mouseIsDown, velocityRatio, instant, mousecords);
		};


		ParticleHitTests.tick();
		
		this._particleManager.tick();


		HomeScreens.tick();

		if((window as any).iconContainer) {
			(window as any).iconContainer.alpha = Math.max(0, 1 - (this._progress / 0.25));
		}

		if((window as any).systemsContainer) {
			(window as any).systemsContainer.alpha = Math.max(0, (this._progress - 0.75) / 0.25);
		}
			

		
		

	}




	/** Things have to be stopped while moving/dragging. So check what state timeline is in:   */
	private _setTransitionState(): void {
		
		//If transitiong then check if should be reset 
		//Note touch itseld

		if (this._sceneProps.flags.transitioning) {
			
			this._sceneProps.flags.transitioning = !this._atSceneVelocityThershold() || !this._atSceneBreakPoint();
			if (!this._sceneProps.flags.transitioning) {
				this._particleManager.homeField.endTransition();
			}
			
			return;
		} 

		this._sceneProps.flags.transitioning = !this._atSceneVelocityThershold() || !this._atSceneBreakPoint();

		if (this._sceneProps.flags.transitioning) {
			this._particleManager.homeField.startTransition();
		}



	}

	/** Is mouse down. Exclude touch (as we want things to stay ineter) */
	private _screenDragging(): boolean {
		return this._sceneProps.mouse.isDown && !this._sceneProps.mouse.isTouch; 
	}

	/** Is the movement slow enough to be considered safely locked in scene */
	private _atSceneVelocityThershold(): boolean {
		return Math.abs(this._velocity) < 0.0001;
	}

	/** Scene is at home|spaces|screens */
	private _atSceneBreakPoint(): boolean {
		let tolerance: number = 0.03;
		return (this._progress >= -tolerance && this._progress <= tolerance) 
		|| (this._progress >= 0.5-tolerance && this._progress <= 0.5 + tolerance) 
		|| (this._progress >= 1-tolerance && this._progress <= 1 + tolerance) 
	}
	

