	/** Reset for deeplink */
	public resetForDeeplink(): void {

		this._animatingToWork = false;

		//Loop through home particles at reset: 
		this._items.forEach((item: TimelineItem) => {
			item.reset();
		});

		//Reset the y position of the spaces / screens UI elements: 
		gsap.TweenMax.set([this._domRefs.screens, this._domRefs.spaces], {y: 0});

	}

	/** Reset when coming back from work */
	public resetFromWork(department: DepartmentType): void {
	
		this._animatingToWork = false;

		let offY: number = this._getWorkOffY();	

		//Loop through home particles at reset 
		this._items.forEach((item: TimelineItem) => {
			//If active department then reset in place for the animation in
			//Otherwise just reset as standard tile 
			if (item.department ===  department) {
				item.resetFromWork(offY);
			} else {
				item.reset();
			}
		});

		//Put the HTML ui element for the department off-screen: 
		let $ui: HTMLElement = this._domRefs[department];
		gsap.TweenMax.set($ui,  {y: offY});

	}

