	//Create the larger items used in the home structure like logos, swirls, etc
	private _createItems($homeContainer: PIXI.Container, spritesheet: PIXI.Spritesheet, pass = 1): void {


		//console.log('spritesheet', spritesheet);

		ParticleHitTests.setGuideContainer($homeContainer);

		//Loop through and create all particles: 
		this._config.particles.forEach( (particleData: HomeParticleJSONModel) => {

			if (!particleData.disable) {
				if (particleData.type === 'field') {
					//this._createParticleField($homeContainer, spritesheet, particleData);		
				} else {
					if(pass == 1 && !particleData.pass) {
						this._createParticle($homeContainer, spritesheet, particleData, pass);
					} else if(pass == 2 && particleData.pass == 2) {
						this._createParticle($homeContainer, spritesheet, particleData, pass);
					}
							
				}
				
			}
		});

		if(pass == 1) {
			//Loop through and create all particles: 
			this._config.regions.forEach( (region: TimelineRegionParams) => {	
				this._createTimelineRegion(region);
			});

			
		}


		this._setContainerX();
		this.resize();

	}

	/** Create single partcile */
	private _createParticle($homeContainer: PIXI.Container, spritesheet: PIXI.Spritesheet, particleData: HomeParticleJSONModel, pass = 1): void {
		
		let item: TimelineItem;
		if (particleData.type === HomeParticleType.MASKED_TEXT ) {
			
			//Maked text setup
			item = new MaskedText( );
			item = this._configParticle<MaskedText>(item as MaskedText, particleData);
			item.create(particleData, {
				container: $homeContainer,
				maskTexture: spritesheet.textures[particleData.maskTexture],
				backgroundTexture: spritesheet.textures[particleData.backgroundTexture]
			}); 

		} else if (particleData.type === HomeParticleType.RELATIVE_CHILDREN ) {
			
			//Maked text setup
			
			item = new RelativeChildren( );
			item = this._configParticle<RelativeChildren>(item as RelativeChildren, particleData);

			item.create(particleData, {
				container: $homeContainer,
				textures: spritesheet.textures
			}); 

		}  else {
			//A normal pixi sprite that will be animated along the timeline

			let container = pass == 2 ? (window as any).pass2Container : $homeContainer;

			
			item = new TimelineItem();
			item = this._configParticle<TimelineItem>(item, particleData);
			item.create(particleData, {
				container: container,
				texture: spritesheet.textures[particleData.texture]	,
				textureMobile: spritesheet.textures[particleData.textureMobile]	,
			}, this._sceneProps);
			
		

		}
		

		//Push in the item 
		this._items.push(item);

	}

		/** Create single partcile */
	private _createParticleField($homeContainer: PIXI.Container, spritesheet: PIXI.Spritesheet, particleData: HomeParticleJSONModel): void {
		
		let item: TimelineItem;
		//Particle fields: 
		//Create a group of textures from the strings 
		let textures: Array <PIXI.Texture> = [];
		particleData.textures.forEach( (textureString) => {
			textures.push(spritesheet.textures[textureString]);
		});

		//Maked text setup:
		item = new ParticleField( );
		item = this._configParticle<ParticleField>(item as ParticleField, particleData);
		item.create(particleData, {
			container: $homeContainer,
			textures: textures,
			particleCount: particleData.particleCount,
			//Pass the logo point data already processed on bitmap on the logo: 
			logoPointPosition: this._logoGrid.gridData
		}); 

		//Push in the item 
		this._items.push(item);

	}

	/** Config common particle settings */
	private _configParticle<T extends TimelineItem>(item: T, config: HomeParticleJSONModel): T {
		
		item.relativeX = config.relativeX;
		item.relativeY = config.relativeY;
		item.easePower = config.easePower;
		item.parallaxOffsetPower = config.parallaxOffsetPower;
		item.parallaxPowerMouse = config.parallaxPowerMouse;	
		item.textureName = config.texture;

		if (config.alpha) {
			item.alpha = config.alpha;
		}

		if (config.tint) {
			item.tint = config.tint;
		}
		
		return item;

	}

	/** Set the container x Postion */
	private _setContainerX(): void {
		
		
		let totalSlides = 4;
		let scale = 1  - (this._viewPortCount/totalSlides);
		let viewPortFix = (this._innerWidth/2) * scale;
		let progressFix = (this._progress/2) * scale;
		
		this._containerX = - 1 * ((this._progress + progressFix) * ((this._virtualWidth - this._innerWidth)) - viewPortFix);


		this._sceneProps.progress.containerX = this._containerX;
		this._sceneProps.progress.edgeX =  (this._sceneProps.sizes.container.w - this._sceneProps.sizes.window.w) + this._containerX;
		//this._sceneProps.progress.relX = 

	}

	//Turn off on CSS classes based on their 
	//Used to hide show the intro para and the screens / spaces UI 
	private _setCSSClasses(instant: boolean = false): void {

		
		//Compress for 2.5 width timeline 
		//this._cssElements[0].relativeX = (this._viewPortCount === 2.5) ?  0.203 : 0.178;
		//this._cssElements[1].relativeX = (this._viewPortCount === 2.5) ?  0.799 : 0.833;

		//this._setCSSWidths();


		return;
		
		if (this._innerWidth < HomeScreens.switchScreensLayoutAt) {

			let width = HomeScreens.getTopScreenWidth() - 20;
			if(width < 300) width = 300;
			fastdom.mutate( () => {
				this._domRefs.workContainer.style.setProperty('--home-ui-width', `${width}px`);
			});
		}
	

		
		let screenX = 0;
		this._cssElements.forEach((element) => {
			
			if (element.syncX) {
				//element.$target.style.transform = this._getCSSXtransform(element, instant);
				//gsap.TweenMax.set(element.$target, {x: this._getCSSY(element, instant)} );

				element.xRecord = Math.round( this._getCSSX(element, instant) );	

			
			
				if(element.id ==  DepartmentType.SCREENS) {
					screenX = HomeScreens.getRightScreenX() + 15 - element.xRecord;
					
					let opacity = 1
					
					if (this._innerWidth >= HomeScreens.switchScreensLayoutAt) {

						let fadeDown = this._mathHelper.mapBetween(this._progress, 0, 1, 0.48, 0.63);
						opacity =  1 - fadeDown;
					}

					element.$target.style.opacity = '' + opacity;
				}

				fastdom.mutate( () => {
					element.$target.style.transform = `translate3d(${element.xRecord}px, 0px, 0)`;
					this._domRefs.screens.style.setProperty('--screens-tv-edge', `${screenX}px`);
				})
			


			}	


			if (!element.showing && !element.once && this._progress >  element.min && this._progress < element.max && this._velocity < 0.01) {
				this._dom.addClass(element.$target, '-show');
				element.showing = true;
				if (element.id === 'dragToExplore') {
					//gsap.TweenMax.to('#home-paging .ui__paging__item', 0.4, {autoAlpha: 0, ease: gsap.Power0.easeNone} );
					//let label = this._mouseController.lastPointerWasTouch() ?  'Swipe to explore' : 'Drag to explore';
					//DelayHelper.setTimeout(() => this._pagingTextScrable.setText(label), 10);	
					//setTimeout(() => this._pagingTextScrable.setText('New site coming soon'), 10);	
				}
			} else if (element.showing && (this._progress <  element.min || this._progress > element.max)) {
				this._dom.removeClass(element.$target, '-show');
			

				element.showing = false;
				if ( element.hideCallback) {
					element.hideCallback(element);
				}
			}

		});
	}






	/** Timeline region effects */
	private _createTimelineRegion(params: TimelineRegionParams): void {
		params.pixiContainer = this._$homeContainer;
		params.onEnter = (id: string) => this._onTimeRegionEnter( id );
		params.onExit = (id: string) => this._onTimeRegionExit( id );
		this._timelineRegionManager.add( params );
	}



