	private _getParticles(id: string): Array<TimelineItem> {
		let particles: Array<TimelineItem> = [];

		this._items.forEach( (timelineItem: TimelineItem) =>  {

			if (timelineItem.id === id) {

				if (timelineItem.type === TimelineItemType.PARTICLE_FIELD || timelineItem.type === TimelineItemType.RELATIVE_CHILDREN) {
					let particlesItems = (timelineItem as ParticleField).getItems();
					for (let i = 0; i < particlesItems.length; i++) {
						particles.push(particlesItems[i]);
					}
				}

				if (timelineItem.type === TimelineItemType.PARTICLE) {
					particles.push(timelineItem);
				}
			}
			
		} );

		return particles;

	}

	private _setParticleState(particlesId: string, timelineState: TimelineStateType): void {
		this._getParticles(particlesId).forEach( (particle: TimelineItem) => {
			particle.setTimelineState(timelineState);
		});
	}

	private _setParticleRepellCenter(particlesId: string, center: Point): void {
		this._getParticles(particlesId).forEach( (particle: TimelineItem) => {
			particle.setParticleRepellCenter(center);
		});
	}

