export enum ParticleBehaviourTagType {
	//CORE TYPES: 
	TINT = 'tint',
	TEXTURE = 'texture',
	POSITION = 'position',								//Default position that partcile will move to (can be overidden by the strong attract/repell/path behaviours)
	//ADVANCED TYPES: 
	PROGRESS_PARALLAX = 'progress_parallax',			//Progress trough scene or timeline and set element position
	MOUSE_PARALLAX = 'mouse_parallax',		   			//Mouse (or accelerometer movement effects mouse)
	PROGRESS_PROPERTIES = 'progress_properties',		//Effect particle scale/alpha/rotation based on progress
	ATTRACT = 'attract',						  		//Atract to a object or mouse (Including defaul resting position)
	REPELL = 'repell',							   		//Repell around a point (Mouse of object)
	PATH = 'path',										//Orbit or 
	EMIT = 'emit',										//Emit child particles randomly
	TRAIL = 'trail',									//Emit trail type partciles  			
	LAMP = 'lamp',										//Set particles alha/tint based on proxy to target (mouse or object)	
}

