    //Deeplink to work, need to load the particle  
	public setupForWork(department: DepartmentType) {


        this._canProcessDragUpdate = true;
        this._dragMinAlpha = 1;
        
        
        this._transitionParticles.forEach(particle => {

            particle.activate();
            particle.setVisible(true);
            particle.active = true;
            particle.animatedProps.alpha = 0;
            particle.animatedProps.scale = 0;
            particle.animatedProps.targetAlpha = 1;
            particle.animatedProps.targetScale = 1;
            particle.moveSeed = particle.random(1, 0.2);
            //particle.sineSeed = particle.random(0.4, 0.8);
            particle.position.set({x: 0.36, y: 0.5}, true)

            particle.orbitPath.active = true;
            particle.orbitPath.expandMuliplier = 0;
            particle.orbitPath.xMuliplier = 0;
            particle.orbitPath.xMuliplier = 0;

            if (department == DepartmentType.SPACES) {
                particle.orbitPath.path = LookupType.CIRCLE;
               
             } else {
                particle.orbitPath.path = LookupType.RECT;
               
             }


            particle.orbitPath.randomise(true, true)
            
            //if(Math.random() < 0.5 ) particle.colour.set('#069c8a')
            //else particle.colour.set('#07dad8')

           // particle.colour.set(HomeColours.getRandomColour(HomeColours.dynamicSets.differ ))

           particle.colour.set(HomeColours.getRandomColour(HomeColours.dynamicSets.m4g ))

            particle.animatedProps.setEase({
                x: 30, 
                y: 30
            })

            setTimeout( () => {
                
                 particle.orbitPath.active = true; 
                 particle.animatedProps.targetAlpha = 0.2 + Math.random() * 0.6;
                 particle.animatedProps.targetScale = 0.2 + Math.random() * 0.15;
                 particle.orbitPath.speed = 1;
                 particle.position.set({x: 0.375, y: 0.5}, true)
                 
                

                 if (department == DepartmentType.SPACES) {
                    particle.orbitPath.speedMuiliplier = 0.001 + Math.random() * 0.04;
                    particle.orbitPath.xMuliplier = 300;
                    particle.orbitPath.yMuliplier = 200;
                    particle.orbitPath.expandMuliplier = 0.7 + Math.random() * 1.7;

                    particle.animatedProps.targetAlpha = 0.1 + Math.random() * 0.4;
                    particle.animatedProps.targetScale = 0.15 + Math.random() * 0.05;

                    

                 } else {
                    particle.orbitPath.speedMuiliplier = 0.1 + Math.random() * 0.3;
                    particle.orbitPath.xMuliplier = 700 + Math.random() * 300;
                    particle.orbitPath.yMuliplier = 350 + Math.random() * 600;

                    //particle.orbitPath.xMuliplier = 600 +  Math.random() * 4300;
                    particle.orbitPath.xMuliplier = 600 +  Math.random() * 4300;
                    particle.orbitPath.yMuliplier = Math.random() * 950

                    particle.orbitPath.expandMuliplier = 1;
                    particle.animatedProps.targetAlpha = 0.15 + Math.random() * 0.5;
                    particle.animatedProps.targetScale = 0.15 + Math.random() * 0.1;
                    particle.animatedProps.setEase({
                        x: 10, 
                        y: 10,

                    })
                 }
   

               
                 
                 particle.animatedProps.startBreathing(  particle.animatedProps.targetScale, 0.001 + Math.random() * 0.01, Math.random() * 0.3)

            }, 0)


            setTimeout( () => {

                particle.animatedProps.setEase({
                    scale: 30,
                    alpha: 30
                })
                //particle.position.set({x: 0.46, y: 0.5})

                particle.animatedProps.setEase({
                    x: 100, 
       
                    
                })
                particle.animatedProps.targetAlpha = 0.2 + Math.random() * 0.3;
                particle.animatedProps.targetScale = 0.15 + Math.random() * 0.1;
                //particle.orbitPath.xMuliplier = 900 +  Math.random() * 1000;

                
            }, 1000)
            
        

            //particle.position.set({x: 0.05 +  Math.random() * 0.1, y: 0.4 + Math.random() * 0.3})
   
            
        });
        


        setTimeout( () => {

     
            let tweenOb = {value: this._dragMinAlpha}
            gsap.TweenMax.to(tweenOb, 3, {value: 0.4, ease: gsap.Power0.easeNone, onUpdate: () => {
                this._dragMinAlpha = tweenOb.value;
                console
            }});
            
        }, 1000)
      


        return;

        if (department === DepartmentType.SCREENS) {
            this._setupForWorkScreens();
            return;
        }


        this._setupForWorkSpaces();

	}



    //PRIVATE 
    //--------------------------------------------------------------------------------------------------
    //--------------------------------------------------------------------------------------------------
