    //--------------------------------------------------------------------------------------------------

    private _toIconParticle(particle: HomeParticle, index: number = 1, fromIntro: boolean = false) {

   
        //particle.transitions.initIconTransitions(fromIntro);
        particle.switching = 'icon';
    }


    private _createParticleSet() {

        //Reserve 1st 100 or so for transitions and mouse following etc: 
        let transitionParticleCount = HomeFieldConfig.transitionParticles;
        this._transitionParticles = this._homeField.particlesFromIndex(0, transitionParticleCount) as Array<HomeParticle>;


        let layout = this._sceneProps.layouts.isometric;
        this._screensIsometricGridPositions  = layout.get(this._transitionParticles.length,  {x: 0, y: 0.2}, {w: 0.2, h: 0.1});
        this._screensRegularGridPositions  = this._sceneProps.layouts.grid.get(this._transitionParticles.length,  {x: 0, y: 0.3}, {w: 0.25, h: 0.5}, {cols: 10});

        this._transitionParticles.forEach( (particle) => {
			particle.particleSceneType = ParticleSceneType.TRANSITION;
		} );


    }


    //Set oscillating grid / sine wave with transition particles:      
    private _setupForWorkScreens(): void {
        
        this._transitionsParticleContainer.y = -1 * (window as any).cachedInnerHeight;
        this._moveGridPartcilesToScreensWork();
        this._setWorkIsoGridLines();
        //this._startSine();


        

        this._transitionParticles.forEach(particle => {
            particle.active = true;
            particle.animatedProps.alpha = 0.3;
            particle.animatedProps.scale = 0.4;
            particle.animatedProps.targetAlpha = 0.5;
            particle.animatedProps.targetScale = 0.4;
            particle.moveSeed = particle.random(1, 0.2);
            particle.sineSeed = particle.random(0.4, 0.8);
        });

    }

    //Set spaces transition particles state: 
    private _setupForWorkSpaces(): void {
        
        //this._transitionsParticleContainer.y = -1 * (window as any).cachedInnerHeight;


        return;
        this._transitionParticles.forEach((particle, index) => {
            particle.active = true;
            this._toSpacesParticle(particle, true, index);

         
        });

    }


    /**
	 * Show home particle as scene slides back in 
	 * Only icon is currently showing 
	 */

    private _toSpacesParticle(particle: HomeParticle, isDeeplinkToSpaces: boolean, index: number) {
        


        return;
        if (particle.switching == 'spaces') {
            return;
        }

        particle.transitions.setSpacesStartLurk();

        let maxVelocity = 0.07327867152503696;
        let ratio = 1 - (this._sceneProps.progress.velocity/maxVelocity);
        if(ratio > 1) ratio = 1;
        else if(ratio < 0) ratio = 0;
        let dampenSpeedPower = 0.5; 
        let dampen =  (1 - dampenSpeedPower) + (dampenSpeedPower * ratio);


        let endY = 0;

        //particle.switchScenes('spaces');

        particle.switching = 'spaces';

        endY = 0.5;
        if (particle.transitions.lurkDepth == 3) endY =  this._math.random(0.15, 0.45);
        else if (particle.transitions.lurkDepth == 2) endY =  this._math.random(0.2, 0.5);
        else if (particle.transitions.lurkDepth == 1) endY =  this._math.random(0.3, 0.55);

        let startX =  this._math.random(0.52, 0.05);
        let endX = this._math.random(0.66, 0.3);
        let frames = this._math.random(120, 120, false, true) * dampen;

        //let startAtPosition = {x: 0.52 + (Math.random() * 0.015), y: 0.4 + (Math.random() * 0.1)};
        
        
        //let startAtPosition = {x:particle.position.x, y: particle.position.y};
        //particle.position.set(startAtPosition, true)

        let goToPosition = {x: endX, y: endY}

    
        if (goToPosition.y > 0.16) {
            if(goToPosition.x > 0.76 || goToPosition.x < 0.83) {
                goToPosition.x = (Math.random() > 0.5) ? this._math.random(0.66, 0.1): this._math.random(0.83, 0.14)
            }
        } 


        
        //particle.match(position)
    
        particle.position.clamp();

      


        particle.position.set({
            y:  goToPosition.y,
            x:  particle.position.x,

        }, true);
        
        
        this._spacesLurkY = goToPosition.y;



        let x = particle.position.x;

        particle.transitions.lurkOnSpaces(goToPosition, isDeeplinkToSpaces);

            
        this._spacesLurkProps[index] = {
            x: x,
            y: particle.position.y,
            scale: particle.animatedProps.targetScale,
            alpha: particle.animatedProps.targetAlpha,
        }

     

    }

