    private _drawLines() {


    
        this._gridLinesGraphic.clear();

        

        this._transitionParticles.forEach( (particle) => {
			
            if(particle.canDrawLine && particle && particle.particlesLinesTo) {


            
                //particle.colour.set(this._gridColour);
  
            
                for(let i=0; i<particle.particlesLinesTo.length; i++) {

                    if(particle.particlesLinesTo[i] && particle.particlesLinesTo[i].canDrawLine && particle.particlesLinesTo[i].particleProps) {
                    
                        this._gridLinesGraphic.lineStyle(1,this._gridColour)
                        .moveTo(particle.particleProps.sprite.x, particle.particleProps.sprite.y)
                        .lineTo(particle.particlesLinesTo[i].particleProps.sprite.x, particle.particlesLinesTo[i].particleProps.sprite.y);
                    }
    
                }

              
    
            }
		} );

        this._gridLinesGraphic.endFill();

    }





    private _setBasicGridLines(): void {
        
        this._gridColour = 0xffffff;
        this._transitionParticles.forEach( (p: HomeParticle, index) => {
            p.canDrawLine = false;
        });


        
    }


    private _fadeGridPartcilesToScreensWork(): void {
        this._transitionParticles.forEach( (p, index) => {
            p.animatedProps.targetAlpha = 0; 
            p.animatedProps.setEase({alpha: 30});
        });
    }

    
    private _moveGridPartcilesToScreensWork(): void {

   
   

                
            this._transitionParticles.forEach( (p, index) => {
            
                let position = this._screensIsometricGridPositions[index];

                if(position) {


                    p.deactivateAllBehaviours(true);
                    p.repell.returnTo = null;

                    //Just want the x to animate as x movment looks messy: 
                    p.position.set({x:position.x, y:p.position.y}, true)
                    p.animatedProps.alpha = 0
                    p.animatedProps.targetAlpha = Config.spacesParticleAlpha; 

                    p.animatedProps.setEase({y:  p.random(10, 15), alpha: 40});


                    p.position.lerp({x:position.x, y:position.y + 1 }, p.random(30, 30), () => {
                        p.animatedProps.setEase({x: p.random(2, 1), y: p.random(5, 5)}, true, 90)
                        DelayHelper.setTimeout(() => {p.canDrawLine = true}, p.random(100, 200));

                    });
                }
             


            } );


    }


    /** Hide grid when moving to work   */
    private _hideGridForWork(): void {
        gsap.TweenMax.to(this._gridLines, 0.4, {pixi:{alpha: 0},  ease: gsap.Power0.easeNone, onComplete: () => {
            this._setWorkIsoGridLines();
        }})
    }


     //Grid is hidden, can show 
     private _setWorkGridLines(): void {
        

        
        this._gridColour = 0xffffff;
        this._gridLines.alpha = Config.workScreensGridAlpha;
        //gsap.TweenMax.to(this._gridLines, 0.4, {pixi:{y: 0},  ease: gsap.Power0.easeNone});
        this._transitionParticles.forEach( (p: HomeParticle, index) => {
                
            let position = this._screensRegularGridPositions[index];

            if(position) {
            
                p.particlesLinesTo = [];
                if( (position.upLineIndex || position.upLineIndex === 0)  && position.upLineIndex >  -1 && [this._transitionParticles[position.upLineIndex ]]) {
                    p.particlesLinesTo.push(this._transitionParticles[position.upLineIndex ]);
                }
    
                if( (position.downLineIndex || position.downLineIndex === 0) && position.downLineIndex >  -1 && [this._transitionParticles[position.downLineIndex ]]) {
                    p.particlesLinesTo.push(this._transitionParticles[position.downLineIndex]);
                }
            }


        });
        
    }


    //Grid is hidden, can show 
    private _setWorkIsoGridLines(): void {
        

        
        this._gridColour = 0x5cd3f5;
        this._gridLines.alpha = Config.workScreensGridAlphaOnWork;
        //gsap.TweenMax.to(this._gridLines, 0.4, {pixi:{y: 0},  ease: gsap.Power0.easeNone});
        this._transitionParticles.forEach( (p: HomeParticle, index) => {
                
            p.canDrawLine = false;
            
            let position = this._screensIsometricGridPositions[index];

            if(position) {
            
                p.particlesLinesTo = [];
                if( (position.upLineIndex || position.upLineIndex === 0)  && position.upLineIndex >  -1 && [this._transitionParticles[position.upLineIndex ]]) {
                    p.particlesLinesTo.push(this._transitionParticles[position.upLineIndex ]);
                }
    
                if( (position.downLineIndex || position.downLineIndex === 0) && position.downLineIndex >  -1 && [this._transitionParticles[position.downLineIndex ]]) {
                    p.particlesLinesTo.push(this._transitionParticles[position.downLineIndex]);
                }
            }


        });
        
    }

    
    private _processSineWave(): void {

   
        this._sineIndex++;
        if(this._sineIndex >= this._sineLookup.length) {
            this._sineIndex = 0;
        }

        let total =  this._transitionParticles.length;
        this._transitionParticles.forEach( (particle, index) => {

            let transformedIndex = this._sineIndex + Math.round((this._sineLookup.length * (index/total)));
            if(transformedIndex >= this._sineLookup.length )  transformedIndex -= this._sineLookup.length;

            let yEffect = this._homeField.getRelativeHeight(this._sineLookup[transformedIndex].y) * 16 * particle.sineSeed;
            
            if(this._screensIsometricGridPositions[index]) {
                particle.position.y =  (this._screensIsometricGridPositions[index].y + 1) + yEffect;     
            
            }
           
            
        });

    }


