//Contracts: 
import { particles } from 'app/contracts/Particles'; 
//Enums: 
import { ParticleLayoutType, HomeParticleStateType } from 'app/enum/Particles';
//Library: 
import { ParticleField} from 'app/library/particle/ParticleField';
import { SpacesGridParticle } from './SpacesGridParticle';


export class SpacesGridField extends ParticleField {

	constructor (sceneProps: particles.SceneProps, container: PIXI.Container, config: particles.ParticleFieldConfig) {
		super(sceneProps, container, config);

	}

	public colourise(colour) {
		this.particles.forEach( (particle) => {
			(particle as SpacesGridParticle).setTint(colour);
		})	
	}

	protected init(): void {
		this.reposition(ParticleLayoutType.PERSPECTIVE, {x: 0.36333 + 0.425, y: 0.43}, {w: 0.04, h: 0.4}, null, this.particles)
	}


	

}
