import * as dat from 'dat.gui';

const GUI = dat.GUI;

// Event types for particle debug changes
export enum ParticleDebugEvent {
    MIN_SIZE_CHANGE = 'minSizeChange',
    MAX_SIZE_CHANGE = 'maxSizeChange',
    PARTICLE_COUNT_CHANGE = 'particleCountChange',
    IMAGE_SCALE_CHANGE = 'imageScaleChange',
    REPELL_POWER_CHANGE = 'repellPowerChange',
    BLEND_MODE_CHANGE = 'blendModeChange',
    ANY_CHANGE = 'anyChange'
}

// PIXI blend modes
export const PIXI_BLEND_MODES = {
    'normal': 0,
    'add': 1,
    'multiply': 2,
    'screen': 3,
    'overlay': 4,
    'darken': 5,
    'lighten': 6,
    'color-dodge': 7,
    'color-burn': 8,
    'hard-light': 9,
    'soft-light': 10,
    'difference': 11,
    'exclusion': 12,
    'hue': 13,
    'saturation': 14,
    'color': 15,
    'luminosity': 16
};

// Particle debug parameters
export interface ParticleDebugParams {
    minSize: number;
    maxSize: number;
    particleCount: number;
    imageScale: number;
    repellPower: number;
    blendMode: string;
}

// Event callback type
type DebugEventCallback = (params: ParticleDebugParams) => void;

/**
 * Debug GUI for particle controls
 *
 * Usage:
 * import { ParticleDebugGUI, ParticleDebugEvent } from '../debug/ParticleDebugGUI';
 *
 * // Listen to specific events
 * ParticleDebugGUI.on(ParticleDebugEvent.MIN_SIZE_CHANGE, (params) => {
 *     console.log('Min size changed to:', params.minSize);
 * });
 *
 * // Listen to any change
 * ParticleDebugGUI.on(ParticleDebugEvent.ANY_CHANGE, (params) => {
 *     console.log('Params:', params);
 * });
 *
 * // Get current values
 * const params = ParticleDebugGUI.getParams();
 */
export class ParticleDebugGUI {
    private static instance: ParticleDebugGUI;
    private gui: dat.GUI;
    private params: ParticleDebugParams;
    private listeners: Map<string, DebugEventCallback[]>;
    private debounceTimers: Map<string, number> = new Map();
    private debounceDelay: number = 150; // milliseconds

    private constructor() {
        this.params = {
            minSize: 0.1,
            maxSize: 0.4,
            particleCount: 1000,
            imageScale: 1.0,
            repellPower: 50,
            blendMode: 'add'
        };

        this.listeners = new Map();
        this.setupGUI();
    }

    private setupGUI(): void {
        this.gui = new GUI();
        this.gui.domElement.style.position = 'fixed';
        this.gui.domElement.style.top = '10px';
        this.gui.domElement.style.right = '10px';
        this.gui.domElement.style.zIndex = '9999';

        // Add controls
        const particleFolder = this.gui.addFolder('Particle Controls');

        particleFolder.add(this.params, 'minSize', 0, 1, 0.1)
            .name('Min Size')
            .onChange(() => {
                this.emit(ParticleDebugEvent.MIN_SIZE_CHANGE);
                this.emit(ParticleDebugEvent.ANY_CHANGE);
            });

        particleFolder.add(this.params, 'maxSize', 0.4, 3, 0.1)
            .name('Max Size')
            .onChange(() => {
                this.emit(ParticleDebugEvent.MAX_SIZE_CHANGE);
                this.emit(ParticleDebugEvent.ANY_CHANGE);
            });

        particleFolder.add(this.params, 'particleCount', 0, 10000, 10)
            .name('Particle Count')
            .onChange(() => {
                this.emit(ParticleDebugEvent.PARTICLE_COUNT_CHANGE);
                this.emit(ParticleDebugEvent.ANY_CHANGE);
            });

        particleFolder.add(this.params, 'imageScale', 0.5, 2.0, 0.01)
            .name('Image Scale')
            .onChange(() => {
                this.emit(ParticleDebugEvent.IMAGE_SCALE_CHANGE);
                this.emit(ParticleDebugEvent.ANY_CHANGE);
            });

        particleFolder.add(this.params, 'repellPower', 1, 100, 1)
            .name('Repell Power')
            .onChange(() => {
                this.emit(ParticleDebugEvent.REPELL_POWER_CHANGE);
                this.emit(ParticleDebugEvent.ANY_CHANGE);
            });

        particleFolder.add(this.params, 'blendMode', Object.keys(PIXI_BLEND_MODES))
            .name('Blend Mode')
            .onChange(() => {
                this.emit(ParticleDebugEvent.BLEND_MODE_CHANGE);
                this.emit(ParticleDebugEvent.ANY_CHANGE);
            });

        particleFolder.open();
    }

    private emit(eventType: ParticleDebugEvent): void {
        // Clear existing timer for this event type
        const existingTimer = this.debounceTimers.get(eventType);
        if (existingTimer !== undefined) {
            clearTimeout(existingTimer);
        }

        // Set new debounced timer for this event
        const timer = window.setTimeout(() => {
            const callbacks = this.listeners.get(eventType);
            if (callbacks) {
                callbacks.forEach(callback => callback(this.params));
            }
            this.debounceTimers.delete(eventType);
        }, this.debounceDelay);

        this.debounceTimers.set(eventType, timer);
    }

    // Static methods for easy access
    public static getInstance(): ParticleDebugGUI {
        if (!ParticleDebugGUI.instance) {
            ParticleDebugGUI.instance = new ParticleDebugGUI();
        }
        return ParticleDebugGUI.instance;
    }

    public static on(event: ParticleDebugEvent, callback: DebugEventCallback): void {
        const instance = ParticleDebugGUI.getInstance();

        if (!instance.listeners.has(event)) {
            instance.listeners.set(event, []);
        }

        instance.listeners.get(event)!.push(callback);
    }

    public static off(event: ParticleDebugEvent, callback: DebugEventCallback): void {
        const instance = ParticleDebugGUI.getInstance();
        const callbacks = instance.listeners.get(event);

        if (callbacks) {
            const index = callbacks.indexOf(callback);
            if (index > -1) {
                callbacks.splice(index, 1);
            }
        }
    }

    public static getParams(): ParticleDebugParams {
        return ParticleDebugGUI.getInstance().params;
    }

    public static getBlendModeValue(modeName: string): number {
        return PIXI_BLEND_MODES[modeName as keyof typeof PIXI_BLEND_MODES] ?? 0;
    }

    public static setParams(newParams: Partial<ParticleDebugParams>): void {
        const instance = ParticleDebugGUI.getInstance();
        Object.assign(instance.params, newParams);
        instance.gui.updateDisplay();
    }

    public static show(): void {
        ParticleDebugGUI.getInstance().gui.show();
    }

    public static hide(): void {
        ParticleDebugGUI.getInstance().gui.hide();
    }

    public static destroy(): void {
        if (ParticleDebugGUI.instance) {
            ParticleDebugGUI.instance.gui.destroy();
            ParticleDebugGUI.instance = null as any;
        }
    }

    public static setDebounceDelay(delay: number): void {
        ParticleDebugGUI.getInstance().debounceDelay = delay;
    }
}

// Initialize the GUI when imported (optional - can be called manually instead)
// ParticleDebugGUI.getInstance();

export default ParticleDebugGUI;
