import {Debugbase} from './../Debugbase';

interface Configurable {
	config: any; 
}

export class FrameworkHelper {

	//Fairly cpu itensive 
	public getParsedAttributes(element: Element): any {

		var attr = Debugbase.DOM.getAttributes(element);
		
		for (var i in attr) {
			attr[i] = Debugbase.string.convert(attr[i]);
		}

		return attr;

	}

	public attributesToConfig(instance: Configurable, attributes: any, match = null): void {
		
		let name: string; 
		let i: string;
		let matches: boolean;
		let clean: string;

		for (i in attributes) {

			matches = true; 
			if (match && !Debugbase.string.contains(i, match)) {
				matches = false; 
			}

			if (matches) {
				clean =  Debugbase.string.remove(i, match + '-');
				name = Debugbase.string.snakeToCamelCase(clean);
				if(!instance.config) instance.config = {};
				instance.config[name] = attributes[i];	
			}

		}
		
	}

	

}
