export class Simple {
	

    public static triggerCounts = {}
  
	
	public static event(eventName: string, meta = null): void {
		
        //(window as any).ga('send', 'event', eventCategory, eventAction, eventLabel, eventValue);	

        if(!(window as any).sa_event) {
            return;
        }
        
        if (!Simple.triggerCounts[eventName]) {
            Simple.triggerCounts[eventName] = 0;
        }

        Simple.triggerCounts[eventName]++;

        if (!meta) {
            meta = {}
        }

        meta.trigger_count = Simple.triggerCounts[eventName];

        (window as any).sa_event(eventName, meta);

        console.log('Simple Track', eventName, meta)
 

	}





}
