export class SystemHelper {

	public cordsFromTouch: boolean = false;

	private _innerWidth: number;
	private _innerHeight: number;

	private _isPortraitDevice: boolean;
	private _isSmallDevice: boolean;

	constructor() {
		this.resize();
	}

	public get innerWidth(): number {
		return  this._innerWidth;
	}

	public get innerHeight(): number {
		return  this._innerHeight;
	}

	public get isPortraitDevice(): boolean {
		return  this._isPortraitDevice;
	}

	public get isSmallDevice(): boolean {
		return  this._isSmallDevice;
	}

	public resize(): void {
		this._innerWidth = window.innerWidth;
		this._innerHeight = window.innerHeight;
		this._isPortraitDevice = window.innerWidth < window.innerHeight;
		this._isSmallDevice  = (window.innerWidth < 800);
	}

}
