import DGeometry from 'package/DGeometry/DGeometry'

export interface Size {
	width: number;
	height: number;
}

export interface Point {
	x: number;
	y: number;
}

export class MathHelper {

	private _distance: number;
	private _distanceX: number;
	private _distanceY: number;
	private _geo: DGeometry = new DGeometry();

	public get distanceX() : number {
	}

	public get distanceY() : number {
		return this._distanceY;
	}

	//https://stackoverflow.com/questions/42110701/how-to-calculate-percentage-between-the-range-of-two-values-a-third-value-is-in
	public mapBetween(currentNum: number, minAllowed: number, maxAllowed: number, min: number, max: number): number {
		return (maxAllowed - minAllowed) * (currentNum - min) / (max - min) + minAllowed;
	}

	public getInbetween(value: number, bottom_value: number, top_value: number) : number {
		let range = top_value - bottom_value;
		let percent = (value - bottom_value) / range;
		return percent;
	}

	public constrain(value: number, min: number, max: number): number {
		if (value > max) {
			value = max;
		} else if (value < min) {
			value = min;
		}
		return value;
	}

	//Get a ratio value between two values by passing a ratio
	//i.e between -1.2 and 2 
	///getRangeRatio(0.4, -1.2, 2);
	public getRangeRatio(ratio: number, lowerValue: number, topValue: number) : number {
		let range: number = topValue - lowerValue;
		return lowerValue + (ratio * range);
	}  

	//fit scale

	//Get the scale an item should be to fit into a space: 
	public getBestFitScale(imageDimensions: Size, containerDimensions: Size): number {
		
		let scale = 1.0; 
		let aspectRatio = imageDimensions.width / imageDimensions.height;
		let targetRatio = containerDimensions.width / containerDimensions.height;

		if (targetRatio > aspectRatio) {
			scale =  containerDimensions.width / imageDimensions.width;
		} else {
			scale =  containerDimensions.height / imageDimensions.height;
		}

		return scale; 
	
	}

	/** Emulate CSS cover logic */
	public getCoverSize(imageDimensions: Size, containerDimensions: Size): any {
		
		let originalRatios = {
		  width: containerDimensions.width / imageDimensions.width,
		  height: containerDimensions.height / imageDimensions.height
		};

		// formula for cover:
		let coverRatio = Math.max(originalRatios.width, originalRatios.height); 

		// result:
		return {
			width: imageDimensions.width * coverRatio,
			height: imageDimensions.height * coverRatio
		 };
		
	}

	public distance(point1: Point, point2: Point): number {
		this._distanceX = point1.x - point2.x;
		this._distanceY = point1.y - point2.y;
		return  Math.sqrt((this._distanceX * this._distanceX) + (this._distanceY * this._distanceY));
	}


	
	public random(base: number = 0, seed: number = 1.0, miniusSeed: boolean = false, round: boolean = false, miniusAll:boolean = false): number 
	{

		let d = miniusSeed && Math.random() < 0.5 ? -1 : 1; 
		let num = base + (Math.random() * (seed * d));

		if (miniusAll) {
			num *= Math.random() < 0.5 ? -1 : 1;
		}
		return (round) ? Math.round(num) : num;
	}


}
