// Config is lazy-loaded to ensure GSAP is available from window
let configCache: any = null;

function getConfig() {
    if (!configCache) {
        // Access GSAP from window (loaded via CDN)
        const gsapLib = (window as any).gsap || (typeof gsap !== 'undefined' ? gsap : null);

        if (!gsapLib) {
            console.warn('GSAP not loaded yet, using fallback config');
            // Return empty config if GSAP isn't ready
            return {};
        }

        configCache = {
            toProjectContainerEase: gsapLib.Power2.easeInOut,
            toProjectEase: gsapLib.Power2.easeInOut,
            fromProjectEase: gsapLib.Power2.easeInOut,
            toProjectMultipler: 0.85,
            fromProjectMultipler: 0.85,
            toProjectTime: 1,
            fromProjectTime: 1,
            toProjectWhiteEase: gsapLib.Power2.easeInOut,
            toProjectWhiteEaseTime: 1,
            toProjectWhiteDelay: 0,
            toProjectSectionChangeDelay: 0,

            //Floating element
            toFloatingElementEase: gsapLib.Power2.easeInOut,
            toFloatingElementBaseTime: 1,
            fromFloatingElementEase: gsapLib.Power2.easeInOut,
            fromFloatingElementBaseTime: 1,

            //Work Effects
            minBlackoutAlpha: 0.4,

            //Case Study Exit transition
            fromProjectWhiteEase: gsapLib.Power2.easeOut,
            fromProjectWhiteEaseTime: 1,
            fromProjectWhiteDelay: 0,

            exitProjectScrollTimeBase: 0.7,
            exitProjectScrollTimePerPx: 0.000009,
            exitScrollEase: gsapLib.Power2.easeInOut,
            exitProjectScrollMuiltplier: 0.75,

            exitProjectScrollSectionChangeDelay: 0.7,
            exitMaskTime: 1,
            exitMaskEase: gsapLib.Power2.easeInOut,
            exitMaskSectionChangeDelay: 0.05,

            //Home > Work
            toWorkEase: gsapLib.Power2.easeInOut,
            toWorkTime: 1.2,
            workScreensGridAlpha: 0.5,
            workScreensGridAlphaOnWork: 0.07,
            spacesParticleAlpha: 0.5,

            //Debugging
            allowMouse: true,
            allowTouch: false,

            //Mobile layout portrait + less than this width:
            mobileWidth: 750,
            topLevelMobileSwitch: 1000,
            topLevelMobileSwitchPortrait: 1100,

            getCurrentMobileSwitch() {
               return (  (window as any).cachedInnerWidth >  (window as any).cachedInnerHeight ) ? this.topLevelMobileSwitch : this.topLevelMobileSwitchPortrait;
            },

            //Square project header at this < with
            squareProjectHeaderWidth: 500,

            doProjectsButtonMouseOver: false,
            doInfoButtonMouseOver: false,
        };
    }
    return configCache;
}

// Export as default - will be evaluated lazily when properties are accessed
export default new Proxy({} as any, {
    get(target, prop) {
        return getConfig()[prop];
    },
    has(target, prop) {
        return prop in getConfig();
    },
    ownKeys(target) {
        return Object.keys(getConfig());
    }
});
