	public removeAllTags(): void {
		for(let i=0; i<this.particles.length; i++) {
			this.particles[i].removeAllTags();
		}
	}

	public addTagToParticles(tag: string, particles: Array<particles.Particle> = null): void {
		let targetParticles: Array<particles.Particle> = particles || this.particles;
		targetParticles.forEach( (particle: particles.Particle, index: number) => {
			particle.addTag(tag);
		});
	}

	public removeTagFromParticles(tag: string, particles: Array<particles.Particle> = null): void {
		let targetParticles: Array<particles.Particle> = particles || this.particles;
		targetParticles.forEach( (particle: particles.Particle, index: number) => {
			particle.removeTag(tag);
		});
	}

	public particlesByTag(tag: string, particles: Array<particles.Particle> = null): Array<particles.Particle> {
		let targetParticles: Array<particles.Particle> = particles || this.particles;
		return targetParticles.filter( (particle: particles.Particle) => {
			return particle.hasTag(tag);
		});
	}

	public particlesFromIndex(startIndex: number = 0, amount: number = null, particles: Array<particles.Particle> = null): Array<particles.Particle> {
		let targetParticles: Array<particles.Particle> = particles || this.particles;
		amount = amount || targetParticles.length;
		return targetParticles.slice(startIndex, startIndex + amount);
	}



	//Set the target position of the particles 
	//Note this most likely be overidden by other
	public reposition(type: ParticleLayoutType, position: geometry.Vector2, size: geometry.Size, config: any = null, particles: Array<particles.Particle> = null, randomSeed: number = 0, instant: boolean = false, onStart: CallableFunction = null, skipItems = false): void {

		//Get the layout instance for the correct type: 
	let layout: particles.ParticleLayout = this._sceneProps.layouts[type];

		//Target particles passed or all
		let targetParticles: Array<particles.Particle> = particles || this.particles;

		//Get an array of positions to set the partciles to: 
		let positions: Array<particles.ParticleLookupProps> = layout.get(targetParticles.length, position, size, config, skipItems);

		//Loop through and set the position: 
		targetParticles.forEach( (particle, index: number) => {
			//particle.position is a shortcut to whatever behaviour is setting the particles base position

			//DelayHelper.setTimeout( () => {			

				if (positions[index]) {
				
					particle.position.xOffset = 0;
					particle.position.set({x: positions[index].x, y: positions[index].y}, instant);
	
					if( positions[index].scale) {
						particle.animatedProps.targetScale = positions[index].scale || 0;
					}
					
					if( positions[index].alpha) {
						particle.animatedProps.targetAlpha = positions[index].alpha || 0.5;
					}
				
					particle.animatedProps.targetRotation = positions[index].rotation || 0;
					//particle.colour.set(0xffffff);
				} else {
	
					particle.position.set({
						x: 0.81 + (Math.random() * 0.12),
						y: 0.41 + (Math.random() * 0.25)
					});
					particle.animatedProps.hardSetScale(0.00000001);
					particle.animatedProps.alpha = particle.animatedProps.targetAlpha = 0.00000001;
					particle.animatedProps.targetRotation =  0;
				
				}

				if(onStart) {
					onStart(particle);
				}
	
			//}, Math.random() * randomSeed )

		});

	}


	


	

	protected repositionFromCache(cacheData:Array<any>, particles: Array<particles.Particle> = null, randomSeed: number = 0,  instant: boolean = false, skipScaleAlpha: boolean = false): void {

	
		//Loop through and set the position: 
		particles.forEach( (particle, index: number) => {
			//particle.position is a shortcut to whatever behaviour is setting the particles base position
			DelayHelper.setTimeout( () => {
				this.positionOneFromCache(cacheData[index], particle, instant, skipScaleAlpha);
			}, Math.random() * randomSeed );

		});

	}

	public positionOneFromCache(cacheData, particle, instant, lerp:boolean = false, lerpFrames: number = 90, lerpCallback: CallableFunction = null, skipScaleAlpha = false) {

		particle.position.xOffset = cacheData.xOffset || 0;
		

		if (lerp) {
			particle.position.lerp({x: cacheData.x, y: cacheData.y}, lerpFrames, lerpCallback)
		} else {
			particle.position.set({x: cacheData.x, y: cacheData.y}, instant);
		}



		if (!skipScaleAlpha) {
			particle.animatedProps.targetScale = cacheData.scale || 0;
			particle.animatedProps.targetAlpha = cacheData.alpha || 1;
			particle.animatedProps.targetRotation = cacheData.rotation || 0;
		}


		particle.col = cacheData.col;
		particle.row = cacheData.row;
		

	}

	


	public repositionLookup(type: ParticleLayoutType, position: geometry.Vector2, size: geometry.Size, config: any = null, particles: Array<particles.Particle> = null,  startIndex: number = 0): Array<any> {

		//Get the layout instance for the correct type: 

		let lookups = [];

		let layout: particles.ParticleLayout = this._sceneProps.layouts[type];

		//Target particles passed or all
		let targetParticles: Array<particles.Particle> = particles || this.particles;

		//Get an array of positions to set the partciles to: 
		let positions: Array<particles.ParticleLookupProps> = layout.get(targetParticles.length, position, size, config);

		//Loop through and set the position: 
		targetParticles.forEach( (particle, index: number) => {
			//particle.position is a shortcut to whatever behaviour is setting the particles base position

			
				let props;
			
				if (positions[index]) {
				

					props = {
						x: positions[index].x,
						xOffset: 0,
					    y: positions[index].y,
						scale:  positions[index].scale || 0,
						alpha:  positions[index].alpha || 0.5,
						rotation: 0,
						index: index,
						computedIndex: startIndex + index,
						col: positions[index].col,
						row: positions[index].row
					}

				} else {


					props = {
						x: 0.81 + (Math.random() * 0.12),
					    y: 0.41 + (Math.random() * 0.25),
						scale: 0.00000001,
						alpha:  0.00000001,
						rotation: 0,
						index: index,
						computedIndex: startIndex + index,
						col: -1, 
						row: -1
					}
	
				
				
				}
			
				

				lookups.push(props);
		

		});


		return lookups;


	}


	public selectRandom(): particles.Particle {
		return this.particles[Math.floor(Math.random() * (this.particles.length - 0.01) )];
	}


