	private _createConfig(fieldConfig: particles.ParticleFieldConfig): particles.ParticleSetConfig {

		let particlesConfig: Array<particles.ParticleConfig> = [];
		let textures: Array<PIXI.Texture> = fieldConfig.particles.textures;
		let tints: Array<number> = fieldConfig.particles.tints;

		let tintIndex: number = 0; 

		//let layoutPoints: Array <particles.ParticleLookupProps> = this._sceneProps.layouts.random.get(fieldConfig.count, fieldConfig.position, fieldConfig.size);
		let layoutPoints: Array <particles.ParticleLookupProps> = this._sceneProps.layouts.grid.get(fieldConfig.count, fieldConfig.position, fieldConfig.size);
		//let layoutPoints: Array <particles.ParticleLookupProps> = this._sceneProps.layouts.perspective.get(fieldConfig.count, {x: 0.36333 + 0.48, y: 0.45}, {w: 0.04, h: 0.4});

		//Create all the p
		let layout: particles.ParticleLookupProps;
		let spareLayout:  particles.ParticleLookupProps = {
			x: 0, 
			y: 0, 
			scale: 0.000001, 
			alpha: 0.000001, 
		};

	

		for (let i = 0; i < fieldConfig.count; i++) {

			layout = layoutPoints[i] || spareLayout;
		
			let particleConfig: particles.ParticleConfig = {
				base: {
					id: fieldConfig.id + '-' + i,
					type: fieldConfig.particles.types,
					texture: textures[0],
					tint: tints[tintIndex],
					z: fieldConfig.z,
				},
				intialState: {
					x: layout.x,
					y: layout.y,
					alpha: layout.alpha || 0.4, 
					scale: layout.scale || 0.2,
					rotation: layout.rotation || 0
				}
			};

			tintIndex++;
			if (tintIndex === tints.length) {
				tintIndex = 0;
			}
			
			particlesConfig.push(particleConfig);
			
		}

		//Create the actual config object: 
		let particleConfig: particles.ParticleSetConfig = {
			particles: particlesConfig, 
			fields: null,
		};

		return particleConfig;

	}


	private _processParticles(config: particles.ParticleSetConfig, delay, lowerIndex, upperIndex, particlePoolIndexes, isHome: Boolean = false) {
		

		let c = config;

	

			for(let i=lowerIndex; i<=upperIndex; i++) {
				
				if (!config.particles[i]) {
					break;
				}
	
			
				this._processParticle(config.particles[i], i, particlePoolIndexes, isHome)
	
	
			}
	
		

		

		

	}

	private _processParticle(particleConfig:  particles.ParticleConfig, index: number, particlePoolIndexes, isHome) {

			
		let typeClass: any = 'HomeParticle';
		
			
			let targetContainer  = null;
			if (isHome) {
		
				if (index <= particlePoolIndexes.transitions.upper) targetContainer = this._subContainers.transitions;
				else if (index >= particlePoolIndexes.icon.lower &&  index <= particlePoolIndexes.icon.upper) targetContainer = this._subContainers.icon;
				else if (index >= particlePoolIndexes.screens.lower &&  index <= particlePoolIndexes.screens.upper) targetContainer = this._subContainers.screens;
				else if (index >= particlePoolIndexes.spaces.lower &&  index <= particlePoolIndexes.spaces.upper) targetContainer = this._subContainers.spaces;
				else if (index >= particlePoolIndexes.systems.lower &&  index <= particlePoolIndexes.systems.upper) targetContainer = this._subContainers.systems;
				
			}

			//if(particleConfig.base.type === ParticleType.SPACES_GRID)  typeClass = SpacesGridParticle;
			//else if(particleConfig.base.type === ParticleType.SCREENS_GRID)  typeClass = ScreensGridParticle;
			
	
			
			this._particleManager.addParticle('HomeParticle', particleConfig, targetContainer);

	}



