
import { HomeTimelineMain } from "app/controller/home/HomeTimelineMain";
import { MouseController } from "app/controller/MouseController";
import { PixiController } from "app/controller/PixiController";


export default {
    
    homeTimelineMain: null as HomeTimelineMain,
    mouse: null as MouseController,
    pixi: null as PixiController,
    progress: 0,

    create() {
        this.mouse = new MouseController();
        this.mouse.init();
        this.homeTimelineMain = new HomeTimelineMain(this.mouse);
        this.pixi = new PixiController();
    },

    tick() {
        this.mouse.tick();
        this.homeTimelineMain.tick(
            this.mouse.getMouseCords(), 
            this.mouse.mouseIsDown(),
            0
        );
        this.homeTimelineMain.setProgress(this.progress);
        this.homeTimelineMain.setVirtualWidth(3 * (window as any).cachedInnerWidth, 3);
    },

    setProcess(progress) {
        this.progress = progress
    }

}