	public init(): void {

		this._setOpeningPointerMethodGuess();

		this._velocity = new Velocity();
		this._direction = new Direction();
		

		this._lockInertiaX = new Inertia(10, 0);
		this._lockInertiaX.round = true;
		this._lockInertiaY = new Inertia(10, 0);
		this._lockInertiaY.round = true;

		//this._setLastCordsFromTouch(true, 'init');
		

		this._lockSupportInertiaX = new Inertia(10, 0);
		this._lockSupportInertiaX.round = true;
		this._lockSupportInertiaY = new Inertia(10, 0);
		this._lockSupportInertiaY.round = true;

		window.mouseController = this;

		this.$body =  document.querySelector('body');


	}

	private _setLastCordsFromTouch(fromTouch, source) {


	
		(window as any).lastCordsFromTouch = fromTouch;


		


	}

	private _setOpeningPointerMethodGuess() {

		if ( this._isIpadOS() ) {
			this._setLastCordsFromTouch(true, '_setOpeningPointerMethodGuess() 1');
			return 
		}

		let lastCordsFromTouch  = (('ontouchstart' in window) ||
		(navigator.maxTouchPoints > 0) ||
		(navigator.msMaxTouchPoints > 0)) && (window as any).cachedInnerWidth < 1400;

		this._setLastCordsFromTouch(lastCordsFromTouch, '_setOpeningPointerMethodGuess() 2');

	}

	private _isIpadOS() {
		return navigator.maxTouchPoints &&
		  navigator.maxTouchPoints > 2 &&
		  /MacIntel/.test(navigator.platform);
	  }

