	public tick(): void {

		//Caluclate veloclity of mouse 
		//Could be disabled in vertical drag sections if required: 


		this._velocity.calculate(this._mouseCords);
		this._direction.calculate(this._mouseCords);

		this._x = 	this._x  + ( ((	this._mouseCords.x -	this._x  ) / 10 ) * (window as any).deltaTime);
		this._y = 	this._y  + ( (( this._mouseCords.y  - this._y  ) / 10 ) * (window as any).deltaTime);


		if(this.guideFollowActive) {
		
			this._$dragPrompt.style.setProperty('--mouse-delayed-x', `${this._x}px`);
			this._$dragPrompt.style.setProperty('--mouse-delayed-y', `${this._y}px`);
	
		}

		this._lockInertiaX.tick();
		this._lockInertiaY.tick();
		this._lockSupportInertiaX.tick();
		this._lockSupportInertiaY.tick();


		let circleRect = null;

		

		if(this._hoverLocked && circleRect) {

			//let radius = this._distanceRuler.measure(this._lockOrgCords, this._mouseCords);
			
			//Make normal circle follow mouse

		
			circleRect = {
				width: 100,
				height: 100
			}
			
			

			if (this._lockPosition == 'relative') {
				this._lockInertiaX.target =  this._mouseCords.x  - this._lockOrgCords.x  - 90;
				this._lockInertiaY.target = this._mouseCords.y - this._lockOrgCords.y  - 90;
			} else {
				this._lockInertiaX.target =  (this._mouseCords.x - Math.round(circleRect.width/2)) - this._lockOrgCords.x  ;
				this._lockInertiaY.target = (this._mouseCords.y - Math.round(circleRect.height/2)) - this._lockOrgCords.y  ;
			}


			//
			//this._lockSupportInertiaX.target =  (this._mouseCords.x - this._lockOrgCords.x) * 0.5;
			//this._lockSupportInertiaY.target = (this._mouseCords.y - this._lockOrgCords.y) * 0.5;

			let add = this._hoverLocked ? 90 : 0;
			this._lockSupportInertiaX.target =  this._lockInertiaX.target + add;
			this._lockSupportInertiaY.target = this._lockInertiaY.target * 0.3;
		
		
			
		}

	

	
		/*if (this._introActionActive) {
			gsap.TweenMax.set(this._$homeIntroCursor, {
				x: this._mouseCords.x, 
				y: this._mouseCords.y,
				top: 0,
				left: 0
			});
		}*/
	}

