import { HomeJSONModel } from "app/model/HomeJSONModel";
import { JSONLoader } from "debugbase/loader/JSONLoader";
import { PixiLoader } from "debugbase/loader/PixiLoader";

export default class {

    public onReady: CallableFunction;
    private flags = {json: false, sprites: false};
    private json: HomeJSONModel;
    private spritesheet: PIXI.Spritesheet;

    load(): void {
        
        
        //JSON config for home 
        const homeJsonLoader = new JSONLoader();
        homeJsonLoader.url = 'json/home.json';
        homeJsonLoader.id = 'json-home';
        homeJsonLoader.onComplete = this.onJSONLoaderReady.bind(this);
        homeJsonLoader.load();

        //Const
        const pixiLoader = new PixiLoader();
        pixiLoader.resources = [{ref: 'home-particles', path: `img/home/spritesheet/home-particles.json`, isSpriteSheet: false}];
        pixiLoader.onResourcesReady =  this.onSpriteSheetsReady.bind(this);
        pixiLoader.load();

    
            
    }

    private onJSONLoaderReady(loaderId: string, json: any) {

        this.json = json;
        this.flags.json = true; 
        this.testReady();
    }

    private onSpriteSheetsReady(loaderId: string, resources: any) {
        this.flags.sprites = true; 
        console.log('resources',  resources['home-particles'])
        this.spritesheet = resources['home-particles'];
        this.testReady();
    }

    private testReady() {
        

        if (!this.flags.json || !this.flags.sprites) {
            return; 
        }

        this.onReady(
            this.spritesheet,
            this.json
        );

    }

    
    
}
