	public setFrame(config, resetAtStart: boolean = false): void {

		this._p.position.xOffset = 0;
		
		this._p.rectPath.xMuliplier = config.w + (Math.random() * 2);
		this._p.rectPath.yMuliplier = config.h + (Math.random() * 2);

		if (resetAtStart) {
			this._p.position.set({x: config.x, y: config.y}, true);
		} else {
			this._p.position.lerp({x: config.x, y: config.y}, 30);
		}
		
		
		this._p.rectPath.alphaActive = false;
		this._p.rectPath.scaleActive = false;

	}


	/** 
	 * Set the properties particle while in screens scene as a frame item: 
	 * 
	 */
	public initScreensFrame(frameConfig: any = null, resetAtStart: boolean  = true): void {

		this._p.deactivateAllBehaviours(true);

		this._p.activate();
		

		this._p.animatedProps.targetAlpha = 0;
		this._p.animatedProps.scale = 0;
		this._p.animatedProps.alpha = 0;
		this._p.animatedProps.targetScale = 0;

		return;
	
		this._p.position.trackMouse = false;
		this._p.position.disableTimelineX = true;

		this._p.setupPath(this._p.rectPath, {
			x: [350, 2], y: [400, 2], speed: [0.2, 2], scale: [0.05, 0.11], alpha: [0.03, 0.6], 
			randomise: {unsync: true} 
		});

		//this.colourise(-2);
		this._p.animatedProps.targetScale  = 0.05 + (Math.random() * 0.11);


		this._p.animatedProps.targetAlpha  = 0.1 + (Math.random() * 0.8);

		this._p.animatedProps.targetAlpha = 0;
		this._p.animatedProps.scale = 0;
		this._p.animatedProps.alpha = 0;
		this._p.animatedProps.targetScale = 0;

		this._p.mouseParallax.active = true;
		this._p.mouseParallax.setPower({x: 0.005, y: 0.008 })

		this._p.repell.active = true;
		this._p.repell.target = this._sceneProps.targets.screensMouse;
		this._p.repell.type = RepellType.PUSH;
		this._p.repell.testAtCount = 1;

		//Allow for 150px circle and very tight ring: 
		this._p.repell.radius = 30;	
		this._p.repell.randomisePower(30, 70);

		let ease = this._p.random(20, 30);
		this._p.animatedProps.setEase({
			x: ease,
			y: ease
		})



		if (frameConfig) {
			this.setFrame(frameConfig, resetAtStart);
		} 


		this._p.colour.set(1 * 0xffffff);

		this._p.position.set({x: 0, y: 0.45}, true);
				


	}


	/** 
	 * Set the properties particle while in screens scene as a frame item: 
	 * 
	 */
	public initScreensShape(reset: boolean = true, sizeReset: boolean = true): void {
		
	
		if(reset && sizeReset) this._p.position.set({x: 0, y: 0.45}, true);

		this._p.position.disableTimelineX = true;
		this._p.repell.target = this._sceneProps.targets.screensMouse;


		return;
		if (reset) {
			this._p.deactivateAllBehaviours(true);
		}

		this._p.repell.active = true;

		this._p.position.trackMouse = false;
		
		this._p.repell.active = true;
		this._p.repell.target = this._sceneProps.targets.screensMouse;
		
		this._p.repell.testAtCount = 1;
		this._p.repell.radius = 10;	

		this._p.repell.type = RepellType.POSITION;
		//this._p.repell.randomisePower(200, 10);
		
		let pSeed = this._p.random(0, 3)
		this._p.repell.randomisePower(0.001 * pSeed, 0.002 * pSeed);
		this._p.repell.returnPower = {x: this._p.random(0.03, 0.09), y:  this._p.random(0.03, 0.09)}



		this._p.mouseParallax.active = false;
		this._p.mouseParallax.setPower({x: 0.015, y: 0.03 })

		this._p.animatedProps.setEase( {
			x: this._p.random(2, 2),
			y: this._p.random(2, 2),
			scale: 4,
			alpha: 4
		} )

	
		this._p.orbitPath.active = false;
		this._p.attract.active = false;

		this._p.animatedProps.targetAlpha =  this._p.random(0.3, 0.8);
		this._p.animatedProps.targetScale = this._p.random(0.1, 0.05);

		this._p.animatedProps.targetAlpha =  1;
		this._p.animatedProps.targetScale = this._p.random(0.1, 0.05);

		this._p.mouseParallax.active = true;
		this._p.mouseParallax.setPower({x: 0.008, y: 0.015 })

		this._p.hover.active = false;
		this._p.hover.radius = 80;
		this._p.hover.setAlpha(this._p.animatedProps.targetAlpha,  this._p.animatedProps.targetAlpha + 0.5);
		this._p.hover.setScale(this._p.animatedProps.targetScale, this._p.animatedProps.targetScale + 0.1);
		this._p.hover.target = this._sceneProps.targets.screensMouse;

		if(reset && sizeReset) this._p.position.set({x: 0, y: 0.45}, true);

	

	}	



