	private _triggerShapeMove(finalPosition:  geometry.Vector2, resetAtStart: boolean = false): void {
			//this._p.position.set(cornerPosition);
			this._p.repell.active = false;
					

			//console.log('finalPosition', finalPosition);

			//this._p.startCounter( this._p.counter2, this._p.random(60, 10), () => {

			let time = resetAtStart ? this._p.random(40, 20) : this._p.random(20, 40);

			time = 0; 

			if (!resetAtStart) {
				//this._p.animatedProps.setEase( {
				//	x: this._p.random(2, 2),
				//	y: this._p.random(2, 2),
				//} );
				let ease = this._p.random(10, 30);
				this._p.animatedProps.setEase( {
					x: ease,
					y: ease,
				} )
			} else {
				this._p.animatedProps.setEase( {
					x: this._p.random(10, 30),
					y: this._p.random(8, 20),
				} )
			}

			(this._p.position as ParticleProgressParallax).lerp(finalPosition, time, () => {
				this._p.animatedProps.setEase( {
					x: this._p.random(10, 30),
					y: this._p.random(8, 20),
				} )
				
			});
			(this._p.position as ParticleProgressParallax).effectLerpPerFrame = -1;

			this._p.orbitPath.active = false;
			this._p.animatedProps.targetAlpha = 0.8 + finalPosition.radius * 0.2;

			this._p.animatedProps.targetScale = 0.19 -  finalPosition.radius * 0.04;


			this._p.startCounter( this._p.counter, 120, () => {
				this._p.repell.active = true;
			});


			this._p.repell.returnTo = {x: finalPosition.x, y: finalPosition.y};

			//this._p.animatedProps.targetAlpha = this._p.random(0.7, 0.3);
			//this._p.animatedProps.targetScale = this._p.random(0.1, 0.08);



			
	}

	public frameCorners(finalPosition:  geometry.Vector2, cornerPosition: geometry.Vector2, resetAtStart: boolean = false): void {

	
		if(resetAtStart) {
			//this._p.colour.set(PIXI.utils.rgb2hex(finalPosition.rgba))
			this._triggerShapeMove(finalPosition, resetAtStart)
		} else {
			this._p.startCounter( this._p.counter2, this._p.random(1, 30), () => {
				//this._p.colour.set(PIXI.utils.rgb2hex(finalPosition.rgba))
				this._triggerShapeMove(finalPosition, resetAtStart)
			});
		}

		

		


	}

	public swirl(config, circleIndex, colourHex, shape = 'solid2.png', sceneconfig) 
	{
		
		
		this._p.deactivateAllBehaviours(true);

		//let index = Math.floor( (settings.length - 0.01) * Math.random());
		//let config = settings[index]

		this._p.changeParticle(shape)

		this._p.activate();
		this._p.orbitPath.active = true;
		//this._p.orbitPath.randomise();
		this._p.orbitPath.xMuliplier = config.r * sceneconfig[0].rscale
		this._p.orbitPath.yMuliplier = config.r * sceneconfig[0].rscale
		this._p.orbitPath.speed = 1;
		this._p.orbitPath.pathIndex = circleIndex;
		this._p.orbitPath.speedMuiliplier = 1;
		this._p.orbitPath.scaleActive = false;
		this._p.orbitPath.path = LookupType.CIRCLE;


		this._p.orbitPath.syncRotation = true;
		this._p.particleProps.sprite.blendMode = null;
	
		this._p.animatedProps.targetAlpha = this._p.animatedProps.alpha = 0.8 + (Math.random() * 0.2);

		this._p.animatedProps._x.setVectorConfig(false);
		this._p.animatedProps._y.setVectorConfig(false);

	

		this._p.animatedProps.stopBreathing();
	
		let shapeFix = shape == 'solid-square.png' ? 0.75 : 1
		let baseScale = config.s * sceneconfig[0].scale * shapeFix ;
		this._p.animatedProps.hardSetScale(baseScale);


		this._p.animatedProps.scale  = 0;
		this._p.colour.set(colourHex);

		this._p.animatedProps.setEase( {
			x: 20 * config.s,
			y: 20 * config.s,
			scale: 20,
			alpha: 2
		} )

		this._p.position.set({
			x: sceneconfig[0].x,
			y: sceneconfig[0].y
		}, true);

		this._orbitRepell();



		this._p.repell.returnTo = {	
			x: sceneconfig[0].x,
			y: sceneconfig[0].y
		}


		DelayHelper.setTimeout(() => {
			
			this._p.orbitPath.xMuliplier = config.r * sceneconfig[1].rscale;
			this._p.orbitPath.yMuliplier = config.r *  sceneconfig[1].rscale;
			this._p.orbitPath.direction = -1;
			this._p.orbitPath.speedMuiliplier = 0.25;
			this._p.animatedProps.targetScale = this._p.animatedProps.targetScaleSaved  = config.s *  sceneconfig[1].scale * shapeFix;


			//Remove whoozy: 
			this._p.animatedProps._x.setVectorConfig(false);
			this._p.animatedProps._y.setVectorConfig(false);

			this._p.position.set({
				x:  sceneconfig[1].x,
				y: sceneconfig[1].y
			});

			this._p.repell.returnTo = {	
				x: sceneconfig[1].x,
				y: sceneconfig[1].y
			}

			this._p.animatedProps.setEase( {
				x: 30 * config.s,
				y: 30 * config.s,
				scale: 20,
				alpha: 2
			} )

			this._p.animatedProps.startBreathing(baseScale, 0.01, 0.5);
	
			
		}, sceneconfig[1].t, null, 'screens')

		

		DelayHelper.setTimeout(() => {
			this._p.orbitPath.xMuliplier = config.r *  sceneconfig[2].rscale;
			this._p.orbitPath.yMuliplier = config.r * sceneconfig[2].rscale;
			this._p.orbitPath.direction = 1;
			this._p.animatedProps.targetScale =  this._p.animatedProps.targetScaleSaved =  config.s * sceneconfig[2].scale;


			this._p.position.set({
				x: sceneconfig[2].x,
				y: sceneconfig[2].y
			});

			this._p.repell.returnTo = {	
				x: sceneconfig[2].x,
				y: sceneconfig[2].y
			}
			
		}, sceneconfig[2].t, null, 'screens')

		DelayHelper.setTimeout(() => {
			this._p.orbitPath.xMuliplier = config.r *  sceneconfig[3].rscale;
			this._p.orbitPath.yMuliplier = config.r * sceneconfig[3].rscale;
			this._p.orbitPath.direction = -1;
			this._p.animatedProps.targetScale = this._p.animatedProps.targetScaleSaved =  config.s * sceneconfig[3].scale;


			this._p.position.set({
				x: sceneconfig[3].x,
				y: sceneconfig[3].y
			});

			this._p.repell.returnTo = {	
				x: sceneconfig[3].x,
				y: sceneconfig[3].y
			}

			this._p.animatedProps.setEase( {
				x: 30,
				y: 30,
				scale: 30,
				alpha: 2
			} )
			
		}, sceneconfig[3].t, null, 'screens')




	}


	public leftDrop() {
		
		this._p.counter.active = true; 
		this._p.counter.millsecsToFrames( Math.random() * 3000);
		this._p.counter.start();
		
		this._p.counter.onTrigger = ()=> {
			
			this._p.deactivateAllBehaviours(true);
			this._p.particlePhysics.active = true;
			this._p.particlePhysics.leftDrop();

			this._p.repell.active = !(window as any).disablePointerRepell;
			this._p.repell.type = RepellType.VECTOR;
			this._p.repell.testAtCount = 1;
			//Allow for 150px circle and very tight ring: 
			this._p.repell.radius = 100;
			//this._p.repell.returnTo = {x: this._p.position.x, y: this._p.position.y};
			this._p.repell.randomisePower(0.00005, 0.0000001);

			this._p.animatedProps.setEase( {
				scale: 4
			} )

		}
	

	}

